import PropTypes from 'prop-types';
import React from 'react';

const FontResizer = ({ children }) => {
  const count = children.length;
  const fontSize = () => {
    if (count < 23) {
      // Large
      return '120%';
    } else if (count < 60) {
      // Medium
      return '80%';
    } else if (count < 70) {
      // Small
      return '70%';
    }
    return '60%';
    // Extra small
  };

  return (
    <span className="resizer" style={{ fontSize: fontSize() }}>
      {children}
    </span>
  );
};

export default FontResizer;

FontResizer.propTypes = {
  children: PropTypes.node,
  // for list types
};

FontResizer.defaultProps = {
  children: '',
};
