/* This class is the connector to the player window.
   It mirrors the youtube API functions and sends / receive messages
   from and to the main process. The main process then proxy the messages
   to the player window and proxy back the here (via callbacks).
*/

class YTPlayer {
  constructor(options = {}) {
    this.onReady = options.onReady;
    this.onEnd = options.onEnd;
    this.onCurrentTime = options.onCurrentTime;
    this.onStateChange = options.onStateChange;
    this.onMprisShuffle = options.onMprisShuffle;
    this.onMprisRepeat = options.onMprisRepeat;
    this.listenToPlayer();
  }

  pauseVideo() {
    this.send('pauseVideo');
  }

  playVideo() {
    this.send('playVideo');
  }

  unMute() {
    this.send('unMute');
  }

  seekTo(value) {
    this.send('seekTo', value);
  }

  loadVideoById(id) {
    this.send('loadVideoById', id);
    setTimeout(() => {
      this.onCurrentTime(0);
    });
  }

  sendTrackInfo(track) {
    this.send('trackInfo', track);
  }

  setVolume(value) {
    this.send('setVolume', value);
  }

  getCurrentTime() {
    return new Promise((resolve) => {
      this.send('getCurrentTime', (time) => resolve(time));
    });
  }

  currentTime(value) {
    this.onCurrentTime(value);
  }

  listenToPlayer() {
    try {
      window.ipcRenderer.on('player2Win', (e, args) => {
        const [command, data] = args;
        this[command](data);
      });
    } catch (err) { /* Test Mode */ }
  }

  // eslint-disable-next-line class-methods-use-this
  send(command, args) {
    try {
      window.ipcRenderer.send('win2Player', [command, args]);
    } catch (err) { /* Test Mode */ }
  }
}

export default YTPlayer;
