import React from 'react';
import PropTypes from 'prop-types';
import {
  CardCVCElement,
  CardExpiryElement,
  CardNumberElement,
  injectStripe,
} from 'react-stripe-elements';
import { post } from 'lib/http';
import { Checkbox } from '@blueprintjs/core';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { ExternalLink } from 'components';
import classnames from 'classnames';

const createOptions = {
  style: {
    base: {
      fontSize: '16px',
      color: '#333',
      letterSpacing: '0.025em',
      fontFamily: 'Source Code Pro, monospace',
      '::placeholder': {
        color: '#aab7c4',
      },
    },
    invalid: {
      color: '#9e2146',
    },
  },
};

class CheckoutForm extends React.Component {
  constructor() {
    super();
    this.termsCheckbox = null;
    this.state = {
      isLoading: false,
      error: null,
      plan: 'year',
    };
  }

  handleSubmit = (e) => {
    e.preventDefault();

    if (!this.termsCheckbox.input.checked) {
      this.setState({
        error: 'Please agree to the terms and conditions',
      });
      return;
    }

    this.setState({ isLoading: true });

    const { lastName, firstName, email } = this.context.user.toJS();
    this.props.stripe.createToken({
      email,
      name: `${firstName} ${lastName}`,
    }).then((result) => {
      if (result.error) {
        throw result.error.message;
      }
      return post({ path: 'subscriptions',
        data: {
          email, stripePayload: result.token, plan: this.state.plan,
        } });
    }).then((subscription) => {
      this.props.onPaySuccess(subscription.data.customer);
      this.setState({ isLoading: false });
    }).catch((error) => {
      this.setState({ error: error.toString(), isLoading: false });
    });
  }

  render() {
    const { plan, error, isLoading } = this.state;

    /* eslint-disable react/jsx-props-no-spreading */
    return (
      <form className="stripe-form" onSubmit={this.handleSubmit}>
        <h5>Select a plan</h5>
        <div className="plan-boxes">
          <div
            onClick={() => this.setState({ plan: 'year' })}
            className={classnames('plan-box', { active: plan === 'year' })}
          >
            $24/year
            <small>Save 33.33%</small>
          </div>
          <div
            onClick={() => this.setState({ plan: 'month' })}
            className={classnames('plan-box', { active: plan === 'month' })}
          >
            $3/month
            <small>Billed monthly</small>
          </div>
        </div>
        <div className="usd-caption">All prices are in USD</div>
        <h5>Add a card</h5>
        {error
        && (
        <div className="error">
          {error}
        </div>
        )}
        <label>
          <span>Card number</span>
          <CardNumberElement {...createOptions} />
        </label>
        <label>
          <span>Expiration date</span>
          <CardExpiryElement {...createOptions} />
        </label>
        <label>
          <span className="upper">CVC</span>
          <CardCVCElement {...createOptions} />
        </label>
        <div className="form-field checkbox">
          <Checkbox
            ref={(terms) => { this.termsCheckbox = terms; }}
          >
            <ExternalLink href="https://headsetapp.co/terms">
              I Agree to the Terms of Service
            </ExternalLink>
          </Checkbox>
        </div>
        <button
          type="submit"
          className="subscribe-button"
        >
          {isLoading ? 'Loading' : 'Upgrade to Pro'}
        </button>
      </form>
    );
  /* eslint-enable react/jsx-props-no-spreading */
  }
}

/* eslint-disable react/forbid-prop-types */
CheckoutForm.propTypes = {
  stripe: PropTypes.object.isRequired,
  onPaySuccess: PropTypes.func.isRequired,
};
/* eslint-enable react/forbid-prop-types */

CheckoutForm.contextTypes = {
  user: ImmutablePropTypes.map,
  theme: PropTypes.string,
};

export default injectStripe(CheckoutForm);
