import PropTypes from 'prop-types';
import React from 'react';

const NoCollection = (props) => {
  let explain;
  switch (props.type) {
    case 'playlists':
      explain = `A Playlist is a group of videos on users&#39; YouTube channels,
        listed in a particular way so you can quickly find content that interests you.
        Look for the <span class="playlist-tag">Playlist</span> tag in the search results!`;
      break;
    case 'channels':
      explain = `A channel on YouTube is the home page for an account. You can follow as many as you want.
        Look for the <span class="channel-tag">Channel</span> tag in the search results!`;
      break;
    case 'collections':
    default:
      explain = `Collections are user created playlists that are populated by individual tracks.
        Build a collection of your favorites, so they’re always close at hand`;
  }

  return (
    <div className="no-collections">
      <p dangerouslySetInnerHTML={{ __html: explain }} />
    </div>
  );
};

NoCollection.propTypes = {
  type: PropTypes.string,
};

NoCollection.defaultProps = {
  type: '',
};

export default NoCollection;
