import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import classnames from 'classnames';
import stations from 'lib/stations';
import { Link } from 'react-router';
import Loader from 'static/images/loader.svg';

class Station extends PureComponent {
  get icon() {
    const { currentlyLoading } = this.props;
    if (currentlyLoading) {
      return <img alt="" src={Loader} className="station-loader" />;
    }

    return (
      <i role="none" className="material-icons">chevron_right</i>
    );
  }

  // eslint-disable-next-line class-methods-use-this
  renderStationName(stationKey) {
    const name = stations[stationKey];
    return (
      <div className="station-name">
        <b>
          {name || stationKey}
        </b>
        {`/r/${stationKey}`}
      </div>
    );
  }

  render() {
    const {
      stationKey,
      active,
      selected,
      saved,
      onSaveClick,
      className,
    } = this.props;
    const { user } = this.context;

    return (
      <div
        className="column station-item"
        key={stationKey}
        role="none"
      >
        <Link to={`/preview/radio/${stationKey}`}>
          <div className={classnames('station', className, { active, selected, saved })}>
            {this.renderStationName(stationKey)}
            {user
              && (
              <i
                className={classnames('material-icons save-icon', { active: saved })}
                onClick={onSaveClick}
                role="none"
              >
                {saved ? 'star' : 'star_border'}
              </i>
              )}
            <div className="station-icon">
              {this.icon}
            </div>
          </div>
        </Link>
      </div>
    );
  }
}

Station.propTypes = {
  stationKey: PropTypes.string,
  selected: PropTypes.bool,
  active: PropTypes.bool,
  currentlyLoading: PropTypes.bool,
  saved: PropTypes.bool,
  onSaveClick: PropTypes.func,
  className: PropTypes.string,
};

Station.contextTypes = {
  user: PropTypes.object,
};

Station.defaultProps = {
  stationKey: '',
  selected: false,
  active: false,
  currentlyLoading: false,
  saved: false,
  onSaveClick: () => {},
  className: '',
};

export default Station;
