import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Button, Checkbox, InputGroup, Dialog } from '@blueprintjs/core';
import ImmutablePropTypes from 'react-immutable-proptypes';
import {
  updateScrobble,
  closeDialog,
  restore,
  toggleRememberAlbum,
  toggleRememberArtist,
} from 'actions/lastFmActions';

class LastFmScrobbleDialog extends PureComponent {
  onFieldChanged = (key, value) => {
    this.props.dispatch(updateScrobble({ key, value }));
  }

  onRememberAlbumCheck = () => {
    this.props.dispatch(toggleRememberAlbum());
  }

  onRememberArtistCheck = () => {
    this.props.dispatch(toggleRememberArtist());
  }

  onClose = () => {
    this.props.dispatch(closeDialog());
  }

  onRestore = (e) => {
    e.preventDefault();
    this.props.dispatch(restore());
  }

  render() {
    const { lastFmStore } = this.props;
    const scrobble = lastFmStore.get('scrobble');
    const dialogOpen = lastFmStore.get('dialogOpen');
    const rememberAlbum = lastFmStore.get('rememberAlbum');
    const rememberArtist = lastFmStore.get('rememberArtist');

    return (
      <Dialog
        lazy
        isOpen={dialogOpen}
        className="scrobble-dialog"
        onClose={this.onClose}
      >
        <div className="dialog-body">
          <h4>Scrobble to last.fm</h4>
          <div className="input-section">
            <label htmlFor="artist">Artist</label>
            <InputGroup
              id="artist"
              autoFocus
              className="input"
              placeholder="Artist"
              type="text"
              value={scrobble.get('artist')}
              onChange={(e) => this.onFieldChanged('artist', e.target.value)}
            />
            <Checkbox
              checked={rememberArtist}
              onChange={this.onRememberArtistCheck}
            >
              Remember for the current queue
            </Checkbox>
          </div>
          <div className="input-section">
            <label htmlFor="title">Title</label>
            <InputGroup
              id="title"
              className="input"
              placeholder="Title"
              type="text"
              value={scrobble.get('title')}
              onChange={(e) => this.onFieldChanged('title', e.target.value)}
            />
          </div>
          <div className="input-section">
            <label htmlFor="album">Album</label>
            <InputGroup
              id="album"
              className="input lower"
              placeholder="Optional"
              type="text"
              value={scrobble.get('album')}
              onChange={(e) => this.onFieldChanged('album', e.target.value)}
            />
            <Checkbox
              checked={rememberAlbum}
              onChange={this.onRememberAlbumCheck}
            >
              Remember for the current queue
            </Checkbox>
          </div>
          <div className="actions">
            <Button
              className="input button"
              onClick={this.onClose}
            >
              Confirm
            </Button>
            <a href="" onClick={this.onRestore}>Restore Default</a>
          </div>
        </div>
      </Dialog>
    );
  }
}

LastFmScrobbleDialog.propTypes = {
  dispatch: PropTypes.func,
  lastFmStore: ImmutablePropTypes.map.isRequired,
};

LastFmScrobbleDialog.defaultProps = {
  dispatch: () => {},
};

const mapStateToProps = (state) => ({
  lastFmStore: state.lastFmReducer,
});

export default connect(mapStateToProps)(LastFmScrobbleDialog);
