import { fromJS } from 'immutable';
import {
  fetchChannels,
  fetchPlaylistItems,
  fetchPlaylists,
} from 'lib/youtube';

import reddit from 'lib/reddit';
import actionTypes from './actionTypes';

export const createPlaylistPreview = (playlistID) => (
  (dispatch) => {
    fetchPlaylists([playlistID]).then((pl) => {
      const playlist = fromJS(pl[0]);
      fetchPlaylistItems(playlistID).then(({ tracks, nextPageToken }) => {
        dispatch({
          type: actionTypes.SET_PLAYLIST_PREVIEW,
          payload: { playlist, nextPageToken, tracks },
        });
      }).catch(() => {
        dispatch({
          type: actionTypes.PLAYLIST_NOT_FOUND,
          payload: { playlistID },
        });
      });
    });
  }
);

export const createRadioPreview = (stationKey, sort, t) => (
  (dispatch) => new Promise((resolve, reject) => {
    /*
      Recursive explanation:
      when sorting subreddits by best of all time, the first or even second page
      might not have any videos in them the original method, if it finds no tracks,
      it will assume there is nothing else to load. but in the recursive way,
      it will paginate (i.e update the nextPage token) until it gets at least 5 more tracks.
    */
    const fetchReddit = (allTracks = [], pageToken = '') => {
      reddit.search(stationKey, pageToken, sort, t).then(({ tracks, nextPageToken }) => {
        allTracks = [...allTracks, ...tracks];

        dispatch({
          type: actionTypes.SET_RADIO_PREVIEW,
          payload: { stationKey, nextPageToken, tracks: allTracks },
        });

        if (allTracks.length && allTracks.length < 5) {
          fetchReddit(allTracks, nextPageToken);
        } else {
          resolve(allTracks);
        }
      }).catch((err) => reject(err));
    };

    fetchReddit();
  })
);

export const updatePlaylistPreview = (playlist) => (
  (dispatch, getState) => {
    /*
      Recursive explanation:
      when sorting subreddits by best of all time, the first or even second page
      might not have any videos in them the original method, if it finds no tracks,
      it will assume there is nothing else to load. but in the recursive way,
      it will paginate (i.e update the nextPage token) until it gets at least 5 more tracks.
    */
    if (playlist.get('type') === 'radio') {
      return new Promise((resolve) => {
        const radioStore = getState().radioReducer;
        const sort = radioStore.get('sort');
        const t = radioStore.get('t');

        const fetchReddit = (allTracks = [], pageToken = '') => {
          reddit.loadMoreTracks(playlist.get('id'), pageToken, sort, t).then(({ tracks, nextPageToken }) => {
            allTracks = [...allTracks, ...tracks];

            dispatch({
              type: actionTypes.UPDATE_PLAYLIST_PREVIEW,
              payload: { playlist, nextPageToken, tracks },
            });

            if (allTracks.length && allTracks.length < 5) {
              // recursive here
              fetchReddit(allTracks, nextPageToken);
            } else {
              resolve(allTracks);
            }
          });
        };

        // entry point
        fetchReddit([], playlist.get('nextPageToken'));
      });
    }

    let id;

    if (playlist.get('type') === 'channel') {
      id = playlist.get('playlistId');
    } else {
      id = playlist.get('id');
    }

    return fetchPlaylistItems(id, playlist.get('nextPageToken')).then(({ tracks, nextPageToken }) => {
      dispatch({
        type: actionTypes.UPDATE_PLAYLIST_PREVIEW,
        payload: { playlist, nextPageToken, tracks },
      });

      return tracks;
    });
  }
);

export const createChannelPreview = (channelID) => (
  (dispatch) => (
    fetchChannels([channelID]).then((chnl) => {
      const channel = fromJS(chnl[0]);
      fetchPlaylistItems(channel.get('playlistId')).then(({ tracks, nextPageToken }) => {
        dispatch({
          type: actionTypes.SET_PLAYLIST_PREVIEW,
          payload: { playlist: channel, nextPageToken, tracks },
        });
      }).catch(() => {
        // channel has no tracks. can happen
        dispatch({
          type: actionTypes.SET_PLAYLIST_PREVIEW,
          payload: { playlist: channel, nextPageToken: null, tracks: [] },
        });
      });
    })
  )
);
