import actionTypes from 'actions/actionTypes';
import { playerStates, formatDuration } from 'lib/youtube';
import { fromJS } from 'immutable';
import { LOCATION_CHANGE } from 'react-router-redux';
import moment from 'moment-mini';

import { AD_VIDEO } from '../lib/constants';

// matches 00:00 and 00:00:00 and 0:00:0, etc
const TIME_PIN_REGEX = /\d{1,2}(:)\d{1,2}(:)?\d{1,2}/;

const initialState = fromJS({
  currentTrack: {
    // artist: 'Relaxing Music Mix',
    // duration: 4028,
    // durationClockFormat: '1:07:08',
    // id: 'NliYy7iqh-U',
    // image: 'https://i.ytimg.com/vi/NliYy7iqh-U/maxresdefault.jpg',
    // thumbnail: 'https://i.ytimg.com/vi/NliYy7iqh-U/mqdefault.jpg',
    // title: 'Beautiful Piano',
    // type: 'video',
  },
  playerState: playerStates.UNSTARTED,
  playerActive: false,
  queue: {
    tracks: [],
    tracksOrder: {},
    source: '',
    shuffled: false,
    currentIndex: 0,
    id: 0,
  },
  queueBeforeAlbum: null,
  trackBeforeAlbum: null,
  currentStation: {},
  currentCollection: {},
  currentPlaylist: {},
  currentPin: null,
  repeatMode: null,
  skipCount: 0,
});

const getPreviousIndex = (state) => {
  const currentIndex = state.getIn(['queue', 'currentIndex']);
  if (currentIndex > 0) {
    return currentIndex - 1;
  }

  return null;
};

const getNextIndex = (state) => {
  const queueSize = state.getIn(['queue', 'tracks']).size;
  const currentIndex = state.getIn(['queue', 'currentIndex']);
  if (currentIndex < queueSize - 1) {
    return currentIndex + 1;
  }

  return null;
};

const indexQueue = (queue) => {
  const indexed = {};
  // map the tracks payload from [{...}, {...}] to { 'id1': 0, 'id2':1 } for faster access
  queue.forEach((track, i) => { indexed[track.get('id')] = i; });
  return indexed;
};

const parseAlbumTrackList = (track) => {
  const trackList = track.get('albumTrackList');
  const jsTrack = track.toJS();

  const getPin = (trackRow) => {
    if (!trackRow) return null;
    const timePointMatch = trackRow.match(TIME_PIN_REGEX);
    if (!timePointMatch) return null;

    let timePoint = '';
    if (timePointMatch[2] === undefined) {
      timePoint = `00:${timePointMatch[0]}`;
    } else {
      [timePoint] = timePointMatch;
    }

    return moment.duration(timePoint).asSeconds();
  };

  return trackList.map((trackRow, i) => {
    // the regex selects ':' to figure out if it is MM:SS or HH:MM:SS
    // moment expects to always be HH:MM:SS
    let duration;
    const pin = getPin(trackRow);
    const nextPin = getPin(trackList.get(i + 1));
    if (nextPin) {
      duration = nextPin - pin;
    } else {
      duration = track.get('duration') - pin;
    }

    return fromJS({
      ...jsTrack,
      pin,
      title: trackRow,
      album: jsTrack.title,
      durationClockFormat: formatDuration(duration),
      albumTrackList: null,
      albumDurationClockFormat: track.get('durationClockFormat'),
    });
  });
};

const playerReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.LOAD_TRACK:
      return state
        .set('currentTrack', payload)
        .set('playerState', playerStates.PLAYING);

    case actionTypes.LOAD_QUEUE: {
      const indexedQueue = indexQueue(payload.queue);

      return state
        .mergeIn(['queue'], {
          tracks: fromJS(payload.queue),
          tracksOrder: fromJS(indexedQueue),
          source: payload.source,
          shuffled: false,
          currentIndex: 0,
          id: state.getIn(['queue', 'id']) + 1,
        })
        .set('currentPin', null)
        .set('queueBeforeAlbum', null);
    }

    case actionTypes.QUEUE_REORDER: {
      const indexedQueue = indexQueue(payload.queue);
      return state
        .mergeIn(['queue'], {
          tracks: fromJS(payload.queue),
          tracksOrder: fromJS(indexedQueue),
        });
    }

    case actionTypes.PAUSE:
      window.ytPlayer.pauseVideo();
      return state;

    case actionTypes.PLAY:
      window.ytPlayer.playVideo();
      return state;

    case actionTypes.YT_PAUSE:
      return state.set('playerState', playerStates.PAUSED);

    case actionTypes.YT_PLAY:
      return state.set('playerState', playerStates.PLAYING);

    case actionTypes.SHOW_PLAYER:
      return state.set('playerActive', true);

    case actionTypes.HIDE_PLAYER:
    case LOCATION_CHANGE:
      return state.set('playerActive', false);

    case actionTypes.PLAY_SILENT_AD: {
      return state
        .set('currentTrack', AD_VIDEO)
        .set('skipCount', 0)
        .set('playerState', playerStates.PLAYING);
    }

    case actionTypes.INCREMENT_SKIP_COUNT: {
      const skipCount = state.get('skipCount');

      return state.set('skipCount', skipCount + 1);
    }

    case actionTypes.PLAY_NEXT: {
      const repeatMode = state.get('repeatMode');
      const nextIndex = getNextIndex(state);

      switch (repeatMode) {
        case 'one':
          // this is a crime to do this here. but there is no other way
          ytPlayer.seekTo(0);
          return state;
        case 'all':
          if (nextIndex === null) {
            return state
              .set('currentTrack', state.getIn(['queue', 'tracks', 0]))
              .setIn(['queue', 'currentIndex'], 0);
          }
        /* eslint-disable no-fallthrough */
        default: {
          const nextTrack = state.getIn(['queue', 'tracks', nextIndex]);
          if (nextIndex === null) return state;

          return state
            .set('currentTrack', nextTrack)
            .set('playerState', playerStates.PLAYING)
            .setIn(['queue', 'currentIndex'], nextIndex);
        }
        /* eslint-enable no-fallthrough */
      }
    }

    case actionTypes.PLAY_NEXT_PIN: {
      const repeatMode = state.get('repeatMode');
      const nextIndex = getNextIndex(state);

      const currentPin = state.get('currentPin');
      switch (repeatMode) {
        case 'one':
          ytPlayer.seekTo(currentPin);
          return state;
        case 'all':
          if (nextIndex === null) {
            ytPlayer.seekTo(0);
            return state
              .set('currentPin', state.getIn(['queue', 'tracks', 0, 'pin']))
              .setIn(['queue', 'currentIndex'], 0);
          }

          return state;
        default: {
          if (nextIndex === null) return state;

          const nextTrack = state.getIn(['queue', 'tracks', nextIndex]);
          const nextPin = nextTrack.get('pin');

          ytPlayer.seekTo(nextPin);

          return state
            .set('currentTrack', nextTrack)
            .set('currentPin', nextPin)
            .setIn(['queue', 'currentIndex'], nextIndex);
        }
      }
    }

    case actionTypes.PLAY_PREVIOUS_PIN: {
      const previousIndex = getPreviousIndex(state);
      if (previousIndex === null) return state;
      const previousTrack = state.getIn(['queue', 'tracks', previousIndex]);

      ytPlayer.seekTo(previousTrack.get('pin'));
      return state
        .set('currentPin', previousTrack.get('pin'))
        .set('playerState', playerStates.PLAYING)
        .setIn(['queue', 'currentIndex'], previousIndex);
    }

    case actionTypes.PLAY_CURRENT_PIN: {
      let pinIndex = 0;
      let track = null;
      state.getIn(['queue', 'tracks']).forEach((t, i) => {
        if (t.get('pin') === payload) {
          track = t;
          pinIndex = i;
        }
      });

      window.ytPlayer.seekTo(payload);

      return state
        .set('currentTrack', track)
        .set('currentPin', payload)
        .setIn(['queue', 'currentIndex'], pinIndex);
    }

    // same as PLAY_CURRENT_PIN only it's not playing
    case actionTypes.SET_CURRENT_PIN: {
      let pinIndex = 0;
      let track = null;
      state.getIn(['queue', 'tracks']).forEach((t, i) => {
        if (t.get('pin') === payload) {
          track = t;
          pinIndex = i;
        }
      });

      return state
        .set('currentTrack', track)
        .set('currentPin', payload)
        .setIn(['queue', 'currentIndex'], pinIndex);
    }

    case actionTypes.PLAY_PREVIOUS: {
      const previousIndex = getPreviousIndex(state);
      if (previousIndex === null) return state;
      const previousTrack = state.getIn(['queue', 'tracks', previousIndex]);

      return state
        .set('currentTrack', previousTrack)
        .set('playerState', playerStates.PLAYING)
        .setIn(['queue', 'currentIndex'], previousIndex);
    }

    // TODO CHANGE THIS
    case actionTypes.SET_RELATED_STATION:
      return state.set('currentStation', fromJS({ key: '', name: payload }));

    case actionTypes.SET_CURRENT_RADIO_STATION:
      // payload is stationKey
      return state.set('currentStation', payload);

    case actionTypes.SET_CURRENT_COLLECTION:
      return state.merge({
        currentCollection: payload,
        currentPlaylist: {},
      });

    case actionTypes.SET_CURRENT_PLAYLIST:
      return state.merge({
        currentCollection: {},
        currentPlaylist: payload,
      });

    case actionTypes.SHUFFLE_ON: {
      const tracksOrder = state.getIn(['queue', 'tracksOrder']);

      if (!tracksOrder.size) {
        return state;
      }

      const currentTrack = state.get('currentTrack');
      const tracks = state.getIn(['queue', 'tracks']).delete(tracksOrder.get(currentTrack.get('id')));
      const shuffled = tracks.sortBy(() => Math.random()).unshift(currentTrack);

      ytPlayer.send('shuffle', true);

      return state
        .mergeIn(['queue'], {
          shuffled: true,
          tracks: shuffled,
          currentIndex: 0,
        });
    }

    case actionTypes.SHUFFLE_OFF: {
      const tracks = state.getIn(['queue', 'tracks']);
      const tracksOrder = state.getIn(['queue', 'tracksOrder']);

      ytPlayer.send('shuffle', false);

      return state
        .mergeIn(['queue'], {
          shuffled: false,
          tracks: tracks.sortBy((t) => tracksOrder.get(t.get('id'))),
        });
    }

    case actionTypes.APPEND_TO_QUEUE: {
      const tracks = state.getIn(['queue', 'tracks']).concat(payload);
      const tracksOrder = indexQueue(tracks);
      return state.mergeIn(['queue'], { tracks, tracksOrder });
    }

    case actionTypes.RESET_QUEUE_CURRENT_INDEX: {
      let currentIndex = 0;
      state.getIn(['queue', 'tracks']).some((t, i) => {
        currentIndex = i;
        return t.get('id') === state.getIn(['currentTrack', 'id']);
      });

      return state.setIn(['queue', 'currentIndex'], currentIndex);
    }

    case actionTypes.SET_CURRENT_INDEX_FROM_TRACK: {
      const index = state.getIn(['queue', 'tracksOrder', payload.get('id')]);
      return state.setIn(['queue', 'currentIndex'], index);
    }

    case actionTypes.SET_REPEAT_MODE: {
      const tracksOrder = state.getIn(['queue', 'tracksOrder']);

      if (!tracksOrder.size) {
        return state;
      }

      ytPlayer.send('repeat', payload);
      return state.set('repeatMode', payload);
    }

    case actionTypes.ADD_TO_PLAY_NEXT: {
      const queue = state.get('queue');
      const currentIndex = queue.get('currentIndex');
      let tracks;

      // remove track if already exists
      tracks = queue.get('tracks').filter((track) => track.get('id') !== payload.get('id'));

      // insert track AFTER current track
      tracks = tracks.splice(currentIndex + 1, 0, payload);

      // rebuild order
      const tracksOrder = indexQueue(tracks);

      return state.mergeIn(['queue'], { tracks, tracksOrder });
    }

    case actionTypes.REMOVE_FROM_QUEUE: {
      const queue = state.get('queue');
      const index = queue.getIn(['tracksOrder', payload.get('id')]);
      const tracks = queue.get('tracks').splice(index, 1);
      const tracksOrder = indexQueue(tracks);
      return state.mergeIn(['queue'], { tracks, tracksOrder });
    }

    case actionTypes.ALBUMIZE_QUEUE: {
      const track = payload || state.get('currentTrack');
      const albumTrackList = track.get('albumTrackList');
      const queueBeforeAlbum = state.get('queue');

      if (!albumTrackList) return state;
      const tracks = parseAlbumTrackList(track);

      const tracksOrder = indexQueue(tracks);
      return state
        .set('currentTrack', tracks.get(0))
        .set('currentPin', tracks.get(0).get('pin'))
        .set('queueBeforeAlbum', queueBeforeAlbum)
        .set('trackBeforeAlbum', track)
        .mergeIn(['queue'], {
          tracks,
          tracksOrder,
          source: 'album',
          currentIndex: 0,
        });
    }

    case actionTypes.SHUFFLE_ALBUM_ON: {
      // the 'album' tracks are not based on tracksOrder.
      const currentPin = state.get('currentPin');
      let currentTrack;

      const tracks = state.getIn(['queue', 'tracks']).filter((track) => {
        if (track.get('pin') === currentPin) {
          currentTrack = track; // in order to save two loops.
          return false;
        }
        return true;
      });

      const shuffled = tracks.sortBy(() => Math.random()).unshift(currentTrack);

      ytPlayer.send('shuffle', true);

      return state
        .mergeIn(['queue'], {
          shuffled: true,
          tracks: shuffled,
          currentIndex: 0,
        });
    }

    case actionTypes.DISABLE_ALBUM_MODE:
      return state.merge({
        trackBeforeAlbum: null,
        queueBeforeAlbum: null,
        currentPin: null,
      });

    default:
      return state;
  }
};

export default playerReducer;
