import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Switch } from '@blueprintjs/core';
import { connect } from 'react-redux';
import ImmutablePropTypes from 'react-immutable-proptypes';
import Subscription from 'lib/Subscription';
import { DIALOGS } from 'lib/constants';
import { List } from 'immutable';
import {
  albumizeQueue,
  loadQueue,
  loadTrack,
} from 'actions/playerActions';

import {
  openDialog,
} from 'actions/appActions';

class AlbumizeSwitcher extends PureComponent {
  get canBeAlbumized() {
    const { track } = this.props;
    return track.get('albumTrackList') || this.isAlbumized;
  }

  get isAlbumized() {
    const { playerStore } = this.props;
    return playerStore.get('currentPin') !== null;
  }

  albumizeTrack = () => {
    if (!Subscription.isPro()) {
      this.props.dispatch(openDialog(DIALOGS.PRO_DISCOVER));
      return;
    }

    ga('send', 'event', 'Album Switcher');

    const { dispatch, playerStore } = this.props;
    // toggle model
    if (this.isAlbumized) {
      const queue = playerStore.get('queueBeforeAlbum');
      const originalTrack = playerStore.get('trackBeforeAlbum');

      dispatch(loadQueue(List(), queue.get('source')));
      setTimeout(() => {
        dispatch(loadQueue(queue.get('tracks'), queue.get('source')));
        dispatch(loadTrack(originalTrack));
      }, 300);
    } else {
      dispatch(albumizeQueue());
    }
  }

  render() {
    if (!this.canBeAlbumized) return null;

    return (
      <div className="albumize">
        <Switch
          checked={this.isAlbumized}
          onChange={this.albumizeTrack}
        />
        <span className="album-tag">Album</span>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  playerStore: state.playerReducer,
});

AlbumizeSwitcher.propTypes = {
  track: ImmutablePropTypes.map.isRequired,
  dispatch: PropTypes.func.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
};

export default connect(mapStateToProps)(AlbumizeSwitcher);
