import actionTypes from 'actions/actionTypes';
import { fromJS } from 'immutable';
import CollectionBuilder from 'lib/CollectionBuilder';

export const initialState = fromJS({
  addDialog: {
    isOpen: true,
    trackToAdd: {},
  },
  newLikeAdded: false,
  collections: {},
});

const addTrackToCollection = (track, collection) => (
  collection
    .setIn(['tracks', track.get('id')], track)
    .setIn(['order', track.get('id')], collection.get('tracks').size) // append track to the end
);

const addTrackToLikes = (track, collection) => {
  const withNew = collection
    .setIn(['tracks', track.get('id')], track)
    .setIn(['order', track.get('id')], -1); // append track to the beginning. -1 guaranteed to be first

  const sortedByOrder = withNew.get('order').keySeq().sortBy((id) => withNew.get('order').get(id)).toArray();

  // rebuild the order object with the new track at the beginning.
  const order = sortedByOrder.reduce((obj, id, i) => {
    obj[id] = i;
    return obj;
  }, {});

  return withNew.set('order', fromJS(order));
};

const removeTrackFromCollection = (track, collection) => (
  collection
    .deleteIn(['tracks', track.get('id')])
    .deleteIn(['order', track.get('id')])
);

const collectionsReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.TOGGLE_ADD_TO_COLLECTION_DIALOG:
      return state
        .setIn(['addDialog', 'isOpen'], !!payload)
        .setIn(['addDialog', 'trackToAdd'], payload);

    case actionTypes.ADD_TRACK_TO_COLLECTION: {
      const collection = addTrackToCollection(payload.track, payload.collection);
      return state.setIn(['collections', collection.get('id')], collection);
    }

    case actionTypes.REMOVE_TRACK_FROM_COLLECTION: {
      const collection = removeTrackFromCollection(payload.track, payload.collection);
      return state.setIn(['collections', collection.get('id')], collection);
    }

    case actionTypes.CREATE_COLLECTION: {
      const collection = new CollectionBuilder({ ...payload });

      return state.setIn(['collections', collection.id], fromJS(collection.build()));
    }

    case actionTypes.RENAME_COLLECTION: {
      return state.setIn([
        'collections',
        payload.collection.get('id'),
        'name',
      ], payload.newName);
    }

    case actionTypes.DELETE_COLLECTION: {
      return state.deleteIn(['collections', payload.get('id')]);
    }

    case actionTypes.ADD_TRACK_TO_LIKES_COLLECTION: {
      const likesCollection = state.getIn(['collections', 'likes']);
      const collection = addTrackToLikes(payload.track, likesCollection);

      return state.setIn(['collections', 'likes'], collection);
    }

    case actionTypes.REMOVE_TRACK_FROM_LIKES_COLLECTION: {
      const likesCollection = state.getIn(['collections', 'likes']);
      const collection = removeTrackFromCollection(payload.track, likesCollection);

      return state.setIn(['collections', 'likes'], collection);
    }

    case actionTypes.TOGGLE_NEW_LIKES_ADDED:
      return state.set('newLikeAdded', !state.get('newLikeAdded'));

    case actionTypes.STORE_COLLECTIONS:
      return state.set('collections', payload);

    case actionTypes.COLLECTION_REORDER: {
      const { collection, tracks } = payload;

      // rebuild the `order` object
      const order = tracks.reduce((obj, track, i) => {
        obj[track.get('id')] = i;
        return obj;
      }, {});

      return state.setIn(['collections', collection.get('id'), 'order'], fromJS(order));
    }

    default:
      return state;
  }
};

export default collectionsReducer;
