import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import ImmutablePropTypes from 'react-immutable-proptypes';
import {
  updateSearchBarValue,
  stepKeyboadPick,
} from 'actions/radioActions';

const BOTTOM_INDEX = -2;

class RadioSearch extends PureComponent {
  onChange = (e) => {
    const { dispatch } = this.props;
    e.stopPropagation();
    dispatch(updateSearchBarValue(e.target.value));
  }

  inputKeyUp = (e) => {
    e.stopPropagation();
    e.preventDefault();

    const { dispatch, radioStore } = this.props;

    const filterredKeys = radioStore.get('filterredKeys');
    const selectedSearchIndex = radioStore.get('selectedSearchIndex');
    const searchBarValue = radioStore.get('searchBarValue');

    if (!searchBarValue.length) {
      dispatch(stepKeyboadPick(-1));
      return;
    }

    switch (e.key) {
      case 'ArrowDown':
        if (selectedSearchIndex < filterredKeys.length - 1) {
          dispatch(stepKeyboadPick(selectedSearchIndex + 1));
        }
        break;
      case 'ArrowUp':
        if (selectedSearchIndex > BOTTOM_INDEX) {
          dispatch(stepKeyboadPick(selectedSearchIndex - 1));
        }
        break;
      case 'Enter': {
        const selectedStation = filterredKeys[selectedSearchIndex];
        const selected = selectedStation || searchBarValue;

        if (!selected || !selected.length) return;

        this.props.onSelect(selected);
        dispatch(stepKeyboadPick(BOTTOM_INDEX));
        break;
      }
      case 'Escape':
        dispatch(updateSearchBarValue(''));
        dispatch(stepKeyboadPick(BOTTOM_INDEX));
        break;
      default:
        break;
    }
  }

  render() {
    const { radioStore } = this.props;
    const searchBarValue = radioStore.get('searchBarValue');

    return (
      <div className="search-bar">
        <i className="material-icons search-icon">search</i>

        <input
          type="text"
          autoFocus
          placeholder="Filter / Search reddit.com"
          onChange={this.onChange}
          onKeyUp={this.inputKeyUp}
          value={searchBarValue}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  radioStore: state.radioReducer,
});

RadioSearch.propTypes = {
  dispatch: PropTypes.func,
  radioStore: ImmutablePropTypes.map.isRequired,
  onSelect: PropTypes.func,
};

RadioSearch.defaultProps = {
  dispatch: () => {},
  onSelect: () => {},
};

export default connect(mapStateToProps)(RadioSearch);
