import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import classnames from 'classnames';
import { post, get } from 'lib/http';
import { Dialog, Checkbox, InputGroup, Intent, Button } from '@blueprintjs/core';
import validator from 'email-validator';
import passwordValidator from 'password-validator';
import { fromJS } from 'immutable';
import { ExternalLink } from 'components';

const passwordSchema = new passwordValidator();
passwordSchema.is().min(8); // Minimum length 8

const INITIAL_STATE = {
  email: { value: '', error: null },
  password: { value: '', error: null },
  firstName: { value: '', error: null },
  lastName: { value: '', error: null },
  passwordAgain: { value: '', error: null },
  agreeToTerms: { value: false, error: null },
  globalError: '',
  isLoading: false,
  errorCount: 0,
  showConsent: false,
};

class UserForm extends PureComponent {
  constructor(props) {
    super(props);
    this.state = INITIAL_STATE;
  }

  UNSAFE_componentWillMount() {
    document.addEventListener('keyup', this.onEnterKey, false);
  }

  componentWillUnmount() {
    document.removeEventListener('keyup', this.onEnterKey, false);
  }

  onFieldChange = (field, value) => {
    this.setState({ [field]: { value } });
  }

  onEnterKey = (e) => {
    if (e.key === 'Enter') {
      this.onActionClick();
    }
  }

  onActionClick = () => {
    if (!this.validate()) {
      return;
    }

    const {
      email,
      password,
      firstName,
      lastName,
    } = this.state;

    this.setState({
      isLoading: true,
      globalError: '',
    });

    const path = this.isLogin ? 'login' : 'users';

    const data = {
      email: email.value,
      password: password.value,
      firstName: firstName.value,
      lastName: lastName.value,
    };

    post({ path, data }).then(() => (
      get({ path: 'sessions', data: { email: data.email } })
        .then((response) => {
          const user = fromJS(response.data);
          if (this.isLogin) {
            this.props.onLogin(user);
          } else {
            this.props.onSignup(user);
          }
        })
    )).catch((response) => {
      this.setState({
        isLoading: false,
        globalError: response.data.message || '',
      });
    });
  }

  onAgreeToTerms = () => {
    const { agreeToTerms } = this.state;

    if (!agreeToTerms.value) {
      this.setState({ showConsent: true });
    }

    this.onFieldChange('agreeToTerms', !agreeToTerms.value);
  }

  onConsentClose = () => {
    this.setState({ showConsent: false });
  }

  get isLogin() {
    const { isLogin } = this.props;
    return isLogin;
  }

  get forgotPasswordLink() {
    return (
      <a
        href=""
        onClick={(e) => {
          e.preventDefault();
          this.props.onForgotPasswordClick(); // eslint-disable-line react/destructuring-assignment
        }}
      >
        Forgot Password?
      </a>
    );
  }

  changeToSignupForm = () => {
    this.props.onFormChange();
    this.emailInput.focus();
  }

  validate() {
    const {
      email,
      password,
      passwordAgain,
      firstName,
      lastName,
      agreeToTerms,
    } = this.state;

    let emailError;
    let passwordError;
    let firstNameError;
    let lastNameError;
    let termsError;
    let passwordAgainError;

    if (email.value === '' || !validator.validate(email.value)) {
      emailError = 'Please enter a valid email';
    }

    if (!passwordSchema.validate(password.value || 'fake')) {
      passwordError = this.isLogin ? null : 'Password must be at least 8 characters';
    }

    if (this.isLogin && password.value === '') {
      passwordError = 'Please enter your password';
    }

    if (passwordAgain.value !== password.value && !this.isLogin) {
      passwordAgainError = 'Passwords do not match';
    }

    if (firstName.value === '' && !this.isLogin) {
      firstNameError = 'Please enter your first name';
    }

    if (lastName.value === '' && !this.isLogin) {
      lastNameError = 'Please enter your last name';
    }

    if (!agreeToTerms.value && !this.isLogin) {
      termsError = 'Please read and agree to the terms of use';
    }

    if (emailError || passwordError || passwordAgainError
        || firstNameError || lastNameError || termsError) {
      this.setState({
        password: { ...password, error: passwordError },
        passwordAgain: { ...passwordAgain, error: passwordAgainError },
        email: { ...email, error: emailError },
        firstName: { ...firstName, error: firstNameError },
        lastName: { ...lastName, error: lastNameError },
        agreeToTerms: { ...agreeToTerms, error: termsError },
      });

      // invalid
      return false;
    }

    return true;
  }

  renderError() {
    const { globalError } = this.state;
    if (!globalError.length) {
      return null;
    }

    return (
      <div className="global-error">
        {globalError}
      </div>
    );
  }

  renderConsentDialog() {
    const { showConsent } = this.state;
    return (
      <Dialog
        isOpen={showConsent}
        onClose={this.onConsentClose}
      >
        <div className="gdpr-consent">
          <p>We use cookies to securely identify your account and keep you signed in.</p>
          <p>We use Google analytics to improve the product and troubleshoot customer issues.</p>
          <p>
            A customer may withdraw their consent and delete their data at any time by deleting their account via the
            account settings menu.
          </p>
        </div>
        <Button
          text="ok"
          className="consent-button upper"
          onClick={this.onConsentClose}
        />
      </Dialog>
    );
  }

  render() {
    const {
      isLoading,
      email,
      password,
      passwordAgain,
      firstName,
      lastName,
      agreeToTerms,
    } = this.state;

    return (
      <div
        className={classnames('user-form-content form-wrapper', { register: !this.isLogin })}
      >
        {this.renderError()}
        {this.renderConsentDialog()}
        {this.isLogin
          ? (
            <p className="form-title">
              <b>Log in to continue</b>
              <span>
                { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
                New user? <a onClick={this.changeToSignupForm}>Sign-up Now</a>
              </span>
            </p>
          ) : (
            <p className="form-title">
              <b>Sign Up</b>
              <span>
                <a onClick={this.changeToSignupForm}>Back to login</a>
              </span>
            </p>
          )}
        <div className={classnames('form', { login: this.isLogin })}>
          <div className="form-field">
            <div className="error">
              {email.error}
            </div>
            <InputGroup
              autoFocus
              inputRef={(node) => { this.emailInput = node; }}
              placeholder="Email"
              type="email"
              className={classnames('input', { 'with-error': email.error })}
              onChange={(e) => this.onFieldChange('email', e.target.value)}
              disabled={isLoading}
              intent={email.error ? Intent.DANGER : ''}
            />
          </div>
          <div className="form-field">
            <div className="error">
              {password.error}
            </div>
            <InputGroup
              placeholder="Password"
              type="password"
              className={classnames('input', { 'with-error': password.error })}
              onChange={(e) => this.onFieldChange('password', e.target.value)}
              disabled={isLoading}
              intent={password.error ? Intent.DANGER : ''}
            />
          </div>
          <div className={classnames('sign-up-section', { hidden: this.isLogin })}>
            <div className="form-field">
              <div className="error">
                {passwordAgain.error}
              </div>
              <InputGroup
                placeholder="Repeat Password"
                type="password"
                className={classnames('input', { 'with-error': passwordAgain.error })}
                onChange={(e) => this.onFieldChange('passwordAgain', e.target.value)}
                disabled={isLoading}
                intent={passwordAgain.error ? Intent.DANGER : ''}
              />
            </div>
            <div className="form-field">
              <div className="error">
                {firstName.error}
              </div>
              <InputGroup
                placeholder="First Name"
                type="text"
                className={classnames('input', { 'with-error': firstName.error })}
                onChange={(e) => this.onFieldChange('firstName', e.target.value)}
                disabled={isLoading}
                intent={firstName.error ? Intent.DANGER : ''}
              />
            </div>
            <div className="form-field">
              <div className="error">
                {lastName.error}
              </div>
              <InputGroup
                placeholder="Last Name"
                type="text"
                className={classnames('input', { 'with-error': lastName.error })}
                onChange={(e) => this.onFieldChange('lastName', e.target.value)}
                disabled={isLoading}
                intent={lastName.error ? Intent.DANGER : ''}
              />
            </div>
            <div className="form-field checkbox">
              <Checkbox
                checked={agreeToTerms.value}
                onChange={this.onAgreeToTerms}
              >
                <ExternalLink href="https://headsetapp.co/terms">
                  I Agree to the Terms of Service
                </ExternalLink>
              </Checkbox>
              <br />
              <div className="error">
                {agreeToTerms.error}
              </div>
            </div>
          </div>
          <div className="action-button">
            <Button
              className="input button"
              intent={this.isLogin ? Intent.PRIMARY : Intent.SUCCESS}
              onClick={this.onActionClick}
              loading={isLoading}
            >
              {this.isLogin ? 'Log In' : 'Sign Up'}
            </Button>
          </div>
          {this.isLogin ? this.forgotPasswordLink : null}
        </div>
      </div>
    );
  }
}

UserForm.propTypes = {
  isLogin: PropTypes.bool,
  onLogin: PropTypes.func.isRequired,
  onSignup: PropTypes.func.isRequired,
  onForgotPasswordClick: PropTypes.func.isRequired,
  onFormChange: PropTypes.func.isRequired,
};

UserForm.defaultProps = {
  isLogin: true,
};

export default UserForm;
