/* eslint-disable camelcase */
import axios from 'axios';
import md5 from 'md5';
import qs from 'qs';
import { put } from 'lib/http';
import Confg from 'config';
import store from '../store';

const api_key = '2c701630b16b1ed4d4232e9ac4dd270d';
const secret = '2ed0bc4d7bcba3c429b4cb45cf48de75';
const LAST_FM_URL = 'https://ws.audioscrobbler.com/2.0/';
const format = 'json';

const callbackPage = `${Confg.rootUrl}/#lastfm`;

const generateSig = (params) => {
  const keys = Object.keys(params);

  // params has to be ordered alphabetically
  keys.sort();

  let o = '';
  keys.forEach((key) => {
    o = `${o}${key}${params[key]}`;
  });

  return md5(o + secret);
};

const getParameterByName = (name) => {
  const url = window.location.href;
  // name = name.replace(/[\[\]]/g, '\\$&');
  const regex = new RegExp(`[?&]${name}(=([^&#]*)|&|#|$)`);
  const results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return '';

  return decodeURIComponent(results[2].replace(/\+/g, ' '));
};

const currentUser = () => store.getState().appReducer.get('user');

class LastFm {
  static connect() {
    if (!currentUser()) return null;
    const token = getParameterByName('token');
    if (!token) return null;

    const params = {
      method: 'auth.getSession',
      token,
      api_key,
    };

    const api_sig = generateSig(params);

    return axios.get(LAST_FM_URL, {
      params: { ...params, api_sig, format },
    }).then(({ data }) => {
      ga('send', 'event', 'LastFm - Connect');
      const lastFmSessionId = data.session.key;
      const lastFmUsername = data.session.name;
      return put({
        path: 'users',
        data: {
          email: currentUser().get('email'),
          lastFmSessionId,
          lastFmUsername,
        },
      });
    });
  }

  static disconnect() {
    if (!currentUser()) return;
    put({ path: 'users', data: { email: currentUser().get('email'), lastFmSessionId: null, lastFmUsername: null } })
      .then(() => {
        ga('send', 'event', 'LastFm - Disconnect');
        window.location.reload();
      });
  }

  static scrobble({ track, artist, album = '' }) {
    if (!currentUser()) return;
    const sk = currentUser().get('lastFmSessionId');
    if (!sk) return;

    const params = {
      method: 'track.scrobble',
      'artist[0]': artist,
      'track[0]': track,
      'album[0]': album,
      'timestamp[0]': Math.floor(Date.now() / 1000),
      api_key,
      sk,
    };

    const api_sig = generateSig(params);

    const url = `${LAST_FM_URL}?${qs.stringify({ ...params, api_sig, format })}`;
    ga('send', 'event', 'LastFm - Scrobble');
    axios.post(url);
  }

  static updateNowPlaying({ track, artist }) {
    if (!currentUser()) return;
    const sk = currentUser().get('lastFmSessionId');
    if (!sk) return;

    const params = {
      method: 'track.updateNowPlaying',
      artist,
      track,
      api_key,
      sk,
    };

    const api_sig = generateSig(params);

    const url = `${LAST_FM_URL}?${qs.stringify({ ...params, api_sig, format })}`;

    axios.post(url);
  }

  static getTrackInfo({ track, artist }) {
    return axios.get(LAST_FM_URL, {
      params: {
        method: 'track.getInfo',
        artist,
        track,
        format,
        api_key,
      },
    }).then((response) => (
      response.data.track
    )).catch(() => { /* swallow */ });
  }

  static auth_link() {
    return `http://www.last.fm/api/auth/?api_key=${api_key}&cb=${callbackPage}`;
  }

  static profile_page() {
    if (!currentUser()) return null;
    return `https://www.last.fm/user/${currentUser().get('lastFmUsername')}`;
  }
}

export default LastFm;
