import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import ImmutablePropTypes from 'react-immutable-proptypes';
import classnames from 'classnames';
import CollectionGridItem from './CollectionGridItem';
import NoCollections from '../../components/BlankStates/NoCollections';

class CollectionsGrid extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
  }

  get extraItem() {
    const { type, onCreateCollectionClick } = this.props;
    if (type !== 'collections') return null;
    return (
      <div className="collection-item">
        <div className="collection-item-content">
          <a
            className="collection-link add-collection"
            onClick={onCreateCollectionClick}
          >
            <div className="collection-thumb extra">
              <i className="material-icons">add</i>
            </div>
          </a>
        </div>
      </div>
    );
  }

  expandGrid = (e) => {
    e.preventDefault();
    this.setState((prevState) => ({ open: !prevState.open }));
  }

  render() {
    const { collections, type, title, onShuffleClick, onPlayClick } = this.props;
    const { open } = this.state;
    if (!collections) {
      return (
        <>
          <h3>
            {title}
          </h3>
          <NoCollections type={type} />
        </>
      );
    }
    const needExpand = collections.size > 2;
    const items = collections.toArray().map((collection, i) => {
      if (collection.get('name') === 'Likes') return null;

      return (
        <CollectionGridItem
          key={i}
          collection={collection}
          onScroll={this.onScroll}
          onShuffleClick={() => onShuffleClick(collection)}
          onPlayClick={() => onPlayClick(collection)}
        />
      );
    });

    return (
      <div className="grid-wrapper">
        <h3>
          {title}
          {needExpand
          && (
          <a
            onClick={this.expandGrid}
            href=""
            className={classnames('expand-button')}
          >
            <i className="material-icons">
              {open ? 'more_horiz' : 'apps'}
            </i>
          </a>
          )}
        </h3>
        <div
          className={classnames('collections-grid', `t-${type}`, { open })}
        >
          {items}
          {this.extraItem}
        </div>
      </div>
    );
  }
}

CollectionsGrid.propTypes = {
  onShuffleClick: PropTypes.func.isRequired,
  onPlayClick: PropTypes.func.isRequired,
  collections: ImmutablePropTypes.map,
  onCreateCollectionClick: PropTypes.func,
  type: PropTypes.string,
  title: PropTypes.string.isRequired,
};

CollectionsGrid.defaultProps = {
  onCreateCollectionClick: () => {},
  collections: null, // Has to be null rather than empty for L#12 to work
  type: '',
};

export default CollectionsGrid;
