import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import PlaylistPreview from './PlaylistPreview';
import CollectionPreview from './CollectionPreview';
import RadioPreview from './RadioPreview';

class Preview extends PureComponent {
  getChildContext() {
    return {
      kind: this.kind,
    };
  }

  get kind() {
    const { params } = this.props;
    return params.kind;
  }

  get id() {
    const { params } = this.props;
    return params.id;
  }

  get previewKind() {
    switch (this.kind) {
      case 'playlist':
      case 'channel':
        return <PlaylistPreview id={this.id} kind={this.kind} />;
      case 'collection':
        return <CollectionPreview id={this.id} />;
      case 'radio':
        return <RadioPreview id={this.id} />;
      default:
        return null;
    }
  }

  render() {
    return (
      <div className="preview">
        {this.previewKind}
      </div>
    );
  }
}

Preview.childContextTypes = {
  kind: PropTypes.string,
};

Preview.propTypes = {
  params: PropTypes.instanceOf(Object),
};

Preview.defaultProps = {
  params: {},
};

export default Preview;
