      margin-left: 11px;\n      a { color: $secondaryGrey; }\n    }\n  }\n\n  .times {\n    align-items: center;\n    display: flex;\n\n    height: 30px;\n    justify-content: space-around;\n    position: relative;\n    right: 0;\n    width: 100%;\n\n    .time {\n      color: #fff;\n      font-size: 13px;\n      font-weight: 100;\n\n      &,\n      span {\n        font-size: 11px;\n      }\n    }\n\n    .live-tag {\n      right: 0;\n      top: 0;\n    }\n\n    progress {\n      appearance: none;\n      border: 0;\n      cursor: pointer;\n      display: block;\n      height: 5px;\n      transition: height 0.1s linear;\n      width: 73%;\n\n      &::-webkit-progress-bar {\n        background: #757575;\n        border-radius: 4px;\n        transition: background 0.3s ease;\n      }\n\n      &::-webkit-progress-value {\n        background: #fff;\n        background-size: 25px 14px, 100% 100%, 100% 100%;\n        border-radius: 4px;\n      }\n\n      &:hover {\n        height: 8px;\n      }\n    }\n  }\n\n  .like-plus {\n    align-items: center;\n    bottom: 0;\n    display: flex;\n    opacity: 0.5;\n    position: absolute;\n    right: 0;\n    top: 0;\n    transition: opacity 0.3s ease;\n    z-index: 2;\n\n    .icon-set {\n      background: rgba(#000, 0.7);\n      display: flex;\n      flex-direction: column;\n      padding: 5px;\n    }\n\n    i {\n      color: $light-grey;\n      opacity: 0.9;\n\n      &:hover {\n        opacity: 1;\n      }\n    }\n\n    .like {\n      font-size: 1.6em;\n      margin-top: 4px;\n      transition: all 0.4s ease;\n\n      &.active {\n        color: $heart;\n      }\n    }\n  }\n\n  .repeat-shuffle {\n    bottom: 53px;\n    position: absolute;\n  }\n\n  .repeat-icon,\n  .shuffle-icon {\n    border-radius: 50%;\n    color: $turquoise;\n    font-size: 20px;\n    padding: 4px;\n\n    &.active {\n      background: $turquoise;\n      border-color: $turquoise;\n      color: #fff;\n    }\n\n    &.disabled {\n      color: #808080;\n      pointer-events: none;\n    }\n  }\n\n  .controls {\n    align-items: center;\n    bottom: 77px;\n    display: flex;\n    justify-content: space-around;\n    position: absolute;\n    width: 88%;\n\n    .play {\n      color: $turquoise;\n      font-size: 4em;\n    }\n\n    .secondary {\n      color: $turquoise;\n      font-size: 1.6em;\n    }\n  }\n\n  .background-wrapper {\n    align-items: center;\n    display: flex;\n    height: $screenHeight;\n    justify-content: center;\n    overflow: hidden;\n    position: absolute;\n  }\n\n  .background-image {\n    filter: blur(30px) brightness(150%);\n    transform: scale(2.5);\n    z-index: -1;\n  }\n\n  .pt-popover-content {\n    font-size: 12px;\n    padding: 8px;\n  }\n}\n\n.age {\n  padding: 10px;\n  width: 80%;\n\n  input {\n    font-size: 20px;\n    padding: 5px;\n  }\n\n  button {\n    padding: 10px;\n  }\n}\n\n.last-fm-logo {\n  cursor: pointer;\n\n  img {\n    width: 23px;\n  }\n}\n\n.albumize {\n  right: 0;\n}\n\n.pt-control {\n  margin-bottom: 3px;\n}\n\n.ad-upgrade-button {\n  border: 1px solid $green;\n  border-radius: 3px;\n  color: #fff;\n  cursor: pointer;\n  margin: 4px auto;\n  padding: 7px;\n  text-align: center;\n  transition: all 0.3s ease;\n  width: 65%;\n\n  &:hover {\n    background: $green;\n  }\n}\n","$screenHeight: 667px;\n$screenWidth: 375px;\n$headerHeight: 34px;\n$searchBarHeight: 50px;\n$menuHeight: 50px;\n$pageHeight: calc(#{$screenHeight} - #{$headerHeight});\n$listHeight: calc(#{$pageHeight} - 46px);\n$green: #0f9960;\n$light-grey: #e6e6e6;\n$dark-grey: #333;\n$secondaryGrey: #cecece;\n// $turquoise: #0cc2c2;\n$turquoise: #37bbe4;\n$textGrey: #7a7a7a;\n$beautiful-grey: #2b3134;\n$heart: #ff7473;\n\n@mixin header-font {\n  font-family: 'Montserrat', sans-serif;\n}\n\n@mixin acts-as-list {\n  height: $listHeight;\n  overflow: auto;\n\n  &.with-player {\n    height: calc(#{$screenHeight} - #{$headerHeight} - 96px);\n  }\n}\n\n@mixin two-line-text {\n  box-orient: vertical; // stylelint-disable property-no-unknown\n  display: box;\n  -webkit-line-clamp: 2;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n"],"sourceRoot":""}]);
