import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import classnames from 'classnames';
import Image from 'lib/Image';
import { ProgressBar, QueueList } from 'containers';
import { Tooltip, Position } from '@blueprintjs/core';
import { VolumeSlider } from 'components';
import LastFmLogo from 'static/images/lastfm.png';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';
import pluralize from 'pluralize';
import AlbumizeSwitcher from './AlbumizeSwitcher';

class LargePlayer extends PureComponent {
  getChildContext() {
    return { inPlayer: true };
  }

  onQueueClick = () => {
    this.props.onCollectionsClick();
  };

  onArtistClick = (e) => {
    e.preventDefault();
    const { track } = this.props;
    const artist = track.get('artist');
    this.props.onArtistClick(artist);
  }

  render() {
    const {
      active,
      onLikeClick,
      onSkipPreviousClick,
      onPlayPauseClick,
      onSkipNextClick,
      isPlaying,
      track,
      onProgressClick,
      onRepeatClick,
      onMinimizeClick,
      onShuffleClick,
      isLiked,
      repeatMode,
      isShuffled,
      queueSource,
      isAlbum,
      isAd,
      isConnectedToLastFm,
      onEditLastFmScrobbleInfo,
      onUpgradeClick,
      queueSize,
      onVolumeChange,
    } = this.props;
    const { user } = this.context;

    if (track.isEmpty()) {
      return <div className="large-player" />;
    }

    const image = track.get('image');
    const title = track.get('title');
    const artist = track.get('artist');
    const repeatIcon = repeatMode === 'one' ? 'repeat_one' : 'repeat';
    let repeatTooltip = null;
    switch (repeatMode) {
      case 'one':
        repeatTooltip = 'Repeat One';
        break;
      case 'all':
        repeatTooltip = 'Repeat All';
        break;
      default:
        repeatTooltip = 'Repeat';
        break;
    }

    return (
      <div className={classnames('large-player', { active })}>
        <div className="track-image-wrapper">
          <i className="material-icons minimize" role="none" onClick={onMinimizeClick}>keyboard_arrow_down</i>
          <div className="like-plus">
            <div className="icon-set">
              <i
                className={classnames('material-icons like secondary', { active: isLiked })}
                onClick={onLikeClick}
                title="Like"
                role="none"
              >
                {isLiked ? 'favorite' : 'favorite_border'}
              </i>
              <i
                className="material-icons queue secondary"
                title="Collections..."
                onClick={this.onQueueClick}
                role="none"
              >
                queue_music
              </i>

              {isConnectedToLastFm
              && (
              <a
                onClick={onEditLastFmScrobbleInfo}
                className="last-fm-logo"
                role="none"
              >
                <img alt="" src={LastFmLogo} />
              </a>
              )}
            </div>
          </div>
          <Image className="track-image" src={image} />
          <div className={classnames('track-info', { ad: isAd })}>
            <div className="title" title={title}>
              {title}
            </div>
            <div className="artist">
              { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
              {artist ? <a href="" onClick={this.onArtistClick}>{artist}</a> : null}
            </div>
            {isAd && user
            && (
              <div
                className="ad-upgrade-button"
                onClick={onUpgradeClick}
              >
                Upgrade Now
              </div>
            )}
            <ProgressBar
              track={track}
              onProgressClick={onProgressClick}
              isAlbum={isAlbum}
            />
          </div>
        </div>
        <div className="queue-info">
          <div className="queue-source">
            {queueSource || 'Current Queue'}
          </div>
          <div className="queue-count">
            {pluralize('Track', queueSize)}
          </div>
          <AlbumizeSwitcher track={track} />
        </div>
        <QueueList
          sortable={!isAlbum}
        />
        <div className="controls">
          <Tooltip
            className="repeat-action"
            content={repeatTooltip}
            position={Position.TOP}
            inline
          >
            <i
              className={classnames('material-icons repeat-icon', { active: repeatMode != null, disabled: isAlbum })}
              onClick={onRepeatClick}
              role="none"
            >
              {repeatIcon}
            </i>
          </Tooltip>
          <i className="material-icons skip prev secondary" role="none" onClick={onSkipPreviousClick}>skip_previous</i>
          <i className="material-icons play" role="none" onClick={onPlayPauseClick}>
            {isPlaying ? 'pause_circle_filled' : 'play_circle_filled'}
          </i>
          <i className="material-icons skip next secondary" role="none" onClick={onSkipNextClick}>skip_next</i>
          <Tooltip className="shuffle-action" content="Shuffle" position={Position.TOP} inline>
            <i
              className={classnames('material-icons shuffle-icon', { active: isShuffled, disabled: isAlbum })}
              onClick={onShuffleClick}
              role="none"
            >
              shuffle
            </i>
          </Tooltip>
        </div>
        <div className="volume-control">
          <VolumeSlider onVolumeChange={onVolumeChange} />
        </div>
      </div>
    );
  }
}

LargePlayer.childContextTypes = {
  inPlayer: PropTypes.bool,
};

LargePlayer.contextTypes = {
  user: PropTypes.object,
};

LargePlayer.propTypes = {
  queueSource: PropTypes.node,
  queueSize: PropTypes.number,
  active: PropTypes.bool,
  track: ImmutablePropTypes.map,
  isPlaying: PropTypes.bool,
  onLikeClick: PropTypes.func.isRequired,
  onSkipPreviousClick: PropTypes.func.isRequired,
  onPlayPauseClick: PropTypes.func.isRequired,
  onSkipNextClick: PropTypes.func.isRequired,
  onMinimizeClick: PropTypes.func.isRequired,
  onProgressClick: PropTypes.func.isRequired,
  onArtistClick: PropTypes.func.isRequired,
  isLiked: PropTypes.bool,
  onVolumeChange: PropTypes.func.isRequired,
  repeatMode: PropTypes.string,
  isShuffled: PropTypes.bool,
  onShuffleClick: PropTypes.func.isRequired,
  onRepeatClick: PropTypes.func.isRequired,
  onCollectionsClick: PropTypes.func.isRequired,
  isConnectedToLastFm: PropTypes.bool,
  onEditLastFmScrobbleInfo: PropTypes.func.isRequired,
  isAlbum: PropTypes.bool.isRequired,
  isAd: PropTypes.bool.isRequired,
  onUpgradeClick: PropTypes.func.isRequired,
};

LargePlayer.defaultProps = {
  queueSource: null,
  queueSize: 0,
  active: false,
  track: Map(),
  isPlaying: false,
  isLiked: false,
  repeatMode: null,
  isShuffled: false,
  isConnectedToLastFm: false,
};

export default LargePlayer;
