import CollectionSyncer from 'lib/CollectionsSyncer';
import { Position, Intent } from '@blueprintjs/core';
/* eslint-disable import/no-cycle */
import { notify } from 'actions/appActions';
/* eslint-enable import/no-cycle */
import actionTypes from './actionTypes';

export const syncCollections = () => (dispatch, getState) => {
  const collections = getState().collectionsReducer.get('collections');
  const syncer = new CollectionSyncer(collections);

  syncer.sync().then(() => {
    dispatch({ type: actionTypes.COLLECTIONS_SYNCED });
  }).catch(() => {
    dispatch(notify(
      'There was a problem with saving, please try to restart the app',
      { intent: Intent.DANGER, timeout: 10000, position: Position.TOP },
    ));
  });
};

export const toggleCollectionsDialog = (track) => (dispatch) => {
  dispatch({ type: actionTypes.TOGGLE_ADD_TO_COLLECTION_DIALOG, payload: track });
  dispatch({ type: actionTypes.TOGGLE_DIALOG, payload: !!track });
};

export const addTrackToCollection = (track, collection) => {
  ga('send', 'event', 'Add Track to Collection', `${track.get('title')}: ${collection.get('name')}`);
  return (dispatch) => {
    dispatch({ type: actionTypes.ADD_TRACK_TO_COLLECTION, payload: { track, collection } });
    dispatch(syncCollections());
  };
};

export const removeTrackFromCollection = (track, collection) => (dispatch) => {
  dispatch({ type: actionTypes.REMOVE_TRACK_FROM_COLLECTION, payload: { track, collection } });
  dispatch(syncCollections());
};

export const createCollection = (collectionData) => {
  ga('send', 'event', 'Create Collection');
  return (dispatch) => {
    dispatch({ type: actionTypes.CREATE_COLLECTION, payload: collectionData });
    dispatch(syncCollections());
  };
};

export const deleteCollection = (collection) => (dispatch) => {
  dispatch({ type: actionTypes.DELETE_COLLECTION, payload: collection });
  dispatch(syncCollections());
};

export const renameCollection = (collection, newName) => (dispatch) => {
  dispatch({ type: actionTypes.RENAME_COLLECTION, payload: { collection, newName } });
  dispatch(syncCollections());
};

export const buildLikesCollection = () => {
  const likesCollection = { id: 'likes', name: 'Likes' };
  return (dispatch) => {
    dispatch({ type: actionTypes.CREATE_COLLECTION, payload: likesCollection });
    dispatch(syncCollections());
  };
};

export const addTrackToLikes = (track) => {
  ga('send', 'event', 'Like', track.get('title'));

  return (dispatch) => {
    dispatch({ type: actionTypes.ADD_TRACK_TO_LIKES_COLLECTION, payload: { track } });
    dispatch({ type: actionTypes.TOGGLE_NEW_LIKES_ADDED });
    setTimeout(() => {
      dispatch({ type: actionTypes.TOGGLE_NEW_LIKES_ADDED });
    }, 2000);
    dispatch(syncCollections());
  };
};

export const removeTrackFromLikes = (track) => (dispatch) => {
  dispatch({ type: actionTypes.REMOVE_TRACK_FROM_LIKES_COLLECTION, payload: { track } });
  dispatch(syncCollections());
};

export const collectionReorder = (collection, tracks) => (dispatch) => {
  dispatch({ type: actionTypes.COLLECTION_REORDER, payload: { collection, tracks } });
  dispatch(syncCollections());
};
