import actionTypes from 'actions/actionTypes';
import { fromJS } from 'immutable';
import { LOCATION_CHANGE } from 'react-router-redux';

const initialState = fromJS({
  isDialogOpen: true, // <-- TODO: Refactor this
  toast: null,
  user: null,
  version: null, // commit hash
  passwordResetDialog: { open: false },
  emailConfirmationDialog: { open: false },
  stripeDialog: { open: false },
  subscriptionDialog: { open: false },
  proDiscoverDialog: { open: false },
  moreSkipsDialog: { open: false },
});

const appReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case LOCATION_CHANGE:
      ga('send', 'pageview', payload.pathname);
      return state.merge({
        isDialogOpen: false,
        toast: null,
      });

    case actionTypes.TOGGLE_DIALOG:
      return state.set('isDialogOpen', payload);

    case actionTypes.TOGGLE_TOAST:
      return state.set('toast', payload);

    case actionTypes.STORE_USER:
      return state.set('user', payload);

    case actionTypes.OPEN_PAY_DIALOG:
      return state.set('payDialogOpen', true);

    case actionTypes.CLOSE_PAY_DIALOG:
      return state.set('payDialogOpen', false);

    case actionTypes.OPEN_DIALOG:
      return state.setIn([payload, 'open'], true);

    case actionTypes.CLOSE_DIALOG:
      return state.setIn([payload, 'open'], false);

    case actionTypes.UPDATE_USER: {
      const { attribute, value } = payload;

      return state.setIn(['user', attribute], value);
    }

    default:
      return state;
  }
};

export default appReducer;
