import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import ImmutablePropTypes from 'react-immutable-proptypes';
import Logo from 'static/images/logo-text.png';
import Subscription from 'lib/Subscription';
import { ProBadge } from 'components';

class Header extends PureComponent {
  onIconClick = () => {
    if (this.user) {
      this.context.router.push('/profile');
    } else {
      this.context.router.push('/collections');
    }
  }

  get rightIcon() {
    return (
      <i
        className="material-icons account-icon"
        role="none"
        onClick={this.onIconClick}
      >
        account_circle
      </i>
    );
  }

  get user() {
    const { appStore } = this.props;
    return appStore.get('user');
  }

  render() {
    return (
      <div className="app-header">
        <div className="logo">
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <img src={Logo} alt="logo" /> {Subscription.isPro() && <ProBadge />}
        </div>
        <div className="app-menu">
          {this.rightIcon}
        </div>
      </div>
    );
  }
}

Header.contextTypes = {
  router: PropTypes.object,
  user: ImmutablePropTypes.map,
};

Header.propTypes = {
  appStore: ImmutablePropTypes.map.isRequired,
};

const mapStateToProps = (state) => ({ appStore: state.appReducer });

export default connect(mapStateToProps)(Header);
