import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { Link } from 'react-router';
import Image from 'lib/Image';
import ellipsize from 'ellipsize';
import BlankCollectionImage from 'static/images/blank-collection.png';
import YouTubeIcon from 'static/images/youtube-icon.png';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';
import pluralize from 'pluralize';

class CollectionGridItem extends PureComponent {
  onPlayClick = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.props.onPlayClick();
  }

  onShuffleClick = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.props.onShuffleClick();
  }

  get tracksInfo() {
    const { collection } = this.props;
    const { size } = collection.get('tracks');
    if (size === 0) return null;

    return pluralize('Track', size, true);
  }

  get backgroundImage() {
    const { collection } = this.props;
    if (collection.get('tracks').size && collection.get('custom')) {
      return collection.get('tracks').first().get('image');
    }

    return collection.get('image') || BlankCollectionImage;
  }

  get youtubeIcon() {
    if (this.kind === 'playlist' || this.kind === 'channel') {
      return <img alt="" src={YouTubeIcon} className="youtube-icon" />;
    }

    return null;
  }

  get kind() {
    const { collection } = this.props;
    return collection.get('type')
      || (collection.get('custom') ? 'collection' : 'playlist');
  }

  render() {
    const { collection } = this.props;

    return (
      <div className="collection-item">
        <div className="collection-item-content">
          <Link
            className="collection-link"
            to={`/preview/${this.kind}/${collection.get('id')}`}
          >
            <Image src={this.backgroundImage} className="collection-thumb" />
            <p className="collection-item-title">
              {ellipsize(collection.get('name'), 30)}
            </p>
          </Link>
        </div>
      </div>
    );
  }
}

CollectionGridItem.propTypes = {
  collection: ImmutablePropTypes.map,
  onShuffleClick: PropTypes.func,
  onPlayClick: PropTypes.func,
};

CollectionGridItem.defaultProps = {
  collection: Map(),
  onShuffleClick: () => {},
  onPlayClick: () => {},
};

export default CollectionGridItem;
