import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import SmallLoader from 'static/images/small-loader.svg';
import ImmutablePropTypes from 'react-immutable-proptypes';
import Config from 'config';

import {
  createCollection,
  deleteCollection,
} from 'actions/collectionsActions';

class SavePlaylistButton extends PureComponent {
  constructor() {
    super();
    this.state = {
      isSaving: false,
    };
  }

  get user() {
    const { appStore } = this.props;
    return appStore.get('user');
  }

  get collection() {
    const { collectionsStore, playlist } = this.props;
    return collectionsStore.getIn(['collections', playlist.get('id')]);
  }

  get isSaved() {
    return !!this.collection;
  }

  unsave = () => {
    const { onUnsave } = this.props;
    // eslint-disable-next-line no-alert
    if (Config.development || window.confirm('Are you sure you want to remove this from your collections?')) {
      this.props.dispatch(deleteCollection(this.collection));
      if (onUnsave) onUnsave();
    }
  }

  save = () => {
    const { onSave, playlist, dispatch } = this.props;

    if (!this.user) {
      this.context.router.push('/collections');
      return;
    }

    const name = playlist.get('title');
    ga('send', 'event', 'Follow Playlist', name);
    dispatch(createCollection({
      name,
      id: playlist.get('id'),
      image: playlist.get('image'),
      type: playlist.get('type'),
    }));

    if (onSave) onSave();
  }

  render() {
    const { isSaving } = this.state;
    if (isSaving) {
      return (
        <div className="save-playlist-button is-saving">
          <img alt="" src={SmallLoader} />
        </div>
      );
    }

    if (this.isSaved) {
      return (
        <div
          onClick={this.unsave}
          role="none"
          className="save-playlist-button saved upper"
        >
          SAVED
        </div>
      );
    }

    return (
      <div
        onClick={this.save}
        role="none"
        className="save-playlist-button not-saved upper"
      >
        SAVE
      </div>
    );
  }
}

SavePlaylistButton.propTypes = {
  playlist: ImmutablePropTypes.map.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  dispatch: PropTypes.func.isRequired,
  onSave: PropTypes.func,
  onUnsave: PropTypes.func,
};

SavePlaylistButton.defaultProps = {
  onSave: () => {},
  onUnsave: () => {},
};

SavePlaylistButton.contextTypes = {
  router: PropTypes.object,
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(SavePlaylistButton);
