import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Autocompletes } from 'components';
import ImmutablePropTypes from 'react-immutable-proptypes';
import {
  fetchAutocompletes,
  fetchSearchResults,
  stepAutocomplete,
  updateSearchBarValue,
} from 'actions/searchActions';

const DEFAULT_SEARCH = 'music channels';

class SearchBar extends PureComponent {
  componentDidMount() {
    const { dispatch, searchStore } = this.props;
    if (searchStore.get('results').isEmpty() && searchStore.get('searchBarValue') === '') {
      dispatch(fetchSearchResults(DEFAULT_SEARCH));
    }
  }

  onChange = (e) => {
    this.props.dispatch(updateSearchBarValue(e.target.value));
  }

  inputKeyUp = (e) => {
    e.stopPropagation();

    const { dispatch, searchStore } = this.props;
    const autocompletes = searchStore.get('autocompletes');
    const currentAutocomplete = searchStore.get('currentAutocomplete');
    const { value } = e.target;

    e.preventDefault();

    switch (e.which) {
      // down
      case 40:
        if (currentAutocomplete < autocompletes.length - 1) {
          dispatch(stepAutocomplete(currentAutocomplete + 1));
        }
        break;
      // up
      case 38:
        if (currentAutocomplete > 0) {
          dispatch(stepAutocomplete(currentAutocomplete - 1));
        }
        break;
      // enter
      case 13:
        if (value !== undefined) {
          window.searchScroll = 0;
          ga('send', 'event', 'search', value);
          dispatch(fetchSearchResults(value || DEFAULT_SEARCH));
          dispatch(stepAutocomplete(-1));
          dispatch(fetchAutocompletes(''));
        }
        break;
      // esc
      case 27:
        dispatch(fetchAutocompletes(''));
        break;
      default:
        dispatch(stepAutocomplete(-1));
        dispatch(fetchAutocompletes(value));
    }
  }

  /* eslint-disable react/destructuring-assignment */
  autocompleteHover(index) {
    this.props.dispatch(stepAutocomplete(index));
  }

  autocompleteClick(value) {
    this.props.dispatch(fetchSearchResults(value));
  }
  /* eslint-enable react/destructuring-assignment */

  render() {
    const { searchStore } = this.props;
    const autocompletes = searchStore.get('autocompletes');
    const searchBarValue = searchStore.get('searchBarValue');
    const currentAutocomplete = searchStore.get('currentAutocomplete');

    return (
      <div className="search-bar">
        <i className="material-icons search-icon">search</i>
        <input
          autoFocus
          type="text"
          placeholder="Search title / album / playlist / channel"
          onKeyUp={this.inputKeyUp}
          onChange={this.onChange}
          value={searchBarValue}
        />
        <Autocompletes
          onClick={(value) => this.autocompleteClick(value)}
          results={autocompletes}
          active={currentAutocomplete}
          onMouseMove={(index) => this.autocompleteHover(index)}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  searchStore: state.searchReducer,
});

SearchBar.propTypes = {
  dispatch: PropTypes.func.isRequired,
  searchStore: ImmutablePropTypes.map.isRequired,
};

export default connect(mapStateToProps)(SearchBar);
