import PropTypes from 'prop-types';

import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Welcome } from 'containers';
import { Dialog } from '@blueprintjs/core';
import { random } from 'lib/utils';
import { Map } from 'immutable';

import {
  createCollection,
} from 'actions/collectionsActions';
import {
  loadTrack,
  loadQueue,
  setCurrentCollection,
  shuffleOn,
} from 'actions/playerActions';
import ImmutablePropTypes from 'react-immutable-proptypes';
import CollectionsGrid from './CollectionsGrid';

class Collections extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      createDialogOpen: false,
      allSaved: Map(),
    };
  }

  UNSAFE_componentWillMount() {
    this.cacheAllSavedToState(this.props);
  }

  componentDidMount() {
    if (!this.list) return;

    this.list.scrollTop = window.collectionsScroll;
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    this.cacheAllSavedToState(newProps);
  }

  onCollectionRename = (e) => {
    const name = e.target.value;
    switch (e.which) {
      case 13:
        this.props.dispatch(createCollection({ name }));
        this.setState({ createDialogOpen: false });
        break;
      case 27:
        this.setState({ createDialogOpen: false });
        break;
      default:
        break;
    }
  }

  onPlayClick = (collection) => {
    const { dispatch } = this.props;
    dispatch(setCurrentCollection(collection));
    const tracks = this.sortedTracks(collection);

    dispatch(loadTrack(tracks.get(0)));
    dispatch(loadQueue(tracks, 'collection'));
  }

  onShuffleClick = (collection) => {
    const { dispatch } = this.props;
    dispatch(setCurrentCollection(collection));
    const tracks = this.sortedTracks(collection);
    dispatch(loadTrack(tracks.get(random(0, tracks.size - 1))));
    dispatch(loadQueue(tracks, 'collection'));
    dispatch(shuffleOn());
  }

  onCreateCollectionClick = () => {
    this.setState({ createDialogOpen: true });
  }

  onScroll = (e) => {
    window.collectionsScroll = e.target.scrollTop;
  }

  /* eslint-disable react/destructuring-assignment */
  get currentTrack() {
    return this.props.playerStore.get('currentTrack');
  }

  // collections and playlists has to match with store.currentView
  get collections() {
    return this.state.allSaved.get(undefined);
  }

  get playlists() {
    return this.state.allSaved.get('playlist');
  }

  get channels() {
    return this.state.allSaved.get('channel');
  }

  // change to context
  get user() {
    return this.props.appStore.get('user');
  }
  /* eslint-enable react/destructuring-assignment */

  closeCreateCollectionDialg = () => {
    this.setState({ createDialogOpen: false });
  }

  // eslint-disable-next-line class-methods-use-this
  sortedTracks(collection) {
    const tracks = collection.get('tracks');
    const order = collection.get('order');
    if (!order) { // backwards compatible
      return tracks.toList();
    }
    return tracks.toList().sortBy((tr) => order.get(tr.get('id')));
  }

  cacheAllSavedToState(props) {
    const allSaved = props.collectionsStore.get('collections').groupBy((x) => x.get('type'));
    this.setState({ allSaved });
  }

  renderCreateDialog() {
    const { createDialogOpen } = this.state;
    return (
      <Dialog
        isOpen={createDialogOpen}
        lazy
        title="Create a Collection"
        canEscapeKeyClose
        className="rename-collection-dialog"
        onClose={this.closeCreateCollectionDialg}
      >
        <div className="edit-text-content">
          <input
            autoFocus
            className="pt-input edit-collection-input"
            onKeyUp={this.onCollectionRename}
          />
          <i className="collection-add-caption">Press &quot;Enter&quot; to save, &quot;Esc&quot; to cancel</i>
        </div>
      </Dialog>
    );
  }

  render() {
    if (this.user === null) {
      return <Welcome />;
    }

    return (
      <div className="collections">
        <CollectionsGrid
          title="My Collections"
          onPlayClick={this.onPlayClick}
          onShuffleClick={this.onShuffleClick}
          collections={this.collections}
          type="collections"
          onCreateCollectionClick={this.onCreateCollectionClick}
        />
        <CollectionsGrid
          title="Playlists"
          onPlayClick={this.onPlayClick}
          onShuffleClick={this.onShuffleClick}
          collections={this.playlists}
          type="playlists"
        />
        <CollectionsGrid
          title="Channels"
          onPlayClick={this.onPlayClick}
          onShuffleClick={this.onShuffleClick}
          collections={this.channels}
          type="channels"
        />
        {this.renderCreateDialog()}
      </div>
    );
  }
}

Collections.propTypes = {
  dispatch: PropTypes.func.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  playerStore: state.playerReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(Collections);
