import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import YTPlayer from 'lib/ytPlayer';
import { playerStates } from 'lib/youtube';
import { DIALOGS, AD_VIDEO } from 'lib/constants';
import LastFm from 'lib/LastFm';
import { LastFmScrobbleDialog } from 'containers';
import ellipsize from 'ellipsize';
import ImmutablePropTypes from 'react-immutable-proptypes';

import {
  loadMoreRelated,
  fetchSearchResults,
  updateSearchBarValue,
} from 'actions/searchActions';

import {
  updatePlaylistPreview,
} from 'actions/previewActions';

import {
  MiniPlayer,
  LargePlayer,
  ExternalLink,
} from 'components';

import {
  openDialog,
} from 'actions/appActions';

import {
  showPlayer,
  hidePlayer,
  play,
  pause,
  timeChanged,
  playNext,
  playPrevious,
  appendToQueue,
  shuffleOn,
  shuffleOff,
  toggleRepeatMode,
  setRepeatMode,
  playPreviousPin,
  playNextPin,
  playCurrentPin,
  setCurrentPin,
  ytPlay,
  ytPause,
} from 'actions/playerActions';

import {
  removeTrackFromLikes,
  addTrackToLikes,
  toggleCollectionsDialog,
} from 'actions/collectionsActions';

import {
  loadScrobble,
  openDialog as openLastFmDialog,
} from 'actions/lastFmActions';

const SOURCE_TYPES = {
  RADIO: 'radio',
  RELATED: 'related',
  COLLECTION: 'collection',
  PLAYLIST: 'playlist',
  CHANNEL: 'channel',
  ALBUM: 'album',
};

class Player extends PureComponent {
  constructor() {
    super();
    this.scrobbles = {};
  }

  componentDidMount() {
    this.listenToElectron();
    this.keyboardEvents();

    window.ytPlayer = new YTPlayer({
      onEnd: this.onSkipNext,
      onReady: this.onReady,
      onCurrentTime: this.onCurrentTime,
      onStateChange: this.onStateChange,
      onMprisShuffle: this.onMprisShuffle,
      onMprisRepeat: this.onMprisRepeat,
    });
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    const { dispatch } = this.props;
    const upcoming = newProps.playerStore.get('currentTrack');

    if (this.currentTrack.get('id') !== upcoming.get('id')) {
      this.loadTrackToEmbeddedPlayer(upcoming);

      const title = upcoming.get('title');
      const artist = upcoming.get('artist');

      LastFm.updateNowPlaying({ track: title, artist });
      dispatch(loadScrobble(upcoming));

      this.notifyNewTrack(title, artist, upcoming.get('thumbnail'));
    }

    if (newProps.playerStore.get('currentPin') !== null) {
      this.setTrackFromPin(newProps);
    }
  }

  onCollectionClick = () => {
    const { dispatch } = this.props;

    if (!this.user) {
      this.context.router.push('/collections');
      return;
    }
    dispatch(toggleCollectionsDialog(this.currentTrack));
  }

  onShuffleClick = () => {
    if (this.isAdPlaying()) return;

    const { playerStore } = this.props;
    const isShuffled = playerStore.getIn(['queue', 'shuffled']);

    this.props.dispatch(isShuffled ? shuffleOff() : shuffleOn());
  }

  onMprisShuffle = (shuffle) => {
    this.props.dispatch(shuffle ? shuffleOn() : shuffleOff());
  }

  onRepeatClick = () => {
    if (this.isAdPlaying()) return;

    this.props.dispatch(toggleRepeatMode());
  }

  onMprisRepeat = (repeatMode) => {
    if (this.isAdPlaying()) return;

    this.props.dispatch(setRepeatMode(repeatMode));
  }

  onEditLastFmScrobbleInfo = () => {
    this.props.dispatch(openLastFmDialog());
  }

  onUpgradeClick = () => {
    ga('send', 'event', 'subscription', 'Click on upgrade from ad');
    this.props.dispatch(openDialog(DIALOGS.STRIPE));
  }

  onProgressClick = (e) => {
    if (this.isAdPlaying()) return;
    const { playerStore } = this.props;
    const playerState = playerStore.get('playerState');

    const progress = e.target;
    const x = e.pageX - progress.offsetLeft;
    const clickedValueSeconds = Math.floor((x * progress.max) / progress.offsetWidth);

    ytPlayer.seekTo(clickedValueSeconds);

    if (playerState !== playerStates.PLAYING) {
      this.props.dispatch(timeChanged(clickedValueSeconds));
    }
  }

  // eslint-disable-next-line class-methods-use-this
  onReady() {
    ytPlayer.setVolume(75);
    ytPlayer.unMute();
  }

  onMiniPlayerActionClick = () => {
    this.props.dispatch(showPlayer());
  }

  onVolumeChange = (volume) => {
    localStorage.setItem('volumeLevel', volume);
    ytPlayer.setVolume(volume);
  }

  onSkipPreviousClick = () => {
    this.onSkipPrevious();
  }

  onSkipNextClick = () => {
    ga('send', 'event', 'skip');
    if (this.isAdPlaying()) return;

    this.onSkipNext();
  }

  onSkipPrevious = () => {
    if (this.isAdPlaying()) return;

    const { dispatch, progressBarStore, playerStore } = this.props;
    const pin = playerStore.get('currentPin');
    // If track is playing over 2 seconds, and user clicks skip previous,
    // it should play currentTrack from the begining instead of previous track
    const currentTime = progressBarStore.get('currentTime');

    if (pin !== null) {
      if (currentTime > pin + 2) {
        dispatch(playCurrentPin(pin));
      } else {
        dispatch(playPreviousPin());
      }
    } else if (currentTime > 2) {
      ytPlayer.seekTo(0);
    } else {
      ga('send', 'event', 'skip');
      dispatch(playPrevious());
    }
  }

  onSkipNext = (isError = false) => {
    const { dispatch } = this.props;
    const { playerStore } = this.props;
    const repeatMode = playerStore.get('repeatMode');
    const currentPin = playerStore.get('currentPin');

    if (currentPin !== null) {
      dispatch(playNextPin());
      return;
    }

    if (this.isLastSong) {
      const { action, params } = this.loadAction;

      // params[1] is the nextPageToken of the source

      // REPEAT ALL
      if (params[1] && repeatMode !== 'one') {
        dispatch(action.apply(this, params)).then((tracks) => {
          dispatch(appendToQueue(tracks));
          dispatch(playNext(isError));
        });
        // REPEAT ONE
      } else if (repeatMode) {
        dispatch(playNext(isError));
      }
    } else {
      // PLAY NEXT
      dispatch(playNext(isError));
    }
  }

  onCurrentTime = (time) => {
    const { lastFmStore } = this.props;
    const duration = this.currentTrack.get('duration');
    const seconds = Math.floor(time);

    const scroble = lastFmStore.get('scrobble');

    this.props.dispatch(timeChanged(seconds));

    // if more than half the track or more than 4 minutes
    if (duration === 0) return;

    if (seconds > Math.floor(duration * 0.5) || seconds > 240) {
      const id = this.currentTrack.get('id');
      if (this.scrobbles[id]) return;

      const track = scroble.get('title');
      const artist = scroble.get('artist');
      const album = scroble.get('album');

      this.scrobbles[id] = true;
      LastFm.scrobble({ track, artist, album });
    }
  }

  onPlayPauseClick = () => {
    const { dispatch, playerStore } = this.props;
    const playerState = playerStore.get('playerState');

    if (!ytPlayer) return;

    switch (playerState) {
      case playerStates.UNSTARTED:
        this.loadTrackToEmbeddedPlayer(this.currentTrack);
        dispatch(play());
        break;
      case playerStates.PLAYING:
        dispatch(pause());
        break;
      case playerStates.PAUSED:
        dispatch(play());
        break;
      default:
        break;
    }
  }

  onMinimizeClick = () => {
    this.props.dispatch(hidePlayer());
  }

  onArtistClick = (artist) => {
    const { dispatch } = this.props;
    this.context.router.push('/search');

    dispatch(fetchSearchResults(artist));
    dispatch(updateSearchBarValue(artist));
    dispatch(hidePlayer());
  }

  onLikeClick = () => {
    const { dispatch } = this.props;

    if (!this.user) {
      this.context.router.push('/preview/collection/likes');
    } else if (this.isLiked) {
      dispatch(removeTrackFromLikes(this.currentTrack));
    } else {
      dispatch(addTrackToLikes(this.currentTrack));
    }
  }

  onStateChange = (state) => {
    switch (state) {
      case playerStates.PLAYING:
        this.props.dispatch(ytPlay());
        break;
      case playerStates.PAUSED:
        this.props.dispatch(ytPause());
        break;
      default:
        break;
    }
  }

  /* eslint-disable react/destructuring-assignment */
  get currentPlaylist() {
    return this.props.playerStore.get('currentPlaylist');
  }

  get currentTrack() {
    return this.props.playerStore.get('currentTrack');
  }

  get user() {
    return this.props.appStore.get('user');
  }

  get previewPlaylist() {
    return this.props.previewStore.getIn(['playlists', this.currentPlaylist.get('id')]);
  }

  get queue() {
    return this.props.playerStore.get('queue');
  }

  get isConnectedToLastFm() {
    return !!this.props.appStore.getIn(['user', 'lastFmSessionId']);
  }

  get queueSize() {
    return this.props.playerStore.getIn(['queue', 'tracks']).size;
  }
  /* eslint-enable react/destructuring-assignment */

  get isLiked() {
    const { collectionsStore } = this.props;
    return !!collectionsStore.getIn(['collections', 'likes', 'tracks', this.currentTrack.get('id')]);
  }

  get isLastSong() {
    /*
      because ad is not part of the queue,
      this method will always return true as it looks like it is the last song
      the first check make sure it is never the last song.
    */
    if (this.isAdPlaying()) return false;

    const { playerStore } = this.props;
    const queueSize = playerStore.getIn(['queue', 'tracks']).size;
    const queueIndex = playerStore.getIn(['queue', 'currentIndex']);

    return queueIndex === queueSize - 1;
  }

  get loadAction() {
    const { playerStore, searchStore } = this.props;

    switch (playerStore.getIn(['queue', 'source'])) {
      case 'related':
        return {
          action: loadMoreRelated,
          params: [
            playerStore.get('currentTrack'),
            searchStore.get('nextRelatedPageToken'),
          ],
        };
      case 'radio':
      case 'playlist':
        return {
          action: updatePlaylistPreview,
          params: [
            this.previewPlaylist,
            this.previewPlaylist.get('nextPageToken'),
          ],
        };
      default:
        return { action: null, params: [] };
    }
  }

  get queueSource() {
    const { playerStore, searchStore } = this.props;
    const currentStation = playerStore.get('currentStation');
    const currentCollection = playerStore.get('currentCollection');
    const currentPlaylist = playerStore.get('currentPlaylist');

    switch (this.queue.get('source')) {
      case SOURCE_TYPES.RADIO:
        return (
          <span key="station">
            <ExternalLink href={`https://reddit.com/r/${currentStation}`}>
              {`/r/${currentStation}`}
            </ExternalLink>
          </span>
        );
      case SOURCE_TYPES.RELATED:
        return (
          <span key="searchTerm" title={searchStore.get('searchTerm')}>
            { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
            Related: &quot;{searchStore.get('searchTerm')}&quot;
          </span>
        );

      case SOURCE_TYPES.COLLECTION:
        return (
          <span key="collection" title={currentCollection.get('name')}>
            <Link to={`/preview/collection/${currentCollection.get('id')}`}>
              {ellipsize(currentCollection.get('name'), 67)}
            </Link>
          </span>
        );

      case SOURCE_TYPES.PLAYLIST:
      case SOURCE_TYPES.CHANNEL: {
        const playlistTitle = currentPlaylist ? currentPlaylist.get('title') : '';
        const id = currentPlaylist ? currentPlaylist.get('id') : '';

        return (
          <span key="playlist" title={playlistTitle}>
            <Link to={`/preview/playlist/${id}`}>
              {ellipsize(playlistTitle, 80)}
            </Link>
          </span>
        );
      }

      case SOURCE_TYPES.ALBUM:
        return (
          <span key="playlist" title={this.currentTrack.get('album')}>
            {ellipsize(this.currentTrack.get('album'), 80)}
          </span>
        );

      default:
        return null;
    }
  }

  setTrackFromPin(newProps) {
    const { dispatch } = this.props;
    const { progressBarStore, playerStore } = newProps;

    const currentPin = playerStore.get('currentPin');
    let newPin = null;
    const tracks = playerStore.getIn(['queue', 'tracks']);
    const time = progressBarStore.get('currentTime');

    tracks.forEach((track) => {
      const pin = track.get('pin');
      // -1 gives a bit of a heads up because the queue list updates slower.
      if (time > (pin - 1)) {
        newPin = pin;
      }
    });

    if (newPin !== null && newPin !== currentPin) {
      dispatch(setCurrentPin(newPin));
    }
  }

  isAdPlaying() {
    return this.currentTrack.get('id') === AD_VIDEO.get('id');
  }

  /* eslint-disable class-methods-use-this */
  loadTrackToEmbeddedPlayer(track) {
    ga('send', 'event', 'Play Track', track.get('title'), track.get('id'));
    ytPlayer.loadVideoById(track.get('id'));

    if (track.get('pin')) {
      ytPlayer.seekTo(track.get('pin'));
    }
    ytPlayer.sendTrackInfo(track.toJS());
  }

  /* eslint-disable no-new */
  notifyNewTrack(title, artist, icon) {
    if (!localStorage.getItem('new-track-notification')) return;

    new Notification(title, {
      title,
      body: artist,
      icon,
      silent: true,
    });
  }
  /* eslint-enable no-new, class-methods-use-this */

  keyboardEvents() {
    const { dispatch } = this.props;
    const JUMP = 5; // seconds
    let jumpTo;
    document.onkeyup = (e) => {
      const { playerStore, progressBarStore } = this.props;
      const currentTime = progressBarStore.get('currentTime');
      const duration = playerStore.getIn(['track', 'duration']);
      if (e.target.tagName === 'INPUT') return;

      switch (e.code) {
        case 'Space':
          this.onPlayPauseClick();
          break;
        case 'ArrowLeft':
          jumpTo = currentTime - JUMP;
          if (jumpTo < 0) { jumpTo = 0; }

          ytPlayer.seekTo(jumpTo);
          dispatch(timeChanged(jumpTo));
          break;
        case 'ArrowRight':
          jumpTo = currentTime + JUMP;
          if (jumpTo > duration) { jumpTo = duration; }

          ytPlayer.seekTo(jumpTo);
          dispatch(timeChanged(jumpTo));
          break;
        default:
          break;
      }
    };
  }

  listenToElectron() {
    window.ipcRenderer.on('media', (e, key) => {
      if (key === 'play-pause') this.onPlayPauseClick();
      if (key === 'play-next') this.onSkipNextClick();
      if (key === 'play-previous') this.onSkipPrevious();
      if (key === 'like') this.onLikeClick();
    });
  }

  render() {
    const { playerStore } = this.props;
    const playerState = playerStore.get('playerState');
    const playerActive = playerStore.get('playerActive');
    const repeatMode = playerStore.get('repeatMode');
    const isPlaying = playerState === playerStates.PLAYING;
    const isShuffled = playerStore.getIn(['queue', 'shuffled']);
    return (
      <div>
        <LastFmScrobbleDialog />
        <div className="players-wrapper">
          <MiniPlayer
            track={this.currentTrack}
            onClick={this.onMiniPlayerActionClick}
            onPlayPauseClick={this.onPlayPauseClick}
            isPlaying={isPlaying}
            onSkipNext={this.onSkipNextClick}
          />
          <LargePlayer
            queueSource={this.queueSource}
            isShuffled={isShuffled}
            repeatMode={repeatMode}
            track={this.currentTrack}
            active={playerActive}
            onLikeClick={this.onLikeClick}
            onPlayPauseClick={this.onPlayPauseClick}
            onSkipPreviousClick={this.onSkipPreviousClick}
            onSkipNextClick={this.onSkipNextClick}
            onArtistClick={this.onArtistClick}
            onProgressClick={this.onProgressClick}
            isPlaying={isPlaying}
            onMinimizeClick={this.onMinimizeClick}
            isLiked={this.isLiked}
            isAlbum={this.queue.get('source') === SOURCE_TYPES.ALBUM}
            onVolumeChange={this.onVolumeChange}
            onShuffleClick={this.onShuffleClick}
            onRepeatClick={this.onRepeatClick}
            onCollectionsClick={this.onCollectionClick}
            onEditLastFmScrobbleInfo={this.onEditLastFmScrobbleInfo}
            isConnectedToLastFm={this.isConnectedToLastFm}
            queueSize={this.queueSize}
            isAd={this.isAdPlaying()}
            onUpgradeClick={this.onUpgradeClick}
          />
        </div>
      </div>
    );
  }
}

Player.propTypes = {
  dispatch: PropTypes.func,
  playerStore: ImmutablePropTypes.map.isRequired,
  searchStore: ImmutablePropTypes.map.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  progressBarStore: ImmutablePropTypes.map.isRequired,
  lastFmStore: ImmutablePropTypes.map.isRequired,
  previewStore: ImmutablePropTypes.map.isRequired,
};

Player.defaultProps = {
  dispatch: () => {},
};

Player.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  radioStore: state.radioReducer,
  lastFmStore: state.lastFmReducer,
  playerStore: state.playerReducer,
  searchStore: state.searchReducer,
  collectionsStore: state.collectionsReducer,
  progressBarStore: state.progressBarReducer,
  previewStore: state.previewReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(Player);
