import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Intent, Dialog, Button, Spinner } from '@blueprintjs/core';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { post, get } from 'lib/http';
import { connect } from 'react-redux';
import {
  openDialog,
  closeDialog,
  updateUser,
} from 'actions/appActions';

import { DIALOGS } from 'lib/constants';

class EmailConfirmation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emailSent: false,
      code: '',
      isConfirmedLoading: false,
      confirmed: false,
      error: null,
    };
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    const { appStore } = this.props;
    const { emailSent } = this.state;
    const wasOpen = appStore.getIn([DIALOGS.EMAIL_CONFIRMATION, 'open']);
    const isOpenNow = newProps.appStore.getIn([DIALOGS.EMAIL_CONFIRMATION, 'open']);

    if (!wasOpen && isOpenNow) {
      if (emailSent) return;
      this.sendCode();
    }
  }

  openConfirmation = () => {
    this.props.dispatch(openDialog(DIALOGS.EMAIL_CONFIRMATION));
  }

  closeConfirmation = () => {
    this.props.dispatch(closeDialog(DIALOGS.EMAIL_CONFIRMATION));
  }

  validateCode = () => {
    const { user } = this.context;

    const data = { email: user.get('email'), code: this.state.code };
    this.setState({ isConfirmedLoading: true });

    get({ path: 'email-confirmation', data }).then(() => {
      this.setState({ confirmed: true, isConfirmedLoading: true });
      setTimeout(() => {
        this.props.dispatch(updateUser({ attribute: 'emailConfirmed', value: true }));
        this.closeConfirmation();
      }, 2000);
    }).catch((err) => {
      this.setState({
        error: err.data.message,
        isConfirmedLoading: false,
      });
    });
  }

  resendCode = (e) => {
    e.preventDefault();
    this.setState({ emailSent: false });
    this.sendCode();
  }

  updateCodeInput = (e) => {
    const { value } = e.target;

    this.setState({ code: value });
  }

  sendCode() {
    const { user } = this.context;
    const data = { email: user.get('email') };

    post({ path: 'email-confirmation', data }).then(() => {
      this.setState({ emailSent: true });
    });
  }

  renderLink() {
    return (
      <span
        onClick={this.openConfirmation}
        className="confirm-email"
      >
        Hi! Please confirm your email address
      </span>
    );
  }

  renderError() {
    const { error } = this.state;
    if (!error) {
      return null;
    }

    return (
      <div className="global-error">
        {error}
      </div>
    );
  }

  renderDialogBody() {
    const { user } = this.context;
    const { confirmed, emailSent, isConfirmedLoading, code } = this.state;

    if (confirmed) {
      return (
        <center>
          <h2 className="dialog-title">Email Confirmed</h2>
          <i className="material-icons email-check">check</i>
        </center>
      );
    }

    if (!emailSent) {
      return (
        <center>
          <Spinner />
        </center>
      );
    }

    return (
      <div className="email-confirmation-dialog-wrapper">
        <h2 className="dialog-title">Email Confirmation</h2>
        <p>
          {/* eslint-disable-next-line react/jsx-one-expression-per-line */}
          An email was sent to&nbsp;<b>{user.get('email')}</b>. <br /> Please enter the 6 digits code below:
        </p>
        <input
          type="text"
          className="pt-input pt-large email-confirmation-code-text"
          maxLength="6"
          placeholder="xxxxxx"
          value={code}
          onChange={this.updateCodeInput}
        />
        {this.renderError()}
        {isConfirmedLoading
          ? <Spinner className="pt-small" />
          : (
            <Button
              intent={Intent.PRIMARY}
              className="email-confirmation-code-button upper"
              onClick={this.validateCode}
              text="Confirm"
            />
          )}
        <br />
        <a
          href=""
          onClick={this.resendCode}
        >
          Resend the code
        </a>
      </div>
    );
  }

  renderDialog() {
    const { appStore } = this.props;
    return (
      <Dialog
        isOpen={appStore.getIn([DIALOGS.EMAIL_CONFIRMATION, 'open'])}
        onClose={this.closeConfirmation}
        className="email-confirmation-dialog"
      >
        {this.renderDialogBody()}
      </Dialog>
    );
  }

  render() {
    const { user } = this.context;
    const { confirmed } = this.state;

    if (!user || user.get('emailConfirmed')) return null;

    return (
      <div>
        {confirmed || this.renderLink()}
        {this.renderDialog()}
      </div>
    );
  }
}

EmailConfirmation.contextTypes = {
  user: ImmutablePropTypes.map,
};

EmailConfirmation.propTypes = {
  appStore: ImmutablePropTypes.map.isRequired,
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(EmailConfirmation);
