import actionTypes from 'actions/actionTypes';
import { fromJS } from 'immutable';
import stations from 'lib/stations';

const initialState = fromJS({
  /*
  'playlists': {
    '${playlistID}': {
      id: playlistID,
      title: ...,
      image: ...
      nextPageToken: 'CBCBS'
      tracks: [track 0, track 1, track 2],
    },
    '${playlistID2}': {
      id: playlistID2,
      title: ...,
      image: ...
      nextPageToken: 'CBCBQ',
      tracks: [track 3, track 4, track 5],
    },
  }
  */
});

const previewReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.SET_PLAYLIST_PREVIEW: {
      const { playlist, tracks, nextPageToken } = payload;
      return state.setIn(['playlists', playlist.get('id')], fromJS({ ...playlist.toJS(), tracks, nextPageToken }));
    }

    case actionTypes.PLAYLIST_NOT_FOUND: {
      const { playlistID } = payload;
      return state.setIn(['playlists', playlistID], null);
    }

    case actionTypes.UPDATE_PLAYLIST_PREVIEW: {
      const { playlist, tracks, nextPageToken } = payload;
      const allTracks = [...playlist.get('tracks'), ...tracks];
      return state.mergeIn(['playlists', playlist.get('id')], {
        tracks: allTracks,
        nextPageToken,
      });
    }

    case actionTypes.SET_RADIO_PREVIEW: {
      const { stationKey, tracks, nextPageToken } = payload;
      return state.setIn(
        ['playlists', stationKey],
        fromJS({
          id: stationKey,
          title: stations[stationKey],
          image: tracks.length ? tracks[0].image : '',
          nextPageToken,
          tracks,
          type: 'radio',
        }),
      );
    }

    default:
      return state;
  }
};

export default previewReducer;
