import actionTypes from 'actions/actionTypes';
import { fromJS } from 'immutable';

const initialState = fromJS({
  dialogOpen: false,
  rememberAlbum: false,
  rememberArtist: false,
  initial: {
    title: '',
    artist: '',
  },
  scrobble: {
    artist: '',
    title: '',
    album: '',
  },
});

const lastFmReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.OPEN_SCROBBLE_DIALOG:
      return state.set('dialogOpen', true);

    case actionTypes.CLOSE_SCROBBLE_DIALOG:
      return state.set('dialogOpen', false);

    case actionTypes.UPDATE_SCROBBLE:
      return state.setIn(['scrobble', payload.key], payload.value);

    case actionTypes.LOAD_SCROBBLE: {
      const rememberAlbum = state.get('rememberAlbum');
      const rememberArtist = state.get('rememberArtist');

      const album = state.getIn(['scrobble', 'album']);
      const artist = state.getIn(['scrobble', 'artist']);

      return state.set('scrobble', fromJS({
        title: payload.get('title'),
        artist: rememberArtist ? artist : payload.get('artist'),
        album: rememberAlbum ? album : '',
      }))
        .set('initial', fromJS({
          title: payload.get('title'),
          artist: payload.get('artist'),
        }));
    }

    case actionTypes.RESTORE_SCROBBLE:
      return state.set('scrobble', fromJS({
        title: state.getIn(['initial', 'title']),
        artist: state.getIn(['initial', 'artist']),
        album: '',
      }))
        .set('rememberAlbum', false)
        .set('rememberArtist', false);

    case actionTypes.TOGGLE_REMMBER_ALBUM: {
      const rememberState = state.get('rememberAlbum');
      return state.set('rememberAlbum', !rememberState);
    }

    case actionTypes.TOGGLE_REMMBER_ARTIST: {
      const rememberState = state.get('rememberArtist');
      return state.set('rememberArtist', !rememberState);
    }

    default:
      return state;
  }
};

export default lastFmReducer;
