\n}\n\n.list-item {\n  align-items: center;\n  border-bottom: 1px solid $light-grey;\n  cursor: default;\n  display: flex;\n  height: 82px;\n  margin: auto;\n  padding: 0 20px;\n  position: relative;\n  user-select: none;\n\n  .chevron-more {\n    color: $textGrey;\n    cursor: pointer;\n  }\n  * { transition: color 0.3s ease; }\n\n  .is-liked-icon {\n    bottom: 6px;\n    color: #ff7473;\n    font-size: 22px;\n    left: 58px;\n    position: absolute;\n    text-shadow: -1px 1px 2px rgba(60, 60, 60, 0.4);\n    z-index: 1;\n  }\n\n  &:hover {\n    .drag-icon {\n      visibility: visible;\n    }\n  }\n\n  .drag-icon {\n    color: $textGrey;\n    cursor: grab;\n    display: block;\n    font-size: 14px;\n    left: 4px;\n    opacity: 0.5;\n    position: absolute;\n    transition: opacity 0.3s ease;\n    visibility: hidden;\n    z-index: 3;\n\n    &:active {\n      cursor: grabbing !important;\n    }\n\n    &:hover {\n      opacity: 1;\n    }\n  }\n\n  .title {\n    @include header-font;\n    @include two-line-text;\n    color: #333;\n    cursor: pointer;\n    font-size: 13px;\n    font-weight: bold;\n    margin-bottom: 4px;\n    overflow: hidden;\n  }\n\n  .artist {\n    color: $textGrey;\n    cursor: pointer;\n    font-size: 11px;\n    font-weight: 100;\n  }\n\n  .album-art {\n    border-radius: 50%;\n    cursor: pointer;\n    height: 60px;\n    position: relative;\n    width: 60px;\n    z-index: 1;\n  }\n\n  .more-button {\n    display: flex;\n\n    i {\n      color: #cbcbcb;\n      cursor: pointer;\n      display: inline-block;\n      font-size: 23px;\n    }\n\n  }\n\n  .right-stuff {\n    align-items: center;\n    color: #505050;\n    cursor: pointer;\n    display: flex;\n    flex: 1;\n    font-size: 11px;\n    height: 100%;\n    justify-content: flex-end;\n    margin-right: 5px;\n    text-align: right;\n\n    span { // tag\n      cursor: pointer;\n      font-size: 9px;\n    }\n\n    .check {\n      font-size: 12px;\n      position: relative;\n      right: 2px;\n     