import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { TrackList } from 'components';
import { scrollTo } from 'lib/utils';
import Loader from 'static/images/loader.svg';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { AD_VIDEO } from 'lib/constants';

import {
  loadTrack,
  resetQueueCurrentIndex,
  queueReorder,
  appendToQueue,
  playCurrentPin,
  play,
} from 'actions/playerActions';

import {
  loadMoreRelated,
} from 'actions/searchActions';

import {
  updatePlaylistPreview,
} from 'actions/previewActions';

const SOURCE_TYPES = {
  RADIO: 'radio',
  RELATED: 'related',
  COLLECTION: 'collection',
  PLAYLIST: 'playlist',
  CHANNEL: 'channel',
};

class QueueList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingMore: false,
    };
  }

  componentDidMount() {
    const queueList = document.querySelector('.queue-list');
    const active = document.querySelector('.queue-list .list-item.active');
    scrollTo(queueList, active);
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    const { playerStore } = this.props;
    const queueList = document.querySelector('.queue-list');
    if (!queueList) return;

    const newTrack = newProps.playerStore.get('currentTrack');
    const newShuffle = newProps.playerStore.getIn(['queue', 'shuffled']);
    const currentShuffle = playerStore.getIn(['queue', 'shuffled']);
    const newQueueId = newProps.playerStore.getIn(['queue', 'id']);
    const currentQueuId = playerStore.getIn(['queue', 'id']);
    const currentPin = playerStore.get('currentPin');
    const newCurrentPin = newProps.playerStore.get('currentPin');
    const queueChanged = newQueueId !== currentQueuId;
    const trackChanged = newTrack.get('id') !== this.currentTrack.get('id')
      || currentPin !== newCurrentPin;
    const shuffleChanged = newShuffle !== currentShuffle;

    // from no shuffle to shuffle
    if ((shuffleChanged && newShuffle)) {
      const active = document.querySelector('.queue-list .list-item:first-child');
      scrollTo(queueList, active);

    // from shuffle to no shuffle or a new queue
    } else if ((shuffleChanged && !newShuffle) || queueChanged) {
      setTimeout(() => {
        const active = queueList.querySelector(`#track-${this.currentTrack.get('id')}`);
        scrollTo(queueList, active);
      });
    } else if (trackChanged) {
      setTimeout(() => {
        const active = queueList.querySelector(`#track-${newCurrentPin || newTrack.get('id')}`);
        scrollTo(queueList, active);
      });
    }
  }

  // speed up search
  shouldComponentUpdate(newProps) {
    const { searchStore } = this.props;
    return newProps.searchStore.get('searchBarValue') === searchStore.get('searchBarValue');
  }

  onTrackClick = (track) => {
    ga('send', 'event', 'skip');

    if (this.isAdPlaying()) {
      return;
    }

    const { dispatch } = this.props;

    const pin = track.get('pin');

    if (pin !== null) {
      dispatch(playCurrentPin(pin));
      dispatch(play());
    } else {
      dispatch(loadTrack(track));
      dispatch(resetQueueCurrentIndex());
    }
  }

  onLoadMore = () => {
    this.setState({ isLoadingMore: true });
    // consider inheritance.
    const { searchStore, dispatch } = this.props;

    switch (this.queue.get('source')) {
      case SOURCE_TYPES.RELATED:
        dispatch(loadMoreRelated(this.currentTrack, searchStore.get('nextRelatedPageToken')))
          .then((tracks) => {
            dispatch(appendToQueue(tracks));
            this.setState({ isLoadingMore: false });
          });
        break;

      case SOURCE_TYPES.RADIO:
      case SOURCE_TYPES.CHANNEL:
      case SOURCE_TYPES.PLAYLIST:
        dispatch(updatePlaylistPreview(this.previewPlaylist)).then((tracks) => {
          dispatch(appendToQueue(tracks, 'playlist'));
          this.setState({ isLoadingMore: false });
        });
        break;
      default:
        break;
    }
  }

  // reload the queue with the sorted tracks
  onReorder = (reOrderedTracks) => {
    this.props.dispatch(queueReorder(reOrderedTracks));
  }

  get queueSize() {
    return this.queue.get('tracks').size;
  }

  /* eslint-disable react/destructuring-assignment */
  get queue() {
    return this.props.playerStore.get('queue');
  }

  get currentTrack() {
    return this.props.playerStore.get('currentTrack');
  }

  get currentPin() {
    return this.props.playerStore.get('currentPin');
  }

  get currentPlaylist() {
    return this.props.playerStore.get('currentPlaylist');
  }

  get currentStation() {
    return this.props.playerStore.get('currentStation');
  }

  get previewPlaylist() {
    return this.props.previewStore.getIn(['playlists', this.currentPlaylist.get('id')]);
  }
  /* eslint-enable react/destructuring-assignment */

  get playlistNextToken() {
    if (!this.currentPlaylist) return false;
    return this.previewPlaylist.get('nextPageToken');
  }

  get isThereMoreToLoad() {
    // consider inheritance. !!!!!!!
    const { searchStore } = this.props;
    if (this.currentPin !== null) {
      return false;
    }
    switch (this.queue.get('source')) {
      case SOURCE_TYPES.RELATED:
        return !!searchStore.get('nextRelatedPageToken');
      case SOURCE_TYPES.PLAYLIST:
      case SOURCE_TYPES.CHANNEL:
      case SOURCE_TYPES.RADIO:
        return !!this.playlistNextToken;
      case SOURCE_TYPES.COLLECTION:
      default:
        return false;
    }
  }

  isAdPlaying() {
    return this.currentTrack.get('id') === AD_VIDEO.get('id');
  }

  render() {
    const { collectionsStore, sortable } = this.props;
    const { isLoadingMore } = this.state;
    const collections = collectionsStore.get('collections');

    return (
      <div className="queue-display">
        {this.queue.get('tracks').size
          ? (
            <TrackList
              collections={collections}
              className="queue-list"
              tracks={this.queue.get('tracks')}
              currentTrack={this.currentTrack}
              currentPin={this.currentPin}
              onItemSelect={this.onTrackClick}
              isLoadingMore={isLoadingMore}
              onLoadMoreClick={this.onLoadMore}
              needLoadMore={this.isThereMoreToLoad}
              onReorder={this.onReorder}
              sortable={sortable}
            />
          ) : <img alt="loader" src={Loader} />}
      </div>

    );
  }
}

QueueList.propTypes = {
  playerStore: ImmutablePropTypes.map.isRequired,
  searchStore: ImmutablePropTypes.map.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  previewStore: ImmutablePropTypes.map.isRequired,
  sortable: PropTypes.bool.isRequired,
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  radioStore: state.radioReducer,
  playerStore: state.playerReducer,
  previewStore: state.previewReducer,
  searchStore: state.searchReducer,
  collectionsStore: state.collectionsReducer,
});

export default connect(mapStateToProps)(QueueList);
