import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { formatDuration } from 'lib/youtube';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';

class ProgressBar extends PureComponent {
  render() {
    const { progressBarStore, track, showTimes, isAlbum, onProgressClick } = this.props;
    const currentTime = progressBarStore.get('currentTime');
    const duration = track.get('duration');
    const isLive = duration === 0;
    const end = isAlbum
      ? track.get('albumDurationClockFormat')
      : track.get('durationClockFormat');

    return (
      <div className="times">
        {showTimes
          && (
          <div className="time current">
            {formatDuration(currentTime)}
          </div>
          )}
        <progress
          min="0"
          value={progressBarStore.get('currentTime')}
          max={duration}
          onClick={onProgressClick}
        />
        {showTimes
          && (
          <div className="time duration">
            {isLive ? <span className="live-tag">Live</span> : end}
          </div>
          )}
      </div>
    );
  }
}

ProgressBar.propTypes = {
  track: ImmutablePropTypes.map,
  onProgressClick: PropTypes.func,
  progressBarStore: ImmutablePropTypes.map.isRequired,
  showTimes: PropTypes.bool,
  isAlbum: PropTypes.bool,
};

ProgressBar.defaultProps = {
  track: Map(),
  onProgressClick: () => {},
  showTimes: true,
  isAlbum: false,
};

const mapStateToProps = (state) => ({ progressBarStore: state.progressBarReducer });

export default connect(mapStateToProps)(ProgressBar);
