import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { TrackList } from 'components';
import { playerStates } from 'lib/youtube';
import Loader from 'static/images/loader.svg';
import { fromJS } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';
import {
  play,
  pause,
  loadTrack,
  loadQueue,
  showPlayer,
  setCurrentPlaylist,
  setCurrentRadioStation,
} from 'actions/playerActions';
import {
  loadMoreSearchResults,
  fetchTrackRelated,
} from 'actions/searchActions';

import ApiKeyForm from '../../components/ApiKeyForm';

class SearchResults extends Component {
  // speedup search
  shouldComponentUpdate(newProps) {
    const { searchStore } = this.props;
    return newProps.searchStore.get('searchBarValue') === searchStore.get('searchBarValue');
  }

  handlePlaylistSelect(item) {
    const { router } = this.context;
    router.push(`preview/playlist/${item.get('id')}`);
  }

  handleChannelSelect(item) {
    const { router } = this.context;
    router.push(`preview/channel/${item.get('id')}`);
  }

  handleTrackSelect(track) {
    const { dispatch, playerStore } = this.props;
    const currentTrack = playerStore.get('currentTrack');
    const playerState = playerStore.get('playerState');

    // handle track
    if (currentTrack.get('id') !== track.get('id')) {
      dispatch(loadTrack(track));
      dispatch(setCurrentPlaylist(null));
      dispatch(showPlayer());
      dispatch(fetchTrackRelated(track)).then((tracks) => {
        // Always put the selected track first in the related list
        const queue = fromJS([track].concat(tracks));
        dispatch(loadQueue(queue, 'related'));
      });
    } else if (playerState === playerStates.PLAYING) {
      dispatch(pause());
    } else {
      dispatch(play());
    }
  }

  onScroll = (e) => {
    window.searchScroll = e.target.scrollTop;
  }

  onItemSelect = (item) => {
    this.props.dispatch(setCurrentRadioStation(null));
    if (item.get('type') === 'playlist') {
      this.handlePlaylistSelect(item);
    } else if (item.get('type') === 'channel') {
      this.handleChannelSelect(item);
    } else {
      this.handleTrackSelect(item);
    }
  };

  get collections() {
    const { collectionsStore } = this.props;
    return collectionsStore.get('collections');
  }

  loadMore = () => {
    const { searchStore, dispatch } = this.props;
    const searchTerm = searchStore.get('searchTerm');
    const nextPageToken = searchStore.get('nextSearchPageToken');
    dispatch(loadMoreSearchResults(searchTerm, nextPageToken));
  }

  render() {
    const { searchStore, playerStore } = this.props;
    const results = searchStore.get('results');
    const playerState = playerStore.get('playerState');
    const currentTrack = playerStore.get('currentTrack');
    const isLoadingMore = searchStore.get('isLoadingMore');
    const currentPlaylist = playerStore.get('currentPlaylist');

    if (!localStorage.getItem('youtube-api-key')) {
      return <ApiKeyForm />;
    }

    if (results.isEmpty()) {
      return (
        <div className="page-loader">
          <img alt="" src={Loader} />
          <h2>Loading</h2>
        </div>
      );
    }

    return (
      <div className="search-results">
        <TrackList
          tracks={results}
          collections={this.collections}
          onItemSelect={this.onItemSelect}
          currentTrack={currentTrack}
          currentPlaylist={currentPlaylist}
          playerState={playerState}
          isLoadingMore={isLoadingMore}
          onLoadMoreClick={this.loadMore}
          onScroll={this.onScroll}
          scrollPosition={window.searchScroll || 0}
        />
      </div>
    );
  }
}

SearchResults.propTypes = {
  searchStore: ImmutablePropTypes.map.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  dispatch: PropTypes.func,
};

SearchResults.defaultProps = {
  dispatch: () => {},
};

SearchResults.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  searchStore: state.searchReducer,
  collectionsStore: state.collectionsReducer,
  playerStore: state.playerReducer,
});

export default connect(mapStateToProps)(SearchResults);
