import moment from 'moment-mini';

const ts = () => moment().unix();

const EXPIRY_DAYS = 10;

class YoutTubeAPICache {
  constructor() {
    YoutTubeAPICache._disableClearStorage();

    setTimeout(() => {
      YoutTubeAPICache._clearExpiredCache();
    }, 1000);
  }

  static get(id) {
    try {
      const item = JSON.parse(localStorage.getItem(id));
      if (!item.id) return null;
      return item;
    } catch (err) { return null; }
  }

  static set(id, data) {
    const dataWithTimeStamp = { ...data, ts: ts() };
    try {
      localStorage.setItem(id, JSON.stringify(dataWithTimeStamp));
    } catch (err) { /* do nothing */ }
  }

  static invalidate(id) {
    localStorage.removeItem(id);
  }

  static missingIds(ids) {
    return ids.filter((id) => !YoutTubeAPICache.get(id));
  }

  static setBulk(obj = {}) {
    Object.keys(obj).forEach((id) => {
      YoutTubeAPICache.set(id, obj[id]);
    });
  }

  static getBulk(ids) {
    return ids
      .map((id) => YoutTubeAPICache.get(id))
      .filter((t) => t !== null);
  }

  static _disableClearStorage() {
    Storage.prototype._clear = Storage.prototype.clear;
    Storage.prototype.clear = () => undefined;
  }

  /*
    When cache needs to be flushed due to a problem,
    we set the lastCacheForcedUpdateDate to today's date
    and postfix the number of times it was changed during the day (e.g 01, 02, etc.)
  */
  static needForceClear() {
    const lastCacheForcedUpdateDate = '2018-03-19-01';
    if (!localStorage.getItem(lastCacheForcedUpdateDate)) {
      localStorage.setItem(lastCacheForcedUpdateDate, 'true');
      return true;
    }

    return false;
  }

  static _clearExpiredCache() {
    const now = moment();
    const needForceClear = YoutTubeAPICache.needForceClear();

    Object.keys(localStorage).forEach((id) => {
      const item = YoutTubeAPICache.get(id);
      if (item === null || !item.ts) return;

      const created = now.diff(moment.unix(item.ts), 'days');
      if (created >= EXPIRY_DAYS || needForceClear) {
        YoutTubeAPICache.invalidate(id);
      }
    });
  }
}

export default YoutTubeAPICache;
