import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { SavePlaylistButton } from 'containers';
import classnames from 'classnames';
import Image from 'lib/Image';
import ellipsize from 'ellipsize';
import BlankCollectionImage from 'static/images/blank-collection.png';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { InputGroup } from '@blueprintjs/core';

class PreviewHeader extends PureComponent {
  constructor() {
    super();
    this.state = {
      showDescription: false,
      isFilterFocused: false,
    };
  }

  get title() {
    const { title, renameable, onTitleClick } = this.props;
    if (renameable) {
      return (
        <span className={classnames({ renameable })} role="none" onClick={onTitleClick}>
          {ellipsize(title, 70)}
        </span>
      );
    }

    return ellipsize(title, 70);
  }

  get backLink() {
    const { hasBackButton } = this.props;
    const { router } = this.context;
    if (!hasBackButton) return null;

    return (
      <a
        href="#"
        onClick={router.goBack}
        className="preivew-back-link"
      >
        <i className="material-icons">chevron_left</i>
      </a>
    );
  }

  get description() {
    const { previewable } = this.props;
    const { showDescription } = this.state;
    const title = previewable.get('title');

    return (
      <div className={classnames('description-wrapper', { active: showDescription })}>
        <div className="preview-description">
          <div className="description-header">
            <h3>
              {title}
            </h3>
          </div>
          <div className="description-content">
            {previewable.get('description')}
          </div>
        </div>
      </div>
    );
  }

  get infoIcon() {
    const { previewable } = this.props;
    if (!previewable.get('description')) return null;
    return (
      <i className="material-icons preview-info-icon" role="none" onClick={this.toggleInfo}>info_outline</i>
    );
  }

  get isBlank() {
    const { previewable } = this.props;
    return !previewable.get('tracks').size;
  }

  toggleInfo = () => {
    const { previewable } = this.props;
    const title = previewable.get('customUrl');
    ga('send', 'event', 'Toggle Channel Info', title);

    this.setState((prevState) => ({ showDescription: !prevState.showDescription }));
  }

  render() {
    const {
      previewable,
      subtitle,
      image,
      onSave,
      onUnsave,
      isLikes,
      title,
      onFilterChange,
      filterValue,
      onShufflePlay,
    } = this.props;
    const { showDescription, isFilterFocused } = this.state;

    if (this.isBlank) {
      return (
        <div className="preview-header blank">
          {this.backLink}
          <Image className="preview-image" src={BlankCollectionImage} />
          <div className="preview-info">
            <h2 className="preview-title">
              {this.title}
            </h2>
          </div>
        </div>
      );
    }

    return (
      <div className={classnames('preview-header', { down: showDescription })}>
        <div className="preivew-overlay" />
        {this.backLink}
        {this.infoIcon}
        <Image className="preview-image" src={image} />
        {this.description}
        <div className={classnames('preview-info', { down: showDescription })}>
          {!isLikes
            && (
            <SavePlaylistButton
              playlist={previewable}
              onSave={onSave}
              onUnsave={onUnsave}
            />
            )}
          <h2 className="preview-title" title={title}>
            {this.title}
          </h2>
          <p className="preview-count">
            {subtitle}
          </p>
          {isLikes && (
            <InputGroup
              leftIcon="filter"
              placeholder="Filter"
              onChange={onFilterChange}
              value={filterValue}
              onFocus={() => this.setState({ isFilterFocused: true })}
              onBlur={() => this.setState({ isFilterFocused: false })}
              className={classnames('likes-filter', { active: isFilterFocused })}
            />
          )}
        </div>
        <div className={classnames('preview-shuffle', { down: showDescription })}>
          <div className="preview-shuffle-button" role="none" onClick={onShufflePlay}>
            Shuffle
          </div>
        </div>
      </div>
    );
  }
}

PreviewHeader.propTypes = {
  previewable: ImmutablePropTypes.map,
  image: PropTypes.string,
  onSave: PropTypes.func,
  onUnsave: PropTypes.func,
  subtitle: PropTypes.string,
  title: PropTypes.string,
  onTitleClick: PropTypes.func,
  renameable: PropTypes.bool,
  onShufflePlay: PropTypes.func,
  hasBackButton: PropTypes.bool,
  isLikes: PropTypes.bool,
  onFilterChange: PropTypes.func,
  filterValue: PropTypes.string,
};

PreviewHeader.defaultProps = {
  previewable: Map(),
  image: '',
  onSave: () => {},
  onUnsave: () => {},
  onFilterChange: () => {},
  filterValue: '',
  subtitle: '',
  title: '',
  onTitleClick: () => {},
  renameable: false,
  onShufflePlay: () => {},
  hasBackButton: true,
  isLikes: false,
};

PreviewHeader.contextTypes = {
  router: PropTypes.object.isRequired,
};

export default PreviewHeader;
