import reddit from 'lib/reddit';
import {
  updateUser,
} from 'actions/appActions';
import actionTypes from './actionTypes';

export const clearLoadingMore = () => ({
  type: actionTypes.MORE_LOADED,
});

export const stepKeyboadPick = (stationIndex) => ({
  type: actionTypes.UPDATE_SELECTED_SEARCH, payload: stationIndex,
});

export const fetchRadioTracks = (station) => (
  (dispatch) => (
    reddit.search(station).then(({ tracks, nextPageToken }) => {
      dispatch({ type: actionTypes.SET_NEXT_RADIO_PAGE_TOKEN, payload: nextPageToken });
      return tracks;
    }).catch(() => {
      dispatch(stepKeyboadPick(-1));
    })
  )
);

export const saveRadioStation = (stations) => (
  (dispatch) => {
    dispatch({ type: actionTypes.STORE_SAVED_RADIO, payload: stations });
    return dispatch(updateUser({ attribute: 'stations', value: stations }));
  }
);

export const updateSearchBarValue = (value) => ({
  type: actionTypes.UPDATE_RADIO_SEARCH_BAR, payload: value,
});

export const changeRadioSort = (sortValue) => ({
  type: actionTypes.CHANGE_REDDIT_SORT_FILTER, payload: sortValue,
});

export const changeRadioT = (tValue) => ({
  type: actionTypes.CHANGE_REDDIT_T_FILTER, payload: tValue,
});
