import PropTypes from 'prop-types';
import React, { Component } from 'react';
import classnames from 'classnames';
import { Map, List } from 'immutable';
import { SortableListItem, LoadMoreButton } from 'components';
import Power from 'static/images/power.png';
import PowerWhite from 'static/images/power-white.png';
import HeadsetLogo from 'static/images/logo-icon.png';
import ImmutablePropTypes from 'react-immutable-proptypes';
import TrackItem from './TrackItem';

const themedLogo = {
  Dark: PowerWhite,
};

class TrackList extends Component {
  constructor(props) {
    super();
    this.loadMoreRef = null;
    this.positionBeforeLoadMore = null;
    this.state = {
      tracks: props.tracks.toArray(),
    };
  }

  componentDidMount() {
    const { onScroll, scrollPosition } = this.props;
    document.addEventListener('drag', this.onDrag);
    if (onScroll) {
      this.list.addEventListener('scroll', onScroll);
    }

    this.list.scrollTop = scrollPosition;
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    this.setState({
      tracks: newProps.tracks.toArray(),
    });
  }

  componentWillUnmount() {
    const { onScroll } = this.props;
    document.removeEventListener('drag', this.onDrag);
    if (onScroll) {
      this.list.removeEventListener('scroll', onScroll);
    }
  }

  onScroll = (e) => {
    window.trackListScrollPosition = e.target.scrollTop;
  }

  // this method is to ensure list scrolls when the drag touches the bottom and top
  onDrag = (e) => {
    const { top, bottom } = document.body.getBoundingClientRect();
    const topTrigger = top + 350;
    const bottomTrigger = bottom - 200;

    // scroll up
    if (e.clientY < topTrigger) {
      this.list.scrollTop -= (topTrigger - e.clientY) * 0.1;
    }
    // scroll bottom

    if (e.clientY > bottomTrigger) {
      this.list.scrollTop += (e.clientY - bottomTrigger) * 0.1;
    }
  }

  // this is the handler of react-sortable
  onTrackDrag = (dragged) => {
    if (!this.props.sortable) return;
    if (!dragged.draggingIndex) {
      this.props.onReorder(this.state.tracks);
      this.forceUpdate();
    }
    this.setState(dragged);
  }

  get trackItems() {
    const {
      onItemSelect,
      collections,
    } = this.props;
    const { tracks } = this.state;

    return tracks.map((track) => (
      <TrackItem
        key={track.get('id')}
        isLiked={!!collections.getIn(['likes', 'tracks', track.get('id')])}
        onClick={onItemSelect.bind(this, track)}
        isCurrent={this.isCurrent(track)}
        item={track}
        sortable={false}
      />
    ));
  }

  get powerImage() {
    const { user } = this.context;
    if (!user) return Power;

    return themedLogo[user.get('theme')] || Power;
  }

  get power() {
    return (
      <div className="power" key="power">
        <img src={HeadsetLogo} alt="logo-2" className="hs" />
        <img src={this.powerImage} alt="logo-1" className="yt" />
      </div>
    );
  }

  get sortableTrackItems() {
    const {
      onItemSelect,
      collections,
    } = this.props;
    const { tracks } = this.state;

    return tracks.map((track, i) => (
      <SortableListItem
        key={i}
        onSortItems={this.onTrackDrag}
        items={tracks}
        sortId={i}
        outline="list"
      >
        <TrackItem
          isLiked={!!collections.getIn(['likes', 'tracks', track.get('id')])}
          onClick={onItemSelect.bind(this, track)}
          isCurrent={this.isCurrent(track)}
          item={track}
          sortable
        />
      </SortableListItem>
    ));
  }

  isCurrent(item) {
    const { currentPin, currentTrack, currentPlaylist } = this.props;

    if (currentPin !== null) {
      return currentPin === item.get('pin');
    }

    switch (item.get('type')) {
      case 'video':
        return currentTrack && currentTrack.get('id') === item.get('id');
      case 'playlist':
        return currentPlaylist && currentPlaylist.get('id') === item.get('id');
      default:
        return false;
    }
  }

  renderTracks() {
    const {
      onLoadMoreClick,
      isLoadingMore,
      needLoadMore,
      sortable,
      loadMore,
    } = this.props;

    /* eslint-disable indent */
    return [
      sortable ? this.sortableTrackItems : this.trackItems,
      loadMore
        && (
          <LoadMoreButton
            buttonRef={(load) => { this.loadMoreRef = load; }}
            key="load-more"
            onClick={onLoadMoreClick}
            loading={isLoadingMore}
            isEnd={!needLoadMore}
          />
        ),
      this.power,
    ];
    /* eslint-enable indent */
  }

  render() {
    const { tracks, currentTrack, className } = this.props;

    if (tracks.isEmpty()) {
      return null;
    }

    return (
      <div
        ref={(node) => { this.list = node; }}
        className={classnames('TrackList', { 'with-player': !currentTrack.isEmpty() }, className)}
      >
        {this.renderTracks()}
      </div>
    );
  }
}

TrackList.propTypes = {
  tracks: ImmutablePropTypes.list,
  collections: ImmutablePropTypes.map,
  currentTrack: ImmutablePropTypes.map,
  currentPlaylist: ImmutablePropTypes.map,
  currentPin: PropTypes.number,
  onItemSelect: PropTypes.func,
  className: PropTypes.string,
  onLoadMoreClick: PropTypes.func,
  isLoadingMore: PropTypes.bool,
  needLoadMore: PropTypes.bool,
  loadMore: PropTypes.bool,
  onReorder: PropTypes.func,
  sortable: PropTypes.bool,
  onScroll: PropTypes.func,
  scrollPosition: PropTypes.number,
};

TrackList.defaultProps = {
  tracks: List(),
  currentTrack: Map(),
  currentPlaylist: Map(),
  onItemSelect: () => {},
  className: '',
  onLoadMoreClick: () => {},
  collections: Map(),
  needLoadMore: true,
  isLoadingMore: false,
  loadMore: true,
  onReorder: () => {},
  sortable: false,
  onScroll: () => {},
  scrollPosition: 0,
  currentPin: null,
};

TrackList.contextTypes = {
  user: PropTypes.object,
};

export default TrackList;
