import actionTypes from './actionTypes';

export const updateScrobble = ({ key, value }) => ({
  type: actionTypes.UPDATE_SCROBBLE, payload: { key, value },
});

export const openDialog = () => ({
  type: actionTypes.OPEN_SCROBBLE_DIALOG,
});

export const closeDialog = () => ({
  type: actionTypes.CLOSE_SCROBBLE_DIALOG,
});

export const restore = () => ({
  type: actionTypes.RESTORE_SCROBBLE,
});

export const loadScrobble = (track) => ({
  type: actionTypes.LOAD_SCROBBLE, payload: track,
});

export const toggleRememberAlbum = () => ({
  type: actionTypes.TOGGLE_REMMBER_ALBUM,
});

export const toggleRememberArtist = () => ({
  type: actionTypes.TOGGLE_REMMBER_ARTIST,
});
