import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import Loader from 'static/images/small-loader.svg';
import classnames from 'classnames';

class LoadMoreButton extends PureComponent {
  // eslint-disable-next-line class-methods-use-this
  renderLoader() {
    return <img alt="loader" src={Loader} className="small-loader" />;
  }

  // eslint-disable-next-line class-methods-use-this
  renderText() {
    return (
      <div className="loading-button-text active upper">
        Load more
      </div>
    );
  }

  render() {
    const { loading, onClick, buttonRef, isEnd } = this.props;
    if (isEnd) return null;

    return (
      <div
        className={classnames('load-more-button', { loading, ended: isEnd })}
        onClick={onClick}
        ref={(node) => buttonRef(node)}
        role="none"
      >
        {loading ? this.renderLoader() : this.renderText()}
      </div>
    );
  }
}

LoadMoreButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  loading: PropTypes.bool,
  buttonRef: PropTypes.func,
  isEnd: PropTypes.bool,
};

LoadMoreButton.defaultProps = {
  loading: false,
  buttonRef: () => {},
  isEnd: false,
};

export default LoadMoreButton;
