import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { browserHistory } from 'react-router';
import FontResizer from 'lib/FontResizer';

class Page extends PureComponent {
  get subtitle() {
    const { subtitle } = this.props;
    if (!subtitle) return null;

    return (
      <span className="subtitle">
        {subtitle}
      </span>
    );
  }

  renderTitle() {
    const { title } = this.props;
    if (typeof title === 'string') {
      return (
        <FontResizer>
          {title}
        </FontResizer>
      );
    }

    return title;
  }

  renderBackButton() {
    const { hasBackLink } = this.props;
    return (
      <a href="#" onClick={browserHistory.goBack} className="back-link">
        {hasBackLink
          ? <i className="material-icons">chevron_left</i>
          : null}
      </a>
    );
  }

  renderRightMenu() {
    const { rightMenu } = this.props;
    if (rightMenu) {
      return rightMenu;
    }
    return null;
  }

  renderHeader() {
    const { header, title, icon } = this.props;

    if (header) return header;

    if (title === undefined) return null;

    return (
      <div className="page-header">
        {this.renderBackButton()}
        <h2 className="title">
          {this.subtitle}
          <i className="material-icons icon-title">
            {icon}
          </i>
          {this.renderTitle()}
        </h2>
        <div className="right-menu">
          {this.renderRightMenu()}
        </div>
      </div>
    );
  }

  render() {
    const { children } = this.props;
    return (
      <div className="page">
        {this.renderHeader()}
        {children}
      </div>
    );
  }
}

Page.propTypes = {
  header: PropTypes.node,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  children: PropTypes.node,
  icon: PropTypes.string,
  hasBackLink: PropTypes.bool,
  subtitle: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  rightMenu: PropTypes.node,
};

Page.defaultProps = {
  header: null,
  title: undefined,
  children: null,
  icon: '',
  hasBackLink: true,
  subtitle: '',
  rightMenu: null,
};
export default Page;
