import React, { useCallback, useState } from "react";
import { Button, InputGroup } from "@blueprintjs/core";
import { ExternalLink } from "components";
import { search } from "lib/youtube";

export default () => {
  const [isLoading, setIsLoading] = useState(false);
  const [key, setKey] = useState("");
  const [error, setError] = useState(null);

  const applyKey = useCallback(async () => {
    try {
      setIsLoading(true);
      localStorage.setItem("youtube-api-key", key);
      await search("abc");
      setIsLoading(false);
      document.location.reload();
    } catch (err) {
      localStorage.removeItem("youtube-api-key");
      setError(err.response);
      setIsLoading(false);
    }
  }, [key]);
  /* eslint-disable no-alert */
  return (
    <div className="api-key-form">
      <h3>Welcome to Headset! Please enter Youtube API key to continue</h3>
      <p>
        As of recent changes with Youtube API usage limits, we ask users to
        provide their own key in order to use Headset. This is a one-time setup.
        We apologize for the inconvenience.
      </p>
      <p>
        <ExternalLink href="https://github.com/headsetapp/headset-electron/wiki/Get-Youtube-API-Key">
          Please follow the instructions on how to do it.
        </ExternalLink>
      </p>
      <br />
      <p>Enter API key</p>
      <span className="api-key">
        <InputGroup
          placeholder="AIzaSy..."
          disabled={isLoading}
          onChange={(e) => setKey(e.target.value)}
          value={key}
        />
      </span>
      <Button
        text="Save"
        className="button"
        intent="success"
        onClick={applyKey}
        loading={isLoading}
      />
      <br />
      <br />
      <p>
        <small>
          <b>Disclaimer:</b>
          {
            `The key is saved on your computer and only used when communicating with Youtube.
            If you use Headset on more than one computer, you will have to repeat this step.`
          }
        </small>
      </p>
      <div
        className="error"
        style={{ color: "red", textAlign: "center", width: "100%" }}
      >
        {error ? (
          <p>
            Error: Invalid key
            <br />
            <button type="button" onClick={() => alert(JSON.stringify(error))}>
              Click for details
            </button>
          </p>
        ) : null}
      </div>
    </div>
  );
};
/* eslint-enable no-alert */
