import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';
import ImmutablePropTypes from 'react-immutable-proptypes';

import {
  notify,
} from 'actions/appActions';

import {
  Header,
  MainMenu,
  UpdateToast,
} from 'components';

import {
  Player,
  AddToCollection,
  StripeDialog,
  // MoreSkipsDialog,
  ProDiscoverDialog,
} from 'containers';

const listenToUpdateEvent = () => {
  try {
    window.ipcRenderer.on('update-ready', () => {
      notify(<UpdateToast />, { timeout: 1000000 });
    });
  } catch (err) { /* doesn't work in test */ }
};

class App extends Component {
  getChildContext() {
    return {
      user: this.user,
      notify: this.onContextNotification,
      theme: this.theme,
    };
  }

  componentDidMount() {
    listenToUpdateEvent();
  }

  onContextNotification = (msg, options) => {
    this.props.dispatch(notify(msg, options));
  }

  get user() {
    const { appStore } = this.props;
    return appStore.get('user');
  }

  get path() {
    const { routingStore } = this.props;
    return routingStore.locationBeforeTransitions.pathname;
  }

  get theme() {
    if (!this.user) return null;
    const theme = this.user.get('theme');
    if (!theme) return null;

    return theme.toLowerCase();
  }

  render() {
    const { appStore, collectionsStore, children } = this.props;
    const isMenuOpen = appStore.get('isMenuOpen');

    return (
      <div className={classnames('theme', this.theme)}>
        <Header path={this.path} />
        <div onClick={this.closeMenu} role="none" className={classnames('wrapper', { 'with-menu': isMenuOpen })}>
          {children}
          <Player />
          <AddToCollection />
        </div>
        <MainMenu path={this.path} newLikeAdded={collectionsStore.get('newLikeAdded')} />
        <StripeDialog />
        {/* <MoreSkipsDialog /> */}
        <ProDiscoverDialog />
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.node.isRequired,
  dispatch: PropTypes.func.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  routingStore: PropTypes.shape({
    locationBeforeTransitions: PropTypes.object.isRequired,
  }).isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
};

App.childContextTypes = {
  user: PropTypes.object,
  notify: PropTypes.func,
  theme: PropTypes.string,
};

App.contextTypes = {
  router: PropTypes.object,
};

const mapStateToProps = (state) => ({
  appStore: state.appReducer,
  collectionsStore: state.collectionsReducer,
  routingStore: state.routing,
});

export default connect(mapStateToProps)(App);
