import PropTypes from "prop-types";
import React, { Component } from "react";
import { connect } from "react-redux";
import ImmutablePropTypes from "react-immutable-proptypes";
import { Button, Dialog } from "@blueprintjs/core";
import { Elements } from "react-stripe-elements";
import { DIALOGS } from "lib/constants";
import { updateUser, openDialog, closeDialog } from "actions/appActions";

import CheckoutForm from "./CheckoutForm";

class StripeDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paymentSuccess: false,
      stripeCustomerID: null,
    };
  }

  onClose = (e) => {
    e.preventDefault();
    localStorage.setItem("stickToBasics", true);
    this.props.dispatch(closeDialog(DIALOGS.STRIPE));
  };

  onSuccessClose = () => {
    this.props.dispatch(
      updateUser({
        attribute: "stripeCustomerID",
        value: this.state.stripeCustomerID,
      }) // eslint-disable-line
    );
    localStorage.setItem("stickToBasics", true);
    this.props.dispatch(closeDialog(DIALOGS.STRIPE));
  };

  onPaySuccess = (stripeCustomerID) => {
    this.setState({ stripeCustomerID, paymentSuccess: true });
  };

  openEmailConfirmationDialog = () => {
    this.props.dispatch(openDialog(DIALOGS.EMAIL_CONFIRMATION));
  };

  renderThankYou() {
    return (
      <center>
        <h2 className="dialog-title">All Set</h2>
        <i className="material-icons payment-check">check</i>
        <h3>Thank you!</h3>
        <br />
        <Button text="Done" onClick={this.onSuccessClose} />
      </center>
    );
  }

  renderStripeFormz() {
    return (
      <div>
        <div className="close-button">
          <a onClick={this.onClose}>
            <i className="material-icons">close</i>
          </a>
        </div>
        <Elements>
          <CheckoutForm onPaySuccess={this.onPaySuccess} />
        </Elements>
      </div>
    );
  }

  renderEmailConfirmButton() {
    return (
      <center>
        <p>You need to confirm your email first</p>
        <br />
        <Button
          className="upper"
          text="Confirm Now"
          onClick={this.openEmailConfirmationDialog}
        />
      </center>
    );
  }

  renderDialogBody() {
    const { paymentSuccess } = this.state;
    if (paymentSuccess) {
      return this.renderThankYou();
    }

    return this.renderStripeFormz();
  }

  render() {
    const { appStore } = this.props;
    const { theme } = this.context;
    return (
      <Dialog
        inline
        lazy
        isOpen={appStore.getIn([DIALOGS.STRIPE, "open"])}
        canOutsideClickClose={false}
        onClose={this.onClose}
        className={`pt-${theme} stripe-dialog`}
      >
        {this.renderDialogBody()}
      </Dialog>
    );
  }
}

StripeDialog.propTypes = {
  dispatch: PropTypes.func.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
};

StripeDialog.contextTypes = {
  theme: PropTypes.string,
  user: ImmutablePropTypes.map,
};

const mapStateToProps = (state) => ({
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(StripeDialog);
