import PropTypes from 'prop-types';
import React from 'react';
import { ProgressBar } from 'containers';
import Image from 'lib/Image';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';

const MiniPlayer = (props) => {
  const { onClick, track, isPlaying } = props;

  if (track.isEmpty()) {
    return <div className="mini-player" />;
  }

  const artist = track.get('artist');
  const title = track.get('title');

  const onPlayPauseClick = (e) => {
    e.stopPropagation();
    props.onPlayPauseClick();
  };

  const onSkipNext = (e) => {
    e.stopPropagation();
    props.onSkipNext();
  };

  return (
    <div className="mini-player active" role="none" onClick={onClick}>
      <Image src={track.get('thumbnail')} className="up-la" />
      <div className="progress-wrapper">
        <ProgressBar
          track={track}
          showTimes={false}
        />
        <div className="description">
          <div className="title">
            {title}
          </div>
          <div className="artist">
            {artist || ''}
          </div>
        </div>
        <i className="material-icons play-pause" role="none" onClick={onPlayPauseClick}>
          {isPlaying ? 'pause_circle_filled' : 'play_circle_filled'}
        </i>
        <i className="material-icons play-next" role="none" onClick={onSkipNext}>
          skip_next
        </i>
      </div>
    </div>
  );
};

MiniPlayer.propTypes = {
  track: ImmutablePropTypes.map,
  onClick: PropTypes.func.isRequired,
  onPlayPauseClick: PropTypes.func.isRequired,
  onSkipNext: PropTypes.func.isRequired,
  isPlaying: PropTypes.bool.isRequired,
};

MiniPlayer.defaultProps = {
  track: Map(),
};

export default MiniPlayer;
