import PropTypes from 'prop-types';
import React, { Component } from 'react';

const defaultStyles = {
  backgroundColor: 'white',
  backgroundPosition: 'center center',
  transition: 'all .3s ease',
  backgroundSize: 'cover',
  backgroundRepeat: 'no-repeat',
  opacity: 0, // for transition effect
  overflow: 'hidden',
};

class ReactImage extends Component {
  // eslint-disable-next-line consistent-return
  onLoad = (e) => {
    const { onLoad } = this.props;
    if (!this.img) return null;
    this.img.style.backgroundImage = `url(${e.target.src})`;
    this.img.style.opacity = '1';

    if (onLoad) onLoad();
  };

  render() {
    const { src, className, style, onClick, children } = this.props;
    const bgImg = new Image();
    bgImg.onload = this.onLoad;
    bgImg.src = src;

    return (
      <div
        onClick={onClick}
        className={className}
        alt="album-art"
        style={Object.assign(defaultStyles, style)}
        ref={(img) => { this.img = img; }}
        role="none"
      >
        {children}
      </div>
    );
  }
}

ReactImage.propTypes = {
  src: PropTypes.string.isRequired,
  className: PropTypes.string,
  style: PropTypes.instanceOf(Object),
  onLoad: PropTypes.func,
  onClick: PropTypes.func,
  children: PropTypes.node,
};

ReactImage.defaultProps = {
  className: '',
  style: {},
  onLoad: () => {},
  onClick: () => {},
  children: null,
};

export default ReactImage;
