import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { MenuDivider, Menu, MenuItem, Position, Popover } from '@blueprintjs/core';
import { IconNames } from '@blueprintjs/icons';
import { connect } from 'react-redux';
import { fetchRelated } from 'lib/youtube';
import { fromJS, Map } from 'immutable';
import Clipboard from 'clipboard';
import ImmutablePropTypes from 'react-immutable-proptypes';

import {
  startStationFromTrack,
  showPlayer,
  addToPlayNext,
  removeFromQueue,
  albumizeQueue,
} from 'actions/playerActions';

import {
  addTrackToLikes,
  removeTrackFromLikes,
  toggleCollectionsDialog,
} from 'actions/collectionsActions';

import {
  notify,
} from 'actions/appActions';

import {
  updateSearchTerm,
} from 'actions/searchActions';

class TrackMenu extends PureComponent {
  onAddCollectionClick = () => {
    const { track, dispatch } = this.props;

    if (!this.user) {
      this.context.router.push('/collections');
      return;
    }

    dispatch(toggleCollectionsDialog(track));
  }

  onStartStationClick = () => {
    ga('send', 'event', 'Start Station');
    const { track, dispatch } = this.props;
    dispatch(showPlayer());
    fetchRelated(track).then(({ tracks }) => {
      const queue = fromJS([track].concat(tracks));
      dispatch(startStationFromTrack(track, queue));
      dispatch(updateSearchTerm(track.get('title')));
    });
  }

  onLikesClick = () => {
    const { track, dispatch } = this.props;

    if (!this.user) {
      this.context.router.push('preview/collection/likes');
    } else if (this.isLiked) {
      dispatch(removeTrackFromLikes(track));
    } else {
      dispatch(addTrackToLikes(track));
    }
  }

  onOpenInYouTube = () => {
    const { track } = this.props;
    window.open(
      `https://www.youtube.com/watch?v=${track.get('id')}`,
      '',
      'width=970,height=640,titleBarStyle="normal"',
    );
  }

  onPlayNext = () => {
    const { dispatch, track } = this.props;
    dispatch(addToPlayNext(track));
    dispatch(notify('Track added to queue'));
  }

  onRemoveFromQueueClick = () => {
    const { dispatch, track } = this.props;
    dispatch(removeFromQueue(track));
    dispatch(notify('Remove From Queue'));
  }

  onAlbumize = () => {
    const { dispatch, track } = this.props;
    dispatch(albumizeQueue(track));
  }

  onCopyYoutubeLink = () => {
    const clipboard = new Clipboard();
    clipboard.on('success', () => {
      this.context.notify('Copied to clipboard');
    });
    clipboard.copy(`https://www.youtube.com/watch?v=${this.props.track.get('id')}`);
    clipboard.destroy();
  }

  get addToQueueMenuItem() {
    const { isCurrent } = this.props;
    return (
      <MenuItem
        icon={IconNames.PLUS}
        onClick={this.onPlayNext}
        text="Play Next"
        disabled={isCurrent}
      />
    );
  }

  get removeFromQueueMenuItem() {
    const { isCurrent, track } = this.props;
    if (isCurrent) return null;
    return (
      <MenuItem
        icon={IconNames.EJECT}
        onClick={this.onRemoveFromQueueClick}
        text="Remove From Queue"
        disabled={track.get('pin')}
      />
    );
  }

  get isLiked() {
    const { collectionsStore, track } = this.props;
    return collectionsStore.getIn(['collections', 'likes', 'tracks', track.get('id')]);
  }

  get user() {
    const { appStore } = this.props;
    return appStore.get('user');
  }

  renderMenu() {
    const { inPlayer } = this.context;
    const { isCurrent } = this.props;
    return (
      <Menu inline>
        <MenuItem
          icon={IconNames.HEART}
          onClick={this.onLikesClick}
          text={this.isliked ? 'Unlike' : 'Like'}
        />
        {inPlayer ? this.removeFromQueueMenuItem : this.addToQueueMenuItem}
        <MenuItem
          icon={IconNames.ADD_TO_ARTIFACT}
          onClick={this.onAddCollectionClick}
          text="Collections"
        />
        <MenuItem
          icon={IconNames.FLASH}
          onClick={this.onStartStationClick}
          text="Play Similar Songs"
        />
        <MenuItem
          icon={IconNames.SHARE}
          onClick={this.onCopyYoutubeLink}
          text="Copy YouTube Link"
        />
        <MenuDivider />
        <MenuItem
          icon={IconNames.VIDEO}
          onClick={this.onOpenInYouTube}
          text="Watch on YouTube"
        />
        {isCurrent && this.isConnectedToLastFm ? this.scrobbleMenuItem : null}
      </Menu>
    );
  }

  render() {
    const { onClose, position, targetElement, className } = this.props;

    return (
      <Popover
        className={className}
        content={this.renderMenu()}
        position={position}
        onClose={onClose}
      >
        {targetElement}
      </Popover>
    );
  }
}

TrackMenu.propTypes = {
  targetElement: PropTypes.node.isRequired,
  className: PropTypes.string,
  track: ImmutablePropTypes.map,
  dispatch: PropTypes.func.isRequired,
  position: PropTypes.string,
  appStore: ImmutablePropTypes.map.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  isCurrent: PropTypes.bool,
  onClose: PropTypes.func,
};

TrackMenu.contextTypes = {
  router: PropTypes.object,
  inPlayer: PropTypes.bool,
  notify: PropTypes.func,
};

TrackMenu.defaultProps = {
  className: '',
  track: Map(),
  position: Position.LEFT_TOP,
  isCurrent: false,
  onClose: () => {},
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(TrackMenu);
