import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { EditableText, Dialog, Classes } from '@blueprintjs/core';
import Image from 'lib/Image';
import { Map } from 'immutable';
import { Link } from 'react-router';
import ImmutablePropTypes from 'react-immutable-proptypes';

class CollectionsDialog extends Component {
  constructor() {
    super();
    this.state = {
      isCreatingACollaction: false,
    };
  }

  createCollection = (name = '') => {
    if (name === '') return;
    this.props.onCreateCollection(name, this.props.track);
    this.setState({ isCreatingACollaction: false });
  }

  startCreatingCollection = () => {
    this.setState({ isCreatingACollaction: true });
  }

  cancelCreate = () => {
    this.setState({ isCreatingACollaction: false });
  }

  actionIcon(collection) {
    const { onRemoveTrackClick, onAddTrackClick, track } = this.props;

    if (!track) return null;

    if (collection.getIn(['tracks', track.get('id')])) {
      return (
        <i
          className="material-icons add-icon added"
          onClick={onRemoveTrackClick.bind(this, collection)}
          role="none"
        >
          check_circle
        </i>
      );
    }

    return (
      <i
        className="material-icons add-icon blank"
        onClick={onAddTrackClick.bind(this, collection)}
        role="none"
      >
        add_circle_outline
      </i>
    );
  }

  renderCollections() {
    const { collections } = this.props;
    const all = [];
    collections.valueSeq().forEach((collection, i) => {
      if (!collection.get('custom') || collection.get('id') === 'likes') return;
      // eslint-disable-next-line function-paren-newline
      all.push(
        <div key={i} className="collection">
          <div className="name">
            <Link to={`/preview/collection/${collection.get('id')}`}>
              { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
              {collection.get('name')} ({collection.get('tracks').size})
            </Link>
          </div>
          <div className="action">
            {this.actionIcon(collection)}
          </div>
        </div>,
      );
    });

    if (!all.length) return 'No Collections';
    return all;
  }

  renderTrackDetails() {
    const { track } = this.props;
    if (!track || !track.size) return null;

    return (
      <div className="track-to-add">
        <div className="title">
          <Image className="avatar" src={track.get('thumbnail')} />
          <div>
            { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
            {track.get('title')} <br />
            <small style={{ fontWeight: '100' }}>
              {track.get('artist')}
            </small>
          </div>
        </div>
      </div>
    );
  }

  renderPlaylistCreationSection() {
    const { isCreatingACollaction } = this.state;
    return (
      <div>
        <EditableText
          className="create-collection-input"
          onEdit={this.startCreatingCollection}
          onConfirm={this.createCollection}
          placeholder="Create a new collection"
          onCancel={this.cancelCreate}
          defaultValue=""
        />

        {isCreatingACollaction ? (
          <i className="collection-add-caption">Press &quot;Enter&quot; to save, &quot;Esc&quot; to cancel</i>
        ) : ''}
      </div>
    );
  }

  render() {
    const { isOpen, onClose } = this.props;
    return (
      <Dialog
        isOpen={isOpen}
        lazy
        title="Collections"
        onClose={onClose}
        canEscapeKeyClose={false}
        className="add-to-collection-dialog"
      >
        <div className={Classes.DIALOG_BODY}>
          {this.renderTrackDetails()}
          <div className="existing-collections">
            {this.renderCollections()}
          </div>
        </div>
        <div className={Classes.DIALOG_FOOTER}>
          {this.renderPlaylistCreationSection()}
        </div>
      </Dialog>
    );
  }
}

CollectionsDialog.propTypes = {
  track: ImmutablePropTypes.map,
  collections: ImmutablePropTypes.map,
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onAddTrackClick: PropTypes.func.isRequired,
  onRemoveTrackClick: PropTypes.func.isRequired,
  onCreateCollection: PropTypes.func.isRequired,
};

CollectionsDialog.defaultProps = {
  collections: Map(),
  track: Map(),
};

export default CollectionsDialog;
