import axios from 'axios';
import Config from 'config';
import qs from 'qs';

const request = (method, { path, data }) => (
  new Promise((resolve, reject) => {
    axios({
      method,
      url: `${Config.headsetApi}/${path}`,
      withCredentials: true,
      headers: {
        'Content-Type': 'application/json',
        'x-api-key': Config.headsetKey,
      },
      data: data || {},
      validateStatus: (status) => status >= 200 && status <= 401,
    }).then((response) => {
      // Success!
      if (response.status === 200) {
        resolve(response);
      } else {
        reject(response);
      }
    });
  })
);

export const post = ({ path, data }) => (
  request('POST', { path, data })
);

export const put = ({ path, data }) => (
  request('PUT', { path, data })
);

export const delete_ = ({ path, data }) => (
  request('DELETE', { path, data })
);

export const get = ({ path, data }) => {
  const fullPath = `${path}?${qs.stringify(data)}`;
  return request('GET', { path: fullPath });
};
