import { fromJS } from 'immutable';
import { notify } from 'actions/appActions';
import { random } from 'lib/utils';
import { SKIP_THRESHOLD } from 'lib/constants';
import actionTypes from 'actions/actionTypes';
import Subscription from 'lib/Subscription';

const shouldPlayAd = (skipCount, repeatMode) => (
  !Subscription.isPro()
    && skipCount >= SKIP_THRESHOLD
    && repeatMode !== 'one'
);

export const playPrevious = () => ({ type: actionTypes.PLAY_PREVIOUS });

export const loadQueue = (queue, source) => (
  (dispatch) => {
    dispatch({ type: actionTypes.LOAD_QUEUE, payload: { queue, source } });
    dispatch({ type: actionTypes.RESTORE_SCROBBLE });
  }
);

export const playSilentAd = () => ({ type: actionTypes.PLAY_SILENT_AD });

export const loadTrack = (track) => (dispatch) => {
  dispatch({ type: actionTypes.LOAD_TRACK, payload: track });
  dispatch({ type: actionTypes.INCREMENT_SKIP_COUNT });
};

export const playNext = (isError) => (dispatch, getState) => {
  /*
    When user jumps between tracks using the queue (i.e not the next button),
    it will not stop for ad due to code complexity/bug.
    if skipCount goes beyond SKIP THRESHOLD, it will show ad the next time skipNext runs
  */
  const skipCount = getState().playerReducer.get('skipCount');
  const repeatMode = getState().playerReducer.get('repeatMode');

  if (shouldPlayAd(skipCount, repeatMode)) {
    ga('send', 'event', 'Ad is playing');
    dispatch(playSilentAd());
  } else {
    dispatch({ type: actionTypes.PLAY_NEXT });
    if (!isError) dispatch({ type: actionTypes.INCREMENT_SKIP_COUNT });
  }
};

export const pause = () => ({ type: actionTypes.PAUSE });

export const play = () => ({ type: actionTypes.PLAY });

export const ytPlay = () => ({ type: actionTypes.YT_PLAY });

export const ytPause = () => ({ type: actionTypes.YT_PAUSE });

export const showPlayer = () => ({ type: actionTypes.SHOW_PLAYER });

export const hidePlayer = () => ({ type: actionTypes.HIDE_PLAYER });

export const timeChanged = (currentTime) => ({ type: actionTypes.TIME_CHANGED, payload: currentTime });

export const setCurrentRadioStation = (stationKey) => ({
  type: actionTypes.SET_CURRENT_RADIO_STATION, payload: stationKey,
});

export const setRelatedStation = (name) => ({ type: actionTypes.SET_RELATED_STATION, payload: name });

export const setCurrentCollection = (collection) => ({ type: actionTypes.SET_CURRENT_COLLECTION, payload: collection });

export const setCurrentPlaylist = (playlist) => ({ type: actionTypes.SET_CURRENT_PLAYLIST, payload: playlist });

export const resetProgress = () => ({ type: actionTypes.RESET_PROGRESS });

export const shuffleOn = () => ({ type: actionTypes.SHUFFLE_ON });

export const shuffleOff = () => ({ type: actionTypes.SHUFFLE_OFF });

export const resetQueueCurrentIndex = () => ({ type: actionTypes.RESET_QUEUE_CURRENT_INDEX });

export const setCurrentIndexFromTrack = (track) => ({ type: actionTypes.SET_CURRENT_INDEX_FROM_TRACK, payload: track });

export const appendToQueue = (tracks) => ({ type: actionTypes.APPEND_TO_QUEUE, payload: fromJS(tracks) });

export const startStationFromTrack = (track, queue) => (dispatch) => {
  const title = track.get('title');
  dispatch(setRelatedStation(title));
  dispatch(loadQueue(queue, 'related'));
  dispatch(notify(`Queue updated with tracks similar to ${title}`));
};

export const queueReorder = (reorderedTracks) => (dispatch) => {
  dispatch({ type: actionTypes.QUEUE_REORDER, payload: { queue: fromJS(reorderedTracks) } });
  dispatch(resetQueueCurrentIndex());
};

export const setRepeatMode = (repeatMode) => ({
  type: actionTypes.SET_REPEAT_MODE, payload: repeatMode,
});

export const toggleRepeatMode = () => (dispatch, getState) => {
  const state = getState().playerReducer;
  const tracksOrder = state.getIn(['queue', 'tracksOrder']);

  if (!tracksOrder.size) {
    return;
  }

  const currentRepeatMode = state.get('repeatMode');
  let nextRepeatMode;
  switch (currentRepeatMode) {
    case null:
      nextRepeatMode = 'all';
      break;
    case 'all':
      nextRepeatMode = 'one';
      break;
    case 'one':
      // circular
      nextRepeatMode = null;
      break;
    default:
      nextRepeatMode = null;
  }

  dispatch(setRepeatMode(nextRepeatMode));
};

export const shufflePreview = (previewable, tracks, source) => (
  (dispatch) => {
    const randomTrack = tracks.get(random(0, tracks.size - 1));
    dispatch(loadQueue(tracks, source));
    if (source === 'collection') {
      dispatch(setCurrentCollection(previewable));
    } else {
      dispatch(setCurrentPlaylist(previewable));
    }
    dispatch(setCurrentIndexFromTrack(randomTrack));

    dispatch(loadTrack(randomTrack));
    dispatch(shuffleOn());
    dispatch(showPlayer());
  }
);

