import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CollectionsDialog } from 'components';
import ImmutablePropTypes from 'react-immutable-proptypes';
import {
  toggleCollectionsDialog,
  addTrackToCollection,
  removeTrackFromCollection,
  createCollection,
} from 'actions/collectionsActions';
import {
  notify,
} from 'actions/appActions';

class AddToCollection extends Component {
  onAddTrack = (collection) => {
    this.props.dispatch(addTrackToCollection(this.trackToAdd, collection));
    this.props.dispatch(notify(`Track Added to ${collection.get('name')}`));
  }

  onRemoveTrack = (collection) => {
    this.props.dispatch(removeTrackFromCollection(this.trackToAdd, collection));
    this.props.dispatch(notify(`Track Removed from ${collection.get('name')}`));
  }

  onCreateCollection = (name, track) => {
    this.props.dispatch(createCollection({ name, tracks: [track] }));
    this.props.dispatch(notify(`Collection ${name}' Created`));
  }

  get trackToAdd() {
    const { collectionsStore } = this.props;
    return collectionsStore.getIn(['addDialog', 'trackToAdd']);
  }

  isDialogOpen = () => {
    const { collectionsStore, appStore } = this.props;

    return collectionsStore.getIn(['addDialog', 'isOpen'])
      && appStore.get('isDialogOpen') && !!this.trackToAdd;
  }

  render() {
    const { collectionsStore, dispatch } = this.props;

    return (
      <CollectionsDialog
        isOpen={this.isDialogOpen()}
        track={this.trackToAdd}
        collections={collectionsStore.get('collections')}
        onClose={dispatch.bind(this, toggleCollectionsDialog(null))}
        onAddTrackClick={this.onAddTrack}
        onRemoveTrackClick={this.onRemoveTrack}
        onCreateCollection={this.onCreateCollection}
      />
    );
  }
}

AddToCollection.propTypes = {
  dispatch: PropTypes.func.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(AddToCollection);
