import { fromJS } from 'immutable';
import {
  autocomplete,
  search,
  fetchRelated,
} from 'lib/youtube';

import actionTypes from './actionTypes';

export const fetchAutocompletes = (q) => {
  // This will close the autocompletes
  if (q === '') {
    return { type: actionTypes.RECEIVED_AUTOCOMPLETES, payload: [] };
  }

  return (dispatch) => {
    autocomplete(q).then((results) => {
      dispatch({ type: actionTypes.RECEIVED_AUTOCOMPLETES, payload: results });
    });
  };
};

export const fetchSearchResults = (q) => (dispatch) => {
  dispatch({ type: actionTypes.GETTING_SEARCH_RESULTS, payload: q });
  search(q).then(({ results, nextPageToken }) => {
    dispatch({ type: actionTypes.RECEIVED_SEARCH_RESULTS, payload: fromJS(results) });
    dispatch({ type: actionTypes.SET_NEXT_SEARCH_PAGE_TOKEN, payload: nextPageToken });
  });
};

export const fetchTrackRelated = (track) => (
  (dispatch) => (
    fetchRelated(track).then(({ tracks, nextPageToken }) => {
      dispatch({ type: actionTypes.SET_NEXT_RELATED_PAGE_TOKEN, payload: nextPageToken });
      return tracks;
    })
  )
);

export const stepAutocomplete = (step) => ({
  type: actionTypes.STEP_AUTOCOMPLETE, payload: step,
});

export const clearLoadingMore = () => ({
  type: actionTypes.MORE_LOADED,
});

export const loadMoreSearchResults = (q, pageToken) => (
  (dispatch) => {
    dispatch({ type: actionTypes.LOAD_MORE });
    search(q, pageToken).then(({ results, nextPageToken }) => {
      dispatch({ type: actionTypes.APPEND_SEARCH_RESULTS, payload: fromJS(results) });
      dispatch({ type: actionTypes.SET_NEXT_SEARCH_PAGE_TOKEN, payload: nextPageToken });
      dispatch(clearLoadingMore());
    });
  }
);

export const loadMoreRelated = (track, pageToken) => (
  (dispatch) => {
    dispatch({ type: actionTypes.LOAD_MORE });
    return fetchRelated(track, pageToken).then(({ tracks, nextPageToken }) => {
      dispatch({ type: actionTypes.SET_NEXT_RELATED_PAGE_TOKEN, payload: nextPageToken });
      dispatch(clearLoadingMore());

      return tracks;
    });
  }
);

export const updateSearchBarValue = (value) => ({ type: actionTypes.UPDATE_SEARCH_BAR, payload: value });

export const updateSearchTerm = (value) => ({
  type: actionTypes.UPDATE_SEARCH_TERM, payload: value,
});
