import PropTypes from 'prop-types';
import React from 'react';
import { TrackList } from 'components';
import { connect } from 'react-redux';
import { fromJS } from 'immutable';
import pluralize from 'pluralize';

import { NoTracks } from 'components/BlankStates';
import ImmutablePropTypes from 'react-immutable-proptypes';
import Loader from 'static/images/loader.svg';

import {
  createChannelPreview,
  createPlaylistPreview,
} from 'actions/previewActions';

import {
  deleteCollection,
} from 'actions/collectionsActions';

import PreviewInterface from './PreviewInterface';

import PreviewHeader from './PreviewHeader';

class PlaylistPreview extends PreviewInterface {
  componentDidMount() {
    const { dispatch, id, kind } = this.props;

    if (this.playlist) return;
    switch (kind) {
      case 'playlist':
        dispatch(createPlaylistPreview(id));
        break;
      case 'channel':
        dispatch(createChannelPreview(id));
        break;
      default:
        break;
    }
  }

  get header() {
    if (!this.playlist) return null;
    return (
      <PreviewHeader
        previewable={this.playlist}
        image={this.image}
        subtitle={pluralize('Track', Number(this.count), true)}
        title={this.title}
        onShufflePlay={this.onShufflePlay}
      />
    );
  }

  get playlistNotFound() {
    return (
      <div className="page-loader">
        <p>Playlist has been deleted by the creator, sorry.</p>
        <a
          href="#"
          onClick={(e) => {
            e.preventDefault();
            this.props.dispatch(deleteCollection(fromJS({ id: this.props.id })));
            this.context.router.goBack();
          }}
          className="delete-collection"
        >
          Delete from your playlists
        </a>
      </div>
    );
  }

  get body() {
    if (this.playlist === null) {
      return this.playlistNotFound;
    }
    if (this.playlist === undefined) {
      return (
        <div className="page-loader">
          <img alt="" src={Loader} />
          <h2>Loading</h2>
        </div>
      );
    }

    if (!this.tracks.size) {
      return (
        <NoTracks onDelete={this.onDeleteFromEmpty} isLikes={this.isLikes} />
      );
    }

    return ([
      <div key={1} className="list-preview">Tracks</div>,
      <TrackList
        key={2}
        collections={this.collections}
        onScroll={this.onScroll}
        tracks={this.tracks}
        onItemSelect={this.onItemSelect}
        currentTrack={this.currentTrack}
        sortable={false}
        onLoadMoreClick={this.onLoadMore}
        isLoadingMore={this.state.isLoadingMore}
        needLoadMore={this.isThereMoreToLoad}
      />,
    ]
    );
  }
}

PlaylistPreview.propTypes = {
  dispatch: PropTypes.func.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  previewStore: ImmutablePropTypes.map.isRequired,
  id: PropTypes.string,
  kind: PropTypes.string,
};

PlaylistPreview.defaultProps = {
  id: '',
  kind: 'playlist',
};

PlaylistPreview.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  previewStore: state.previewReducer,
  collectionsStore: state.collectionsReducer,
  playerStore: state.playerReducer,
  searchStore: state.searchReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(PlaylistPreview);
