import PropTypes from 'prop-types';
import React from 'react';
import classnames from 'classnames';

const Autocompletes = (props) => {
  const { results, active, onMouseMove, onClick } = props;

  if (!results || !results.length) {
    return null;
  }

  return (
    <div className="autocomplete-list">
      <ul>
        {results.map((item, i) => (
          <li
            onClick={onClick.bind(this, item)}
            key={i}
            className={classnames({ active: active === i })}
            onMouseMove={onMouseMove.bind(this, i)}
          >
            {item}
          </li>
        ))}
      </ul>
    </div>
  );
};

Autocompletes.propTypes = {
  results: PropTypes.arrayOf(PropTypes.string),
  active: PropTypes.node,
  onMouseMove: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
};

Autocompletes.defaultProps = {
  results: [],
  active: null,
};

export default Autocompletes;
