import axios from 'axios';
import urldecode from 'urldecode';
import fetchJsonp from 'fetch-jsonp';
import shuffle from 'shuffle-array';
import { getArtistTitle } from 'lib/utils';
import YoutTubeAPICache from 'lib/YoutTubeAPICache';

const MAX_RESULTS = 40;
const BASE_URL = 'https://www.googleapis.com/youtube/v3';
const AUTO_COMPLETE_URL = 'https://suggestqueries.google.com/complete/search?hl=en&ds=yt&client=youtube&q=';
const SEARCH_URL = `${BASE_URL}/search`; // 100
const VIDEOS_URL = `${BASE_URL}/videos`; // (1 + 2 + 2)*20 = 100
const CHANNELS_URL = `${BASE_URL}/channels`;
const PLAYLISTS_URL = `${BASE_URL}/playlists`; // (1 + 2 + 2)*20 = 100
const PLAYLIST_ITEMS_URL = `${BASE_URL}/playlistItems`; // (1 + 2 + 2)*20 = 100
const CHANNEL_SECTIONS_URL = `${BASE_URL}/channelSections`; // (1 + 2 + 2)*20 = 100

const ytk = () => localStorage.getItem('youtube-api-key');

export const getDuration = (ytDuration) => {
  let n = ytDuration.match(/\d+/g);
  const hasSeconds = ytDuration.indexOf('S') !== -1;
  const hasMinutes = ytDuration.indexOf('M') !== -1;
  const hasHours = ytDuration.indexOf('H') !== -1;

  if (hasHours && hasMinutes && hasSeconds) {
    n = [n[0], n[1], n[2]];
  }

  if (!hasHours && hasMinutes && !hasSeconds) {
    n = [0, n[0], 0];
  }

  if (hasHours && !hasMinutes) {
    n = [n[0], 0, n[1]];
  }

  if (hasHours && !hasMinutes && !hasSeconds) {
    n = [n[0], 0, 0];
  }

  if (hasHours && hasMinutes && !hasSeconds) {
    n = [n[0], n[1], 0];
  }

  let duration = 0;

  if (n.length === 3) {
    duration += parseInt(n[0], 10) * 3600;
    duration += parseInt(n[1], 10) * 60;
    duration += parseInt(n[2], 10);
  }

  if (n.length === 2) {
    duration += parseInt(n[0], 10) * 60;
    duration += parseInt(n[1], 10);
  }

  if (n.length === 1) {
    duration += parseInt(n[0], 10);
  }

  return duration;
};

export const formatDuration = (duration) => {
  const date = new Date(null);
  let formattedDuration;

  date.setSeconds(duration);
  // one hour
  if (duration < 600) { // < 10 minutes
    // 0:00
    formattedDuration = date.toISOString().substr(15, 4);
  } else if (duration < 3600) { // < 1 hour
    // 00:00
    formattedDuration = date.toISOString().substr(14, 5);
  } else if (duration < 36000) { // < 10 hours
    // 0:00:00
    formattedDuration = date.toISOString().substr(12, 7);
  } else {
    // 00:00:00
    try {
      formattedDuration = date.toISOString().substr(11, 8);
    } catch (err) {
      // swallow
    }
  }

  return formattedDuration;
};

const extractAlbumTracklist = (description) => {
  const trackListLReg = /.*\d{1,2}:\d{1,2}(:\d{1,2})?.*/g;
  const trackList = description.match(trackListLReg);

  // not convinced that album has one track. probably malfunction description
  if (!trackList || trackList.length === 1) return null;

  return trackList;
};

export const cleanTitle = (t) => t.replace(/-?\s$|^-/g, '').replace("()", "");

/*
  e.g
  track: {
    artist: 'Relaxing Music Mix',
    duration: 4028,
    durationClockFormat: '1:07:08',
    id: 'NliYy7iqh-U',
    image: 'https://i.ytimg.com/vi/NliYy7iqh-U/maxresdefault.jpg',
    thumbnail: 'https://i.ytimg.com/vi/NliYy7iqh-U/mqdefault.jpg',
    title: 'Beautiful Piano',
    type: 'video',
  }
*/
export const parseVideosResults = (items) => (
  items.reduce((obj, item) => {
    const { id, contentDetails, snippet: { description, thumbnails, title } } = item;

    if (!contentDetails) return obj;

    const artistTitle = getArtistTitle(cleanTitle(title));

    const duration = getDuration(contentDetails.duration);

    obj[id] = {
      id,
      thumbnail: (thumbnails.medium || thumbnails.default).url,
      image: (thumbnails.maxres || thumbnails.standard || thumbnails.high).url,
      artist: artistTitle[0] || '',
      title: cleanTitle(artistTitle[1]),
      duration,
      durationClockFormat: formatDuration(duration),
      type: 'video',
      albumTrackList: extractAlbumTracklist(description),
      pin: null,
    };

    return obj;
  }, {})
);
/*
  e.g
  playlist: {
    artist: "Playlist"
    id: "PLFgquLnL59ak1QNHmrUSjNM6WTegpgX__"
    image: "https://i.ytimg.com/vi/YQHsXMglC9A/maxresdefault.jpg"
    itemCount: 200
    thumbnail: "https://i.ytimg.com/vi/YQHsXMglC9A/mqdefault.jpg"
    title: "Trending Music Videos"
    type: "playlist"
  }
*/

export const parsePlaylistsResults = (items) => (
  items.reduce((obj, item) => {
    const { id, contentDetails: { itemCount }, snippet: { thumbnails, title } } = item;

    obj[id] = {
      id,
      thumbnail: (thumbnails.medium || thumbnails.default).url,
      image: (thumbnails.maxres || thumbnails.standard || thumbnails.high).url,
      title: cleanTitle(title),
      itemCount,
      type: 'playlist',
    };

    return obj;
  }, {})
);

export const parseChannelResults = (items = []) => (
  items.reduce((obj, item) => {
    const {
      id,
      snippet: {
        customUrl, thumbnails, title, description,
      },
      statistics: { videoCount },
      contentDetails: {
        relatedPlaylists: {
          uploads,
        },
      },
    } = item;

    obj[id] = {
      id,
      thumbnail: (thumbnails.medium).url,
      image: (thumbnails.high).url,
      description: description.replace(/(?:\r\n|\r|\n)/g, '<br />'),
      title,
      type: 'channel',
      customUrl,
      itemCount: videoCount,
      playlistId: uploads,
    };

    return obj;
  }, {})
);

// search url doesn't give enough details
// So need to do another call to videos endpoint with contentDetails
export const fetchVideos = (ids = []) => {
  if (!ids) throw new Error('no videos');

  const missingIds = YoutTubeAPICache.missingIds(ids);
  if (!missingIds.length) {
    return Promise.resolve(YoutTubeAPICache.getBulk(ids));
  }

  return axios(VIDEOS_URL, {
    params: {
      key: ytk(),
      part: 'snippet,contentDetails',
      fields: 'items(id,snippet(title, thumbnails, description),contentDetails(duration))',
      id: missingIds.join(','),
    },
  }).then((response) => {
    const { items } = response.data;

    if (items.length > 0) {
      const parsed = parseVideosResults(items);
      YoutTubeAPICache.setBulk(parsed);
    }

    return YoutTubeAPICache.getBulk(ids);
  });
};

export const fetchChannels = (ids = []) => {
  const missingIds = YoutTubeAPICache.missingIds(ids);
  if (!missingIds.length) {
    return Promise.resolve(YoutTubeAPICache.getBulk(ids));
  }

  return axios(CHANNELS_URL, {
    params: {
      key: ytk(),
      part: 'snippet,contentDetails,statistics',
      id: missingIds.join(','),
    },
  }).then((response) => {
    const { items } = response.data;

    if (items.length > 0) {
      const parsed = parseChannelResults(items);
      YoutTubeAPICache.setBulk(parsed);
    }

    return YoutTubeAPICache.getBulk(ids);
  });
};

export const fetchPlaylists = (ids = []) => {
  const missingIds = YoutTubeAPICache.missingIds(ids);
  if (!missingIds.length) {
    return Promise.resolve(YoutTubeAPICache.getBulk(ids));
  }

  return axios(PLAYLISTS_URL, {
    params: {
      key: ytk(),
      part: 'snippet,contentDetails',
      fields: 'items(id,snippet(title, thumbnails),contentDetails(itemCount))',
      id: missingIds.join(','),
    },
  }).then((response) => {
    const { items } = response.data;
    const parsed = parsePlaylistsResults(items);
    YoutTubeAPICache.setBulk(parsed);
    return YoutTubeAPICache.getBulk(ids);
  });
};

const idsFromResponse = (response) => {
  const { items } = response.data;
  if (!items.length) return null;

  return items
    .map((r) => (r.snippet ? r.snippet.resourceId.videoId : r.id.videoId))
    .filter((id) => !!id);
};

export const fetchPlaylistItems = (playlistId, pageToken, options = {}) => (
  axios(PLAYLIST_ITEMS_URL, {
    params: {
      key: ytk(),
      part: 'snippet,contentDetails',
      playlistId,
      maxResults: options.count || MAX_RESULTS,
      pageToken,
    },
  }).then((response) => {
    const ids = idsFromResponse(response);
    const { nextPageToken } = response.data;
    return fetchVideos(ids).then((tracks) => ({ tracks, nextPageToken }));
  })
);

export const fetchChannelSections = (channelId, pageToken, options = {}) => (
  axios(CHANNEL_SECTIONS_URL, {
    params: {
      key: ytk(),
      part: 'snippet,contentDetails',
      channelId,
      maxResults: options.count || MAX_RESULTS,
      pageToken,
    },
  }).then((response) => {
    const ids = idsFromResponse(response);
    const { nextPageToken } = response.data;
    return fetchVideos(ids).then((tracks) => ({ tracks, nextPageToken }));
  })
);

const parseSearchResponse = (response) => {
  const order = {};
  const playlistIds = [];
  const videoIds = [];
  const channelIds = [];
  const { nextPageToken } = response.data;

  response.data.items.forEach((item, i) => {
    let id;

    switch (item.id.kind) {
      case 'youtube#playlist':
        id = item.id.playlistId;
        playlistIds.push(id);
        break;
      case 'youtube#video':
        id = item.id.videoId;
        videoIds.push(id);
        break;
      case 'youtube#channel':
        id = item.id.channelId;
        // this is an auto generated playlist from youtube. ignore it since it doesn't have uploads
        if (id !== 'UC-9-kyTW8ZkZNDHQJ6FgpwQ') {
          channelIds.push(id);
        }
        break;
      default:
        break;
    }
    order[id] = i;
  });

  return {
    order, playlistIds, videoIds, nextPageToken, channelIds,
  };
};

export const search = (q, pageToken = '') => (
  axios(SEARCH_URL, {
    params: {
      q,
      maxResults: MAX_RESULTS,
      key: ytk(),
      part: 'snippet',
      fields: 'items/id,nextPageToken',
      pageToken,
    },
  }).then((response) => {
    const {
      order, playlistIds, videoIds, nextPageToken, channelIds,
    } = parseSearchResponse(response);
    return Promise.all([
      fetchChannels(channelIds),
      fetchPlaylists(playlistIds),
      fetchVideos(videoIds),
    ]).then((allItems) => {
      const all = [...allItems[0], ...allItems[1], ...allItems[2]];
      let results = [];
      all.forEach((item) => {
        results[order[item.id]] = item;
      });
      results = results.filter((i) => i !== undefined);

      // mix of playlists and videos in the same order they came from youtube
      return { results, nextPageToken };
    });
  }).catch()
);

export const autocomplete = (q) => (
  fetchJsonp(AUTO_COMPLETE_URL + q)
    .then((response) => response.json())
    .then((results) => results[1].map((r) => r[0]))
);

export const fetchRelated = (track, pageToken) => (
  axios(SEARCH_URL, {
    params: {
      relatedToVideoId: track.get('id'),
      maxResults: MAX_RESULTS,
      type: 'video',
      key: ytk(),
      part: 'snippet',
      fields: 'items/id, nextPageToken',
      pageToken,
    },
  }).then((response) => {
    const ids = idsFromResponse(response);
    const { nextPageToken } = response.data;

    return fetchVideos(ids).then((tracks) => ({ tracks: shuffle(tracks), nextPageToken }));
  })
);

export const idResolver = (url) => {
  const match = urldecode(url).match(/\?v=(.{11})|youtu.be\/(.{11})/);
  if (!match) return null;

  //     youtube  || youtu.be
  return match[1] || match[2];
};

export const playerStates = {
  UNSTARTED: -1,
  ENDED: 0,
  PLAYING: 1,
  PAUSED: 2,
  BUFFERING: 3,
  VIDEO_CUED: 5,
};
