import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import ImmutablePropTypes from 'react-immutable-proptypes';
import classnames from 'classnames';
import { DIALOGS } from 'lib/constants';
import { Intent } from '@blueprintjs/core';

import {
  storeUser,
  notify,
  openDialog,
  closeDialog,
} from 'actions/appActions';

import {
  UserForm,
  PasswordResetDialog,
} from 'components';

import WelcomeImage from 'static/images/welcome-image.png';

import packageJson from '../../../package.json';

class Welcome extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLogin: true,
    };
  }

  onLogin = (user) => {
    this.props.dispatch(storeUser(user));
    this.context.router.push('/');
    this.props.dispatch(notify(`Hi ${user.get('firstName')}, welcome back`));
  }

  onSignup = (user) => {
    this.props.dispatch(storeUser(user));
    this.context.router.push('/');
    this.props.dispatch(notify(
      `Hello ${user.get('firstName')}! Welcome to Headset!`,
      { intent: Intent.PRIMARY },
    ));
  }

  onFormToggle = () => {
    this.setState((prevState) => ({ isLogin: !prevState.isLogin }));
  }

  onPasswordResetClose = () => {
    this.props.dispatch(closeDialog(DIALOGS.PASSWORD_RESET));
  }

  onPasswordChange = () => {
    ga('send', 'event', 'Forgot Password change');
    this.props.dispatch(notify('Password updated successfully', { intent: Intent.SUCCESS }));
  }

  onForgotPasswordClick = () => {
    ga('send', 'event', 'Forgot Password click');
    this.props.dispatch(openDialog(DIALOGS.PASSWORD_RESET));
  }

  // eslint-disable-next-line class-methods-use-this
  get versionPack() {
    return (
      <div className="package-json">
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          Core: {packageJson.version}, wrapper: {localStorage.electronVersion}
        </p>
      </div>
    );
  }

  render() {
    const { appStore } = this.props;
    const { isLogin } = this.state;
    const passwordResetDialog = appStore.get(DIALOGS.PASSWORD_RESET);

    return (
      <div>
        <div className={classnames('welcome-form', { 'is-signup': !isLogin })}>
          <UserForm
            isLogin={isLogin}
            onFormChange={this.onFormToggle}
            onLogin={this.onLogin}
            onSignup={this.onSignup}
            onForgotPasswordClick={this.onForgotPasswordClick}
          />
          <PasswordResetDialog
            isOpen={passwordResetDialog.get('open')}
            onClose={this.onPasswordResetClose}
            onLogin={this.onLogin}
            onSuccess={this.onPasswordChange}
          />
        </div>
        <img src={WelcomeImage} className="login-image" alt="" />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  appStore: state.appReducer,
});

Welcome.propTypes = {
  dispatch: PropTypes.func.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
};

Welcome.contextTypes = {
  router: PropTypes.object,
};

export default connect(mapStateToProps)(Welcome);
