import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { connect } from 'react-redux';
import { Button, Dialog } from '@blueprintjs/core';
import { DIALOGS } from 'lib/constants';
import { ExternalLink } from 'components';
import { closeDialog } from 'actions/appActions';

class MoreSkipsDialog extends Component {
  onClickOnCancel = (e) => {
    e.preventDefault();
    this.closeDialog();
  }

  closeDialog = () => {
    ga('send', 'event', 'subscription', 'Click on maybe later from upgrade dialog');
    this.props.dispatch(closeDialog(DIALOGS.MORE_SKIPS));
  }

  render() {
    const { appStore } = this.props;
    const isOpen = appStore.getIn([DIALOGS.MORE_SKIPS, 'open']);

    return (
      <Dialog
        inline
        lazy
        canEscapeKeyClose
        onClose={this.closeDialog}
        isOpen={isOpen}
        className="upgrade-dialog"
      >
        <h3>Want more skips?</h3>
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          Get unlimited skips with <span className="pro-badge">Pro</span>!
        </p>
        <p>
          <ExternalLink href="https://headsetapp.co/#pricing" onClick={this.onClickOnCancel}>
            Click here to learn about our pricing
          </ExternalLink>
        </p>
        <Button
          className="button"
          onClick={this.closeDialog}
          text="Got it"
        />
      </Dialog>
    );
  }
}

MoreSkipsDialog.contextTypes = {
  theme: PropTypes.string,
  user: PropTypes.object,
  router: PropTypes.object,
};

MoreSkipsDialog.propTypes = {
  dispatch: PropTypes.func.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
};

const mapStateToProps = (state) => ({ appStore: state.appReducer });

export default connect(mapStateToProps)(MoreSkipsDialog);
