import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { Link } from 'react-router';

class NoTracks extends PureComponent {
  onDelete = (e) => {
    e.preventDefault();
    this.props.onDelete();
  }

  get deleteSection() {
    return (
      <span>
        <br />
        <a href="" onClick={this.onDelete} className="delete-collection">
          Delete
        </a>
      </span>
    );
  }

  render() {
    const { isLikes, onDelete } = this.props;
    return (
      <div className="no-tracks">
        <p className="meh">
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <span className="cd">{isLikes ? '❤️' : '💿'}</span>¯\_(ツ)_/¯
        </p>
        <h3>Save some tunes!</h3>
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <Link to="/">Search</Link> for new songs or check out a <Link to="radio">Radio</Link> station
          {onDelete && !isLikes ? this.deleteSection : null}
        </p>
      </div>
    );
  }
}

NoTracks.propTypes = {
  onDelete: PropTypes.func,
  isLikes: PropTypes.bool,
};

NoTracks.defaultProps = {
  onDelete: () => {},
  isLikes: false,
};

export default NoTracks;
