import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { TrackList, Page } from 'components';
import { connect } from 'react-redux';
import { playerStates } from 'lib/youtube';
import { Dialog } from '@blueprintjs/core';
import { Welcome } from 'containers';
import { NoTracks } from 'components/BlankStates';
import Config from 'config';
import ImmutablePropTypes from 'react-immutable-proptypes';
import pluralize from 'pluralize';
import {
  deleteCollection,
  renameCollection,
  collectionReorder,
} from 'actions/collectionsActions';

import {
  play,
  pause,
  loadTrack,
  loadQueue,
  setCurrentCollection,
  setCurrentIndexFromTrack,
  shufflePreview,
  showPlayer,
} from 'actions/playerActions';

import PreviewHeader from './PreviewHeader';

// TODO: inherit from PreviewInterface
class CollectionPreview extends PureComponent {
  constructor() {
    super();
    this.state = {
      editDialogOpen: false,
      filterValue: '',
      filteredTracks: null,
    };
  }

  onShufflePlay = () => {
    ga('send', 'event', 'Shuffle Preview', this.collection.get('name'));
    this.props.dispatch(shufflePreview(this.collection, this.sortedTracks, 'collection'));
  }

  onItemSelect = (track) => {
    ga('send', 'event', 'Select Track from CollectionPreview');
    const { dispatch } = this.props;

    if (this.currentTrack.get('id') !== track.get('id')) {
      if (!this.collectionInQueue || this.collection.get('id') !== this.collectionInQueue.get('id')) {
        dispatch(loadQueue(this.sortedTracks, 'collection'));
      }

      dispatch(setCurrentCollection(this.collection));
      dispatch(loadTrack(track));
      dispatch(setCurrentIndexFromTrack(track));
      dispatch(showPlayer());
    } else if (this.playerState === playerStates.PLAYING) {
      dispatch(pause());
    } else {
      dispatch(play());
    }
  };

  onRenameClick = () => {
    if (this.custom) {
      this.setState({ editDialogOpen: true });
    }
  }

  onCollectionRename = (e) => {
    const { value } = e.target;
    switch (e.which) {
      case 13:
        this.props.dispatch(renameCollection(this.collection, value));
        this.setState({ editDialogOpen: false });
        break;
      case 27:
        this.setState({ editDialogOpen: false });
        break;
      default:
        break;
    }
  }

  onReorder = (tracks) => {
    this.props.dispatch(collectionReorder(this.collection, tracks));
  }

  onUnsave = () => {
    if (this.custom) {
      this.context.router.goBack();
    } else {
      setTimeout(() => {
        this.context.router.push(`/preview/playlist/${this.props.id}`);
      });
    }
  }

  onFilterChange = (e) => {
    const { value } = e.target;
    this.setState({ filterValue: value });
    const re = new RegExp(value, 'ig');

    setTimeout(() => { // settimeout to not block the typing
      const filteredTracks = this.tracks.filter((track) => (
        track.get('title').match(re) || track.get('artist').match(re)
      ));

      if (value === '') {
        this.setState({ filteredTracks: null });
      } else {
        this.setState({ filteredTracks });
      }
    }, 0);
  }

  onDeleteFromEmpty = () => {
    // eslint-disable-next-line no-alert
    if (Config.development || window.confirm('This will delete the collection. Are you sure you want to do that?')) {
      this.props.dispatch(deleteCollection(this.collection));
      this.onUnsave();
    }
  }

  get isLikes() {
    return this.collection.get('id') === 'likes';
  }

  get sortedTracks() {
    const tracks = this.collection.get('tracks');
    const order = this.collection.get('order');
    if (!order) { // backwards compatible
      return tracks.toList();
    }

    return tracks.toList().sortBy((tr) => order.get(tr.get('id')));
  }

  get renameDialog() {
    const { editDialogOpen } = this.state;
    return (
      <Dialog
        isOpen={editDialogOpen}
        lazy
        title="Rename Collection"
        canEscapeKeyClose={false}
        className="rename-collection-dialog"
        onClose={this.closeDialog}
      >
        <div className="edit-text-content">
          <input
            autoFocus
            className="pt-input edit-collection-input"
            defaultValue={this.collection.get('name')}
            onKeyUp={this.onCollectionRename}
          />
          <i className="collection-add-caption">Press &quot;Enter&quot; to save, &quot;Esc&quot; to cancel</i>
        </div>
      </Dialog>
    );
  }

  /* eslint-disable react/destructuring-assignment */
  get collectionInQueue() {
    return this.props.playerStore.get('currentCollection');
  }

  get currentTrack() {
    return this.props.playerStore.get('currentTrack');
  }

  get playerState() {
    return this.props.playerStore.get('playerState');
  }

  get user() {
    return this.props.appStore.get('user');
  }
  /* eslint-enable react/destructuring-assignment */

  get collection() {
    const { collectionsStore, id } = this.props;
    return collectionsStore.getIn(['collections', id]);
  }

  get tracks() {
    return this.collection.get('tracks');
  }

  get count() {
    return this.collection.get('tracks').size;
  }

  get trackList() {
    const { collectionsStore } = this.props;
    const { filteredTracks } = this.state;
    if (!this.tracks.size) {
      return (
        <NoTracks onDelete={this.onDeleteFromEmpty} isLikes={this.isLikes} />
      );
    }
    return (
      [<div key={1} className="list-preview">Tracks</div>,
        <TrackList
          key={2}
          collections={collectionsStore.get('collections')}
          tracks={filteredTracks || this.sortedTracks}
          onItemSelect={this.onItemSelect}
          currentTrack={this.currentTrack}
          source="collections"
          needLoadMore={false}
          loadMore={false}
          onScroll={this.onScroll}
          onReorder={this.onReorder}
          sortable={!filteredTracks}
        />,
      ]
    );
  }

  get custom() {
    return this.collection.get('custom');
  }

  get image() {
    if (this.tracks.size) {
      return this.collection.get('image')
        || this.collection.get('tracks').last().get('image');
    }

    return null;
  }

  get title() {
    const title = this.collection.get('name');
    if (title === 'Likes') {
      return 'Likes';
    }
    return title;
  }

  get header() {
    const { filterValue } = this.state;
    if (!this.collection.get('tracks').size) {
      return <div />;
    }
    return (
      <PreviewHeader
        onFilterChange={this.onFilterChange}
        filterValue={filterValue}
        previewable={this.collection}
        image={this.image}
        onUnsave={this.onUnsave}
        subtitle={pluralize('Track', this.count, true)}
        title={this.title}
        isLikes={this.isLikes}
        onTitleClick={this.onRenameClick}
        hasSaveButton={!this.custom}
        hasBackButton={!this.isLikes}
        renameable={this.custom && !this.isLikes}
        onShufflePlay={this.onShufflePlay}
        defaultSaveText="SAVE"
        activeSaveText="SAVED"
      />
    );
  }

  closeDialog = () => {
    this.setState({ editDialogOpen: false });
  }

  render() {
    if (!this.user) {
      return (
        <Welcome />
      );
    }

    if (!this.collection) return null;

    return (
      <Page
        header={this.header}
      >
        {this.renameDialog}
        {this.trackList}
      </Page>
    );
  }
}

CollectionPreview.propTypes = {
  dispatch: PropTypes.func.isRequired,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
  id: PropTypes.string,
};

CollectionPreview.defaultProps = {
  id: '',
};

CollectionPreview.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  playerStore: state.playerReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(CollectionPreview);
