import { Position, Toaster } from '@blueprintjs/core';
import { put } from 'lib/http';

/* eslint-disable import/no-cycle */
import { buildLikesCollection } from 'actions/collectionsActions';
/* eslint-enable import/no-cycle */

import CollectionsSyncer from 'lib/CollectionsSyncer';
import actionTypes from './actionTypes';

let toaster = null;

export const toggleDialog = () => ({
  type: actionTypes.TOGGLE_DIALOG,
});

export const storeUser = (user) => (
  (dispatch) => {
    dispatch({ type: actionTypes.STORE_USER, payload: user.set('collections', null) });

    localStorage.setItem('recoveryEmail', user.get('email'));

    const syncer = new CollectionsSyncer(user.get('collections'));
    syncer.rebuild().then((collections) => {
      dispatch({ type: actionTypes.STORE_COLLECTIONS, payload: collections });
      dispatch({ type: actionTypes.STORE_SAVED_RADIO, payload: user.get('stations') });

      if (!collections.get('likes')) {
        dispatch(buildLikesCollection());
      }
    });
  }
);

export const openDialog = (type) => ({
  type: actionTypes.OPEN_DIALOG, payload: type,
});

export const closeDialog = (type) => ({
  type: actionTypes.CLOSE_DIALOG, payload: type,
});

export const notify = (msg, { intent, position, timeout } = {}) => {
  if (!toaster) {
    toaster = Toaster.create({
      position: position || Position.BOTTOM,
    });
  }

  toaster.show({
    message: msg,
    timeout: timeout || 2500,
    intent,
  });

  return { type: null };
};

export const updateUser = ({ attribute, value }) => (
  (dispatch) => {
    dispatch({ type: actionTypes.UPDATE_USER, payload: { attribute, value } });
    // optimistic
    return put({ path: 'users', data: { [attribute]: value, email: localStorage.recoveryEmail } });
  }
);
