import React, { PureComponent } from 'react';
import { playerStates } from 'lib/youtube';
import { Page } from 'components';
import {
  play,
  pause,
  loadTrack,
  loadQueue,
  appendToQueue,
  setCurrentPlaylist,
  shufflePreview,
  setCurrentIndexFromTrack,
  showPlayer,
} from 'actions/playerActions';

import {
  updatePlaylistPreview,
} from 'actions/previewActions';

// this is a Base class so the prop types are defined by the sub classes
/* eslint-disable react/no-unused-state, react/prop-types */

class PreviewInterface extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingMore: false,
    };
  }

  onItemSelect = (track) => {
    ga('send', 'event', 'Select Track from Playlist', track.get('title'));
    const { dispatch } = this.props;

    if (this.currentTrack.get('id') !== track.get('id')) {
      if (this.shouldReloadQueue()) {
        dispatch(loadQueue(this.tracks, this.props.kind));
      }
      dispatch(setCurrentPlaylist(this.playlist));
      dispatch(loadTrack(track));
      dispatch(setCurrentIndexFromTrack(track));
      dispatch(showPlayer());
    } else if (this.playerState === playerStates.PLAYING) {
      dispatch(pause());
    } else {
      dispatch(play());
    }
  };

  onLoadMore = () => {
    const { dispatch } = this.props;

    this.setState({ isLoadingMore: true });
    dispatch(updatePlaylistPreview(this.playlist)).then((tracks) => {
      this.setState({ isLoadingMore: false });

      if (this.playlist.get('id') === this.playlistInQueue.get('id')) {
        dispatch(appendToQueue(tracks, 'playlist'));
      }
    });
  }

  onShufflePlay = () => {
    ga('send', 'event', 'Shuffle Preview', this.playlist.get('title'));
    const { dispatch } = this.props;
    dispatch(shufflePreview(this.playlist, this.tracks, 'playlist'));
  }

  get playlist() {
    const { previewStore, id } = this.props;

    return previewStore.getIn(['playlists', id]);
  }

  /* eslint-disable react/destructuring-assignment */
  get currentTrack() {
    return this.props.playerStore.get('currentTrack');
  }

  get playlistInQueue() {
    return this.props.playerStore.get('currentPlaylist');
  }

  get playerState() {
    return this.props.playerStore.get('playerState');
  }

  get user() {
    return this.props.appStore.get('user');
  }

  get isThereMoreToLoad() {
    return !!this.props.previewStore.getIn(['playlists', this.playlist.get('id'), 'nextPageToken']);
  }

  get collections() {
    return this.props.collectionsStore.get('collections');
  }

  get following() {
    return this.collections.get(this.props.id);
  }
  /* eslint-enable react/destructuring-assignment */

  get tracks() {
    return this.playlist.get('tracks');
  }

  get image() {
    return this.playlist.get('image');
  }

  get title() {
    return (
      this.playlist.get('title')
    );
  }

  get count() {
    return this.playlist.get('itemCount');
  }

  shouldReloadQueue() {
    return !this.playlistInQueue || this.playlist.get('id') !== this.playlistInQueue.get('id');
  }

  render() {
    return (
      <Page
        header={this.header}
      >
        {this.body}
      </Page>
    );
  }
}

export default PreviewInterface;
