import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { Position } from '@blueprintjs/core';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { fromJS } from 'immutable';
import stationsCategories from 'lib/stationsCategories';
import stations from 'lib/stations';
import { random } from 'lib/utils';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { DIALOGS } from 'lib/constants';
import Loader from 'static/images/loader.svg';
import {
  notify,
  openDialog,
} from 'actions/appActions';

import {
  setCurrentRadioStation,
  loadTrack,
  loadQueue,
  showPlayer,
  setCurrentPlaylist,
} from 'actions/playerActions';

import {
  fetchRadioTracks,
  saveRadioStation,
} from 'actions/radioActions';

import RadioSearch from './RadioSearch';
import Station from './Station';
import Subscription from '../../lib/Subscription';

const stationKeys = Object.keys(stations);

let pageScroll = 0; // intentionally global

const rememberScroll = (e) => {
  pageScroll = e.target.scrollTop;
};

class Radio extends PureComponent {
  constructor() {
    super();
    this.list = null;
    this.state = {
      currentlyLoading: null,
    };
  }

  componentDidMount() {
    this.list.addEventListener('scroll', rememberScroll);
    this.list.scrollTop = pageScroll;
  }

  componentWillUnmount() {
    this.list.removeEventListener('scroll', rememberScroll);
  }

  onRandomStationClick = () => {
    ga('send', 'event', 'Play Random Radio');
    this.setState({ currentlyLoading: 'random' });
    const randomStationKey = stationKeys[random(0, stationKeys.length - 1)];

    this.playRadioStation(randomStationKey);
  }

  onPartyShuffleClick = (e) => {
    e.preventDefault();

    if (!Subscription.isPro()) {
      this.props.dispatch(openDialog(DIALOGS.PRO_DISCOVER));
      return;
    }

    ga('send', 'event', 'Play Party Shuffle');

    const combinedStations = this.savedStations.reduce((current, all) => {
      if (!current) return all;
      all += `+${current}`;
      return all;
    }, '');

    this.onRadioSearchSelect(combinedStations);
  }

  onCategoryMixClick = (e, category) => {
    e.preventDefault();

    if (!Subscription.isPro()) {
      this.props.dispatch(openDialog(DIALOGS.PRO_DISCOVER));
      return;
    }

    ga('send', 'event', 'Play Category Mix');

    const stationKeysForCategory = stationsCategories[category];

    const combinedStations = stationKeysForCategory.reduce((current, all) => {
      if (!current) return all;
      all += `+${current}`;
      return all;
    }, '');

    this.onRadioSearchSelect(combinedStations);
  }

  onSaveClick = (e, stationKey) => {
    const { dispatch } = this.props;
    e.preventDefault();
    let updatedStations;

    if (this.savedStations.has(stationKey)) {
      updatedStations = this.savedStations.delete(stationKey);
    } else {
      updatedStations = this.savedStations.add(stationKey);
    }

    ga('send', 'event', 'click on saved radio', stationKey);

    dispatch(saveRadioStation(updatedStations)).catch(() => {
      notify('Something went wrong! Please try again', { intent: 3 });
    });
  }

  onRadioSearchSelect = (stationKey) => {
    this.context.router.push(`/preview/radio/${stationKey}`);
  }

  /* eslint-disable react/destructuring-assignment */
  get savedStations() {
    return this.props.radioStore.get('savedStations');
  }

  get currentStation() {
    return this.props.playerStore.get('currentStation');
  }
  /* eslint-enable react/destructuring-assignment */

  get randomPick() {
    const { currentlyLoading } = this.state;
    return (
      <div className="">
        <div className="station special" role="none" onClick={this.onRandomStationClick}>
          <div className="station-name">
            { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
            <b>Don&apos;t know what to listen to?</b> Pick a random station 🔮
          </div>
          {currentlyLoading === 'random'
            && (
            <div className="station-loader random">
              <img alt="" src={Loader} />
            </div>
            )}
        </div>
      </div>
    );
  }

  get partyShuffle() {
    return (
      <div className="station party" role="none" onClick={this.onPartyShuffleClick}>
        <div className="station-name">
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <b>Party Shuffle</b> <p>Play a mix of all your favourites in one neat playlist</p>
        </div>
        <div className="station-icon">
          <i role="none" className="material-icons">chevron_right</i>
        </div>
      </div>
    );
  }

  get savedCategory() {
    if (!this.savedStations.size) return null;
    return (
      <div className="station-category saved-category" key="saved">
        <div className="pinned-header">
          <h3>
            Pinned
          </h3>
        </div>
        {this.partyShuffle}
        {this.savedStations.map(this.renderStation)}
      </div>
    );
  }

  get categories() {
    return [
      this.savedCategory,
      ...Object.keys(stationsCategories).map((category) => (
        <div className="station-category" key={category}>
          <div className="category-header">
            <h3>
              {category}
            </h3>
            <a
              href=""
              onClick={(e) => this.onCategoryMixClick(e, category)}
            >
              Mix All
            </a>
          </div>
          {this.renderStationsFor(category)}
        </div>
      ))];
  }

  get searchResults() {
    const { radioStore } = this.props;
    const searchBarValue = radioStore.get('searchBarValue');
    const filterredKeys = radioStore.get('filterredKeys');
    // active if there are no more suggestions from existing stations so the hover is always
    // on the search on reddit.com
    const active = this.currentStation === searchBarValue;
    const selectedSearchIndex = radioStore.get('selectedSearchIndex');
    return (
      <div>
        <Station
          key={searchBarValue}
          stationKey={searchBarValue}
          selected={selectedSearchIndex === -1}
          active={active}
          onSaveClick={(e) => this.onSaveClick(e, searchBarValue)}
          saved={this.isPinned(searchBarValue)}
          className="reddit"
        />
        {filterredKeys.map(this.renderStation)}
      </div>
    );
  }

  playRadioStation = (stationKey) => {
    const { dispatch } = this.props;

    dispatch(setCurrentRadioStation(stationKey));

    dispatch(fetchRadioTracks(stationKey)).then((tracks) => {
      this.setState({ currentlyLoading: null });
      if (!tracks || !tracks.length) {
        notify(`Can't load ${stationKey}`, { position: Position.TOP });
        return;
      }

      const immutableTracks = fromJS(tracks);
      dispatch(setCurrentPlaylist(null));
      dispatch(loadQueue(immutableTracks, 'radio'));
      dispatch(loadTrack(immutableTracks.get(0)));
      dispatch(showPlayer());
    });
  }

  isPinned(key) {
    return this.savedStations.has(key);
  }

  renderStation = (stationKey, i) => {
    const selectedSearchIndex = this.props.radioStore.get('selectedSearchIndex');

    return (
      <Station
        key={stationKey}
        stationKey={stationKey}
        selected={selectedSearchIndex === i}
        active={stationKey === this.currentStation}
        onSaveClick={(e) => this.onSaveClick(e, stationKey)}
        currentlyLoading={stationKey === this.state.currentlyLoading}
        saved={this.isPinned(stationKey)}
      />
    );
  }

  renderStationsFor(category) {
    const stationKeysForCategory = stationsCategories[category];
    return stationKeysForCategory.map(this.renderStation);
  }

  render() {
    const { playerStore, radioStore } = this.props;
    const currentTrack = playerStore.get('currentTrack');
    const searchBarValue = radioStore.get('searchBarValue');
    const withPlayer = currentTrack && !currentTrack.isEmpty();

    return (
      <div>
        <RadioSearch onSelect={this.onRadioSearchSelect} />
        <div
          ref={(l) => { this.list = l; }}
          className={classnames('stations', { 'with-player': withPlayer })}
        >
          <div className="station-category">
            {searchBarValue ? null : this.randomPick}
          </div>
          {searchBarValue ? this.searchResults : this.categories}
        </div>
      </div>
    );
  }
}

Radio.propTypes = {
  dispatch: PropTypes.func.isRequired,
  radioStore: ImmutablePropTypes.map.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
};

Radio.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  playerStore: state.playerReducer,
  radioStore: state.radioReducer,
});

export default connect(mapStateToProps)(Radio);
