import 'lib/ga';

import React, { PureComponent } from 'react';
import { render } from 'react-dom';
import sample from 'array-sample';
import { fromJS } from 'immutable';
import { get } from 'lib/http';
import YoutTubeAPICache from 'lib/YoutTubeAPICache';
import 'styles/App.scss';
import 'styles/dark.scss';
import { SplashScreen } from 'components';
import Config from 'config';
import { StripeProvider } from 'react-stripe-elements';
import { AppRoutes } from './Routes';

const setup = () => {
  // eslint-disable-next-line no-extend-native
  Array.prototype.sample = sample;
  new YoutTubeAPICache(); // eslint-disable-line no-new

  window.ipcRenderer.on('version', (e, electronVersion) => {
    localStorage.setItem('electronVersion', electronVersion);
  });

  // Fix issue https://github.com/headsetapp/headset-electron/issues/163
  document.body.onauxclick = (e) => {
    e.preventDefault();
  };
};

class Startup extends PureComponent {
  constructor() {
    super();
    this.state = {
      user: null,
      isLoading: true,
    };
  }

  componentDidMount() {
    const email = localStorage.getItem('recoveryEmail');
    get({ path: 'sessions', data: { email } })
      .then((response) => {
        const user = fromJS(response.data);
        this.setState({ isLoading: false, user });
      }).catch((err) => {
        console.log('error', err); // eslint-disable-line no-console
        this.setState({ isLoading: false });
      });
  }

  render() {
    const { isLoading, user } = this.state;
    if (isLoading) {
      return <SplashScreen />;
    }

    return (
      <StripeProvider apiKey={Config.stripeKey}>
        <AppRoutes user={user} />
      </StripeProvider>
    );
  }
}

const dom = document.getElementById('app');

setup();
render(
  <Startup />,
  dom,
);
