import geArtistTitle from 'get-artist-title';

Math.easeInOutQuad = (t, b, c, d) => {
  t /= d / 2;
  if (t < 1) return (((c / 2) * t) * t) + b;
  t -= 1;
  return ((-c / 2) * ((t * (t - 2)) - 1)) + b;
};

const getArtistTitle = (title) => {
  const videoTitle = title.replace('"', '');
  const breakdown = geArtistTitle(videoTitle);
  if (!breakdown || (breakdown[1] === '')) return [null, videoTitle];

  return breakdown;
};

const indexById = (array) => {
  const indexed = {};
  array.forEach((item) => { indexed[item.id] = item; });
  return indexed;
};

const random = (min, max) => Math.floor((Math.random() * ((max - min) + 1)) + min);

const scrollTo = (element, target, duration = 300) => {
  if (!target) return;

  const to = target.offsetTop - target.offsetHeight - 188;
  const start = element.scrollTop;
  const change = to - start;
  let currentTime = 0;
  const increment = 20;

  const animateScroll = () => {
    currentTime += increment;
    const val = Math.easeInOutQuad(currentTime, start, change, duration);
    element.scrollTop = val;
    if (currentTime < duration) {
      setTimeout(animateScroll, increment);
    }
  };
  animateScroll();
};

export { getArtistTitle, indexById, random, scrollTo };
