import actionTypes from 'actions/actionTypes';
import { Set, fromJS } from 'immutable';
import fuzzy from 'fuzzy';
import stations from 'lib/stations';

const stationKeys = Object.keys(stations);

const initialState = fromJS({
  searchBarValue: '',
  selectedSearchIndex: -2,
  filterredKeys: [],
  savedStations: [],
  sort: '',
  t: 'all',
});

const radioReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.UPDATE_RADIO_SEARCH_BAR: {
      const filter = fuzzy.filter(payload, stationKeys);
      const sorted = filter.map((s) => s.original);

      return state
        .set('searchBarValue', payload)
        .set('filterredKeys', sorted);
    }

    case actionTypes.UPDATE_SELECTED_SEARCH:
      return state.set('selectedSearchIndex', payload);

    case actionTypes.STORE_SAVED_RADIO:
      return state.set('savedStations', Set(payload));

    case actionTypes.CHANGE_REDDIT_SORT_FILTER:
      return state.set('sort', payload);

    case actionTypes.CHANGE_REDDIT_T_FILTER:
      return state.set('t', payload);

    default:
      return state;
  }
};

export default radioReducer;
