import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { Intent, InputGroup, Dialog, Button } from '@blueprintjs/core';
import passwordValidator from 'password-validator';
import { fromJS } from 'immutable';
import { post, get, put } from 'lib/http';

const passwordSchema = new passwordValidator();
passwordSchema.is().min(8); // Minimum length 8

const INITIAL_STATE = {
  step1: false,
  step2: false,
  step3: false,
  email: '',
  token: '',
  newPassword: '',
  confirmNewPassword: '',
  isLoading: false,
  error: null,
};

class PasswordResetDialog extends PureComponent {
  constructor() {
    super();
    this.state = INITIAL_STATE;
  }

  onFieldChange = (field, value = '') => {
    this.setState({ [field]: value.trim() });
  }

  onCancel = (e) => {
    e.preventDefault();

    if (this.state.step1) {
      // eslint-disable-next-line no-alert
      if (window.confirm('If you cancel now, you will have to start over. Are you sure?')) {
        this.setState(INITIAL_STATE);
        this.props.onClose();
      }
    } else {
      this.setState(INITIAL_STATE);
      this.props.onClose();
    }
  }

  get stepOne() {
    const { isLoading, email } = this.state;
    return (
      <form>
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <b>Step 1:</b> Enter your email
        </p>
        <InputGroup
          autoFocus
          className="input"
          disabled={isLoading}
          placeholder="Email"
          type="email"
          value={email}
          onChange={(e) => this.onFieldChange('email', e.target.value)}
        />
        <Button
          type="submit"
          className="input button"
          loading={isLoading}
          intent={Intent.PRIMARY}
          onClick={this.sendEmail}
        >
          Continue
        </Button>
      </form>
    );
  }

  get stepTwo() {
    const { isLoading, token } = this.state;
    return (
      <form>
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <b>Step 2:</b> We just sent you an email. Follow the instructions in the email to continue.
          Do not close this dialog, otherwise you&apos;ll have to start over.
        </p>
        <InputGroup
          autoFocus
          className="input"
          disabled={isLoading}
          placeholder="Enter Reset token"
          type="text"
          value={token}
          onChange={(e) => this.onFieldChange('token', e.target.value)}
        />
        <Button
          type="submit"
          className="input button"
          loading={isLoading}
          intent={Intent.PRIMARY}
          onClick={this.validateToken}
        >
          Validate
        </Button>
      </form>
    );
  }

  get stepThree() {
    const { isLoading, newPassword, confirmNewPassword } = this.state;
    return (
      <form>
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          <b>Step 3:</b> Success! Now change your password, and you&apos;re good to go.
        </p>
        <InputGroup
          autoFocus
          className="input"
          disabled={isLoading}
          placeholder="Enter a new password"
          type="password"
          value={newPassword}
          onChange={(e) => this.onFieldChange('newPassword', e.target.value)}
        />
        <InputGroup
          className="input"
          disabled={isLoading}
          placeholder="Confirm the new password"
          type="password"
          value={confirmNewPassword}
          onChange={(e) => this.onFieldChange('confirmNewPassword', e.target.value)}
        />
        <Button
          type="submit"
          className="input button"
          loading={isLoading}
          intent={Intent.PRIMARY}
          onClick={this.updatePassword}
        >
          Update Password
        </Button>
      </form>
    );
  }

  get currentStep() {
    const { step1, step2, step3 } = this.state;

    if (!step1) {
      return this.stepOne;
    } else if (!step2) {
      return this.stepTwo;
    } else if (!step3) {
      return this.stepThree;
    }

    return null;
  }

  get renderError() {
    const { error } = this.state;
    if (!error) {
      return null;
    }

    return (
      <div className="global-error">
        {error}
      </div>
    );
  }

  validatePasswords = (newPass, confirmPass) => {
    if (!passwordSchema.validate(newPass || 'fake')) {
      this.setState({ error: 'Password must be at least 8 characters' });
      return false;
    }
    if (newPass !== confirmPass) {
      this.setState({ error: 'Passwords do not match' });
      return false;
    }

    return true;
  }

  updatePassword = () => {
    this.setState({ isLoading: true });
    const { email, newPassword, token, confirmNewPassword } = this.state;
    if (!this.validatePasswords(newPassword, confirmNewPassword)) {
      this.setState({ isLoading: false });
      return;
    }

    // Update Password
    put({ path: 'users/password', data: { token, email, password: newPassword } }).then(() => (
      post({ path: 'login', data: { email, password: newPassword } }).then(() => (
        get({ path: 'sessions', data: { email } }).then((response) => {
          const user = fromJS(response.data);
          this.props.onLogin(user);
          this.props.onClose();
          this.props.onSuccess();
        })
      ))
    ));
  }

  validateToken = () => {
    this.setState({ isLoading: true });
    const { email, token } = this.state;
    get({ path: 'password-tokens', data: { email, token } }).then(() => {
      this.setState({ step2: true, isLoading: false, error: null });
    }).catch(() => {
      this.setState({
        isLoading: false,
        error: 'Something went wrong! Please try again',
      });
    });
  }

  sendEmail = () => {
    const done = () => {
      this.setState({ step1: true, isLoading: false, error: null });
    };

    this.setState({ isLoading: true });
    const { email } = this.state;
    post({ path: 'password-tokens', data: { email } })
      .then(done)
      .catch(done);
  }

  render() {
    const { isOpen } = this.props;
    return (
      <Dialog
        className="user-form-content password-reset-dialog"
        lazy
        isOpen={isOpen}
      >
        {this.renderError}
        <div
          className="form-wrapper"
        >
          <h2 className="dialog-title">Password Reset</h2>
          {this.currentStep}
          <div className="action-button">
            <a href="" onClick={this.onCancel}>Cancel</a>
          </div>
        </div>
      </Dialog>
    );
  }
}

PasswordResetDialog.propTypes = {
  isOpen: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  onLogin: PropTypes.func.isRequired,
  onSuccess: PropTypes.func.isRequired,
};

PasswordResetDialog.defaultProps = {
  isOpen: false,
};

export default PasswordResetDialog;
