import React, { PureComponent } from 'react';
import LastFm from 'lib/LastFm';
import Config from 'config';

class LastFmConnect extends PureComponent {
  constructor() {
    super();
    LastFm
      .connect()
      .then(() => window.location.replace(`${Config.rootUrl}/#profile`));
  }

  render() {
    return (
      <div className="lastfm-loading">
        <h3>Connecting to last.fm</h3>
        <p>One moment please...</p>
      </div>
    );
  }
}

export default LastFmConnect;
