import shortid from 'shortid';

class CollectionBuilder {
  static randomId() {
    shortid.seed(Date.now());
    return shortid.generate();
  }

  constructor({
    id, name, tracks, image, type,
  }) {
    this.name = name || '';
    this.tracks = {};
    this.order = {};
    this.image = image;
    this.type = type;
    this.id = id || this.createId();
    this.custom = !id || this.isLike;

    if (tracks) {
      this.indexTracks(tracks);
    }
  }

  get isLike() {
    return this.name.toLowerCase() === 'likes';
  }

  createId() {
    // to avoid collusion with hardcoded playlist called 'likes'
    if (this.isLike) {
      return 'likes';
    }
    return CollectionBuilder.randomId();
  }

  indexTracks(tracks) {
    tracks.forEach((track, i) => {
      const id = track.id || track.get('id');
      this.tracks[id] = track;
      this.order[id] = i;
    });
  }

  build() {
    return {
      id: this.id,
      name: this.name,
      tracks: this.tracks,
      order: this.order,
      custom: this.custom,
      image: this.image,
      type: this.type,
    };
  }
}

export default CollectionBuilder;
