import actionTypes from 'actions/actionTypes';

import { Map, List } from 'immutable';

const initialState = Map({
  autocompletes: [], // because we need the -1 index to be undefined, must keep it as array.
  currentAutocomplete: -1,
  results: List(),
  searchTerm: '',
  isLoadingMore: false,
  nextSearchPageToken: '',
  nextRelatedPageToken: '',
  searchBarValue: '',
});

const searchReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.RECEIVED_AUTOCOMPLETES:
      return state.set('autocompletes', payload);

    case actionTypes.RECEIVED_SEARCH_RESULTS:
      return state
        .set('results', payload)
        .set('autocompletes', []);

    case actionTypes.GETTING_SEARCH_RESULTS:
      return state
        .set('results', List())
        .set('autocompletes', [])
        .set('searchTerm', payload)
        .set('currentAutocomplete', -1);

    case actionTypes.STEP_AUTOCOMPLETE:
      if (state.get('autocompletes').length) {
        const searchBarValue = state.get('autocompletes')[payload];
        return state
          .set('currentAutocomplete', payload)
          .set('searchBarValue', searchBarValue);
      }
      return state;

    case actionTypes.LOAD_MORE:
      return state.set('isLoadingMore', true);

    case actionTypes.APPEND_SEARCH_RESULTS: {
      const currentResults = state.get('results');
      return state.merge({ results: currentResults.concat(payload) });
    }

    case actionTypes.MORE_LOADED:
      return state.set('isLoadingMore', false);

    case actionTypes.SET_NEXT_SEARCH_PAGE_TOKEN:
      return state.set('nextSearchPageToken', payload);

    case actionTypes.SET_NEXT_RELATED_PAGE_TOKEN:
      return state.set('nextRelatedPageToken', payload);

    case actionTypes.UPDATE_SEARCH_BAR:
      return state.set('searchBarValue', payload);

    case actionTypes.UPDATE_SEARCH_TERM:
      return state.set('searchTerm', payload);

    default:
      return state;
  }
};

export default searchReducer;
