import PropTypes from 'prop-types';
import React, { Component } from 'react';
import classnames from 'classnames';
import Image from 'lib/Image';
import { TrackMenu } from 'containers';
import { Map } from 'immutable';
import ImmutablePropTypes from 'react-immutable-proptypes';

class TrackItem extends Component {
  constructor() {
    super();

    this.clipboard = null;
  }

  shouldComponentUpdate(newProps) {
    const { item, isCurrent, isLiked, sortable } = this.props;
    // sweet optimized!
    return (
      newProps.item.get('id') !== item.get('id')
        || newProps.isCurrent !== isCurrent
        || newProps.isLiked !== isLiked
        || newProps.sortable !== sortable
    );
  }

  onMenueClose = () => {
    if (this.clipboard) {
      this.clipboard.destroy();
    }
  }

  get moreMenu() {
    const { item, onClick, isCurrent } = this.props;

    if (item.get('type') !== 'video') {
      return <i onClick={onClick} role="none" className="material-icons chevron-more">chevron_right</i>;
    }

    return (
      <TrackMenu
        isCurrent={isCurrent}
        track={item}
        className="more-button track-menu"
        targetElement={<i className="material-icons">more_vert</i>}
        onClose={this.onMenueClose}
      />
    );
  }

  get rightStuff() {
    const { item } = this.props;
    const isLive = item.get('duration') === 0;
    const durationClockFormat = item.get('durationClockFormat');

    if (isLive) {
      return '';
    }

    return durationClockFormat;
  }

  get likedIcon() {
    const { isLiked } = this.props;
    if (!isLiked) return null;
    return <i className="material-icons is-liked-icon lower">Favorite</i>;
  }

  get subTitle() {
    const { item } = this.props;
    const isAlbum = item.get('albumTrackList');
    const isLive = item.get('duration') === 0;

    const type = item.get('type');

    if (isAlbum) {
      return <span className="album-tag">Album</span>;
    }

    if (isLive) {
      return <span className="live-tag">Live</span>;
    }

    switch (type) {
      case 'video':
        return item.get('artist');
      case 'channel':
        return <span className="channel-tag">Channel</span>;
      case 'playlist':
        return <span className="playlist-tag">Playlist</span>;
      default:
        return null;
    }
  }

  render() {
    const { isLiked, onClick, isCurrent, item, sortable } = this.props;
    const title = item.get('title');
    const thumbnail = item.get('thumbnail');
    const isNew = item.get('isNew');

    return (
      <div
        className={classnames('list-item', { active: isCurrent, isNew, isLiked })}
        id={`track-${item.get('pin') || item.get('id')}`}
      >
        {sortable && <i className="material-icons drag-icon">reorder</i>}
        <Image src={thumbnail} className="album-art" onClick={onClick} />
        {this.likedIcon}
        <div className="description" role="none" onClick={onClick}>
          <div className="title">
            {title}
          </div>
          <div className="artist">
            {this.subTitle}
          </div>
        </div>
        <span className="right-stuff" role="none" onClick={onClick}>
          {this.rightStuff}
        </span>
        {this.moreMenu}
      </div>
    );
  }
}

TrackItem.contextTypes = {
  kind: PropTypes.string,
  notify: PropTypes.func,
  subscription: PropTypes.object,
};

TrackItem.propTypes = {
  item: ImmutablePropTypes.map,
  isCurrent: PropTypes.bool,
  onClick: PropTypes.func.isRequired,
  isLiked: PropTypes.bool,
  sortable: PropTypes.bool,
};

TrackItem.defaultProps = {
  item: Map(),
  isCurrent: false,
  isLiked: false,
  sortable: false,
};

export default TrackItem;
