import React from 'react';

const UpdateToast = () => (
  <span>
    A new version of Headset is ready for you. Just&nbsp;
    <a
      style={{ textDecoration: 'underline' }}
      onClick={(e) => {
        e.preventDefault();
        window.ipcRenderer.send('restart-to-update');
      }}
    >
      restart to use it
    </a>
  </span>
);

export default UpdateToast;
