import { put } from 'lib/http';
import { fromJS } from 'immutable';
import { fetchVideos } from 'lib/youtube';
import YoutTubeAPICache from 'lib/YoutTubeAPICache';

class CollectionsSyncer {
  constructor(collections) {
    this.collections = collections;
  }

  // save collections to the db
  sync() {
    this._dropTracks();
    const email = localStorage.getItem('recoveryEmail');
    return put({ path: 'users', data: { collections: this.collections, email } });
  }

  // db doesn't have tracks (i.e details like artist and thumbnail). it only saves the order {id1: 0, id2: 2, id3: 1}.
  // the rebuild() will read the order and 'recover' the tracks by looking at the localStorage (cahce) first.
  // if it's not found, it will go to youtube to fetch the track.
  rebuild() {
    if (this.collections.size === 0) {
      return Promise.resolve(this.collections);
    }

    const allCollections = {};
    const missingIds = this._getMissingIds();
    return this._addMissingTracksToCache(missingIds).then(() => {
      this.collections.forEach((collection) => {
        const tracks = {};
        collection.get('order').keySeq().forEach((id) => {
          const track = YoutTubeAPICache.get(id);
          // if video has been removed by the user
          if (!track) return;
          tracks[id] = YoutTubeAPICache.get(id);
        });
        allCollections[collection.get('id')] = collection.set('tracks', fromJS(tracks));
      });

      return fromJS(allCollections);
    });
  }

  _addMissingTracksToCache(ids) {
    const chunks = this._arrayChunks(ids, 50);

    // for each chunk fetch videos from youtube and return array of promises
    // fetchVideos will add the missingIds to the cache.
    const promiseChunks = chunks.map((chunkIds) => fetchVideos(chunkIds));

    return Promise.all(promiseChunks);
  }

  // return ids of tracks that are not in cache
  _getMissingIds() {
    const missing = [];
    this.collections.forEach((collection) => {
      collection.get('order').keySeq().forEach((id) => {
        if (YoutTubeAPICache.get(id) === null) {
          missing.push(id);
        }
      });
    });

    return missing;
  }

  // do this before saving to db (save space)
  _dropTracks() {
    this.collections = this.collections.map((collection) => (
      collection.set('tracks', {})
    ));
  }

  // conver array into chunks of n
  // e.g chunk = 2 array = [1,2,3,4,5,6]
  // return is [[1,2], [3,4], [5,6]]
  // eslint-disable-next-line class-methods-use-this
  _arrayChunks(array, chunk) {
    const chunks = [];
    for (let i = 0, j = array.length; i < j; i += chunk) {
      chunks.push(array.slice(i, i + chunk));
    }

    return chunks;
  }

  // Legacy cache had collection ids as the high level keys. New cahce has individual tracks as the high level key
  // in order to avoid massive youtube api calls when new cache is rolled out by reducing the number of missingIds,
  // this function will dig into all the collections, and add the tracks to be high level key.
  _migrateLegacyCahce() {
    this.collections.forEach((collection) => {
      const id = collection.get('id');
      const tracks = JSON.parse(localStorage.getItem(id));
      if (tracks !== null) {
        YoutTubeAPICache.setBulk(tracks);
      }
    });
  }
}

export default CollectionsSyncer;
