import PropTypes from "prop-types";
import React, { PureComponent } from "react";
import { Link } from "react-router";
import classnames from "classnames";

class MainMenu extends PureComponent {
  render() {
    const { path, newLikeAdded } = this.props;

    return (
      <div className="main-menu">
        {/* <EmailConfirmation /> */}
        {/* <div className={classnames('menu-item', { active: path.match(/browse/) })}>
          <Link to="/browse" className="radio-link">
            <i className="material-icons">headset</i>
            <span className="content">
                Browse
              </span>
          </Link>
        </div> */}
        <div
          className={classnames("menu-item", {
            active: path.match(/search/) || path === "/",
          })}
        >
          <Link to="/search" className="search-link">
            <i className="material-icons">search</i>
            <span className="content">Search</span>
          </Link>
        </div>
        <div
          className={classnames("menu-item", { active: path.match(/radio/) })}
        >
          <Link to="/radio" className="radio-link">
            <i className="material-icons">radio</i>
            <span className="content">Radio</span>
          </Link>
        </div>
        <div
          className={classnames("menu-item", {
            active: path.match(/collection(?!\/likes)/),
          })}
        >
          <Link to="/collections" className="collections-link">
            <i className="material-icons">album</i>
            <span className="content">Collections</span>
          </Link>
        </div>
        <div
          className={classnames("menu-item", {
            active: path === "/preview/collection/likes",
          })}
        >
          <Link
            to="preview/collection/likes"
            className={classnames("likes-link", { "new-like": newLikeAdded })}
          >
            <i className="material-icons">
              {newLikeAdded ? "favorite" : "favorite_border"}
            </i>
            <span className="content">Likes</span>
          </Link>
        </div>
      </div>
    );
  }
}

MainMenu.propTypes = {
  path: PropTypes.string,
  newLikeAdded: PropTypes.bool,
};

MainMenu.defaultProps = {
  path: null,
  newLikeAdded: false,
};

export default MainMenu;
