import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import LastFm from 'lib/LastFm';
import { ExternalLink, ProBadge } from 'components';
import { Switch } from '@blueprintjs/core';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { delete_ } from 'lib/http';
import { DIALOGS } from 'lib/constants';
import Subscription from 'lib/Subscription';
import classnames from 'classnames';
import {
  updateUser,
  openDialog,
  closeDialog,
} from 'actions/appActions';
import packageJson from '../../../package.json';

class ProfilePage extends PureComponent {
  constructor() {
    super();
    this.exportLink = null;
    this.state = {
      isLoading: false,
      themes: ['Original', 'Dark'],
      currentTab: 'preferences',
    };
  }

  UNSAFE_componentWillMount() {
    if (!this.user) {
      window.location.replace('/');
    }
  }

  onThemeChange = (e) => {
    this.props.dispatch(updateUser({ attribute: 'theme', value: e.target.value }));
  }

  onOpenStripeDialog = () => {
    ga('send', 'event', 'subscription', 'Click on upgrade from profile');
    this.props.dispatch(openDialog(DIALOGS.STRIPE));
  }

  onCloseStripeDialog = () => {
    this.props.dispatch(closeDialog(DIALOGS.STRIPE));
  }

  onChangeTab = (tab) => {
    this.setState({ currentTab: tab });
  }

  // eslint-disable-next-line class-methods-use-this
  get versionPack() {
    return (
      <div className="package-json">
        <p>
          { /* eslint-disable-next-line react/jsx-one-expression-per-line */}
          Core: v{packageJson.version}, Wrapper: {localStorage.electronVersion}
        </p>
      </div>
    );
  }

  /* eslint-disable react/destructuring-assignment */
  get user() {
    return this.context.user;
  }

  get collections() {
    return this.props.collectionsStore.get('collections');
  }

  get savedStations() {
    return this.props.radioStore.get('savedStations');
  }
  /* eslint-enable react/destructuring-assignment */

  get fullName() {
    return `${this.user.get('firstName')} ${this.user.get('lastName')}`;
  }

  get likesSize() {
    if (!this.collections.size) {
      return 0;
    }

    return this.collections.getIn(['likes', 'tracks']).size;
  }

  get connectedToLastFm() {
    return this.user.get('lastFmUsername') && this.user.get('lastFmSessionId');
  }

  get quickStats() {
    return (
      <div className="quick-stats-grid">
        <div className="item">
          <p className="number">
            {this.likesSize}
          </p>
          <p className="info">Likes</p>
        </div>

        <div className="item">
          <p className="number">
            {this.collections.size - 1}
          </p>
          <p className="info">Collections</p>
        </div>

        <div className="item">
          <p className="number">
            {this.savedStations.size}
          </p>
          <p className="info">Stations</p>
        </div>
      </div>
    );
  }

  exportData = () => {
    const exportObj = JSON.stringify({
      collections: this.props.collectionsStore.get('collections').toJS(),
      user: this.props.appStore.get('user').toJS(),
      savedStations: this.props.radioStore.get('savedStations').toJS(),
    }, null, 2);

    const exportBase64 = Buffer.from(exportObj).toString('base64');
    const href = `data:application/json;charset=utf-8;base64,${exportBase64}`;

    this.exportLink.setAttribute('href', href);
    this.exportLink.click();
  }

  cancelSubscription = () => {
    ga('send', 'event', 'subscription', 'Click cancel subscription');
    /* eslint-disable no-alert */
    if (window.confirm('are you sure you want to cancel your membership?')) {
      const data = {
        email: this.user.get('email'),
        stripeCustomerID: this.user.get('stripeCustomerID'),
      };

      delete_({ path: 'subscriptions', data }).then(() => {
        this.props.dispatch(updateUser({ attribute: 'stripeCustomerID', value: null }));
      });
    }
    /* eslint-enable no-alert */
  }

  deleteAccount = () => {
    /* eslint-disable no-alert */
    if (this.user.get('stripeCustomerID')) {
      window.alert('Cannot delete an account with an active subscription.');
      return;
    }

    if (window.confirm(`All data will be permanently removed.
                         Are you absolutely sure you want to close your account ?`)) {
      ga('send', 'event', 'deleteAccount');
      const data = {
        email: this.user.get('email'),
      };

      delete_({ path: 'users', data }).then(() => {
        window.location.replace('');
      });
    }
    /* eslint-enable no-alert */
  }

  toggleNotifications = (e) => {
    if (e.target.checked) {
      localStorage.setItem('new-track-notification', true);
      // example
      const title = 'Never Gonna Give You Up';
      const body = 'Rick Astley';
      const icon = 'https://i.ytimg.com/vi/dQw4w9WgXcQ/mqdefault.jpg';

      /* eslint-disable no-new */
      new Notification(title, {
        title,
        body,
        icon,
        silent: true,
      });
      /* eslint-enable no-new */
    } else {
      localStorage.removeItem('new-track-notification');
    }

    this.forceUpdate();
  }

  showLoader = () => {
    this.setState({ isLoading: true });
  }

  toggleDiscord = (e) => {
    if (e.target.checked) {
      localStorage.setItem('discord', true);
    } else {
      localStorage.removeItem('discord');
    }
    this.forceUpdate();
    window.ipcRenderer.send('discord', e.target.checked);
  }

  renderAccount() {
    const { isLoading } = this.state;
    return (
      <div className="settings-list">
        <div className="setting-item">
          <div className="details">
            <h4>Plan</h4>
            <p>
              {Subscription.isPro() ? <ProBadge /> : 'Basic'}
            </p>
          </div>
          <div className="action">
            <p>
              {Subscription.isPro()
                ? <a className="" onClick={this.cancelSubscription}>Cancel</a>
                : <a className="button upgrade" onClick={this.onOpenStripeDialog}>Upgrade</a>}
            </p>
          </div>
        </div>
        <div className="setting-item">
          <div className="details">
            <h4>Last.fm</h4>
            {this.connectedToLastFm
              ? (
                <p className="connected">
                  <i className="material-icons check">check_circle</i>
                  Connected as&nbsp;
                  <ExternalLink href={LastFm.profile_page()}>
                    {this.user.get('lastFmUsername')}
                  </ExternalLink>
                </p>
              )
              : <p>Scrobble to last.fm</p>}
          </div>
          <div className="action">
            <span>
              {this.connectedToLastFm
                ? <a className="button" onClick={LastFm.disconnect}>Disconnect</a>
                : (
                  <a className="button" href={LastFm.auth_link()} onClick={this.showLoader}>
                    {isLoading ? 'Redirecting...' : 'Connect'}
                  </a>
                )}
            </span>
          </div>
        </div>
        <div className="setting-item">
          <div className="details">
            <h4>Export</h4>
            <p>Download all you data, in raw format</p>
          </div>
          <div className="action">
            <p>
              {/* eslint-disable jsx-a11y/anchor-has-content */}
              <a download={`headset-data-${Date.now()}.json`} ref={(el) => { this.exportLink = el; }} />
              {/* eslint-enable jsx-a11y/anchor-has-content */}
              <a
                className="button"
                onClick={this.exportData}
              >
                Export
              </a>
            </p>
          </div>
        </div>
        <div className="setting-item">
          <div className="details">
            <h4>Delete</h4>
            <p>Permanently delete all your data</p>
          </div>
          <div className="action">
            <p>
              <a className="bad" onClick={this.deleteAccount}>Delete</a>
            </p>
          </div>
        </div>
        <p className="signout">
          <Link className="bad button logout" to="/logout">Sign Out</Link>
        </p>
      </div>
    );
  }

  renderSettings() {
    const { themes } = this.state;
    return (
      <div className="settings-list">
        <div className="setting-item">
          <div className="details">
            <h4>Theme</h4>
            <p>Select color scheme</p>
          </div>
          <div className="action">
            <div className="pt-select">
              <select
                onChange={this.onThemeChange}
                value={this.user.get('theme')}
                className="theme-select"
                id="theme-select"
              >
                {themes.map((theme, i) => (
                  <option key={i} value={theme}>
                    {theme}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
        <div className="setting-item">
          <div className="details">
            <h4>Notifications</h4>
            <p>Show alert when new song is playing</p>
          </div>
          <div className="action">
            <Switch
              checked={!!localStorage.getItem('new-track-notification')}
              onChange={this.toggleNotifications}
            />
          </div>
        </div>
        <div className="setting-item">
          <div className="details">
            <h4>Discord</h4>
            <p>Discord Rich Presence integration</p>
          </div>
          <div className="action">
            <Switch
              checked={!!localStorage.getItem('discord')}
              onChange={this.toggleDiscord}
            />
          </div>
        </div>
      </div>
    );
  }

  renderTabContent() {
    const { currentTab } = this.state;
    switch (currentTab) {
      case 'account':
        return this.renderAccount();
      case 'settings':
        // fallthrough
      default:
        return this.renderSettings();
    }
  }

  render() {
    const { currentTab } = this.state;
    return (
      <div className="profile-page">
        <div className="header">
          <h2 className="title">
            {this.fullName}
          </h2>
          <p className="days-joined">
            {this.user.get('email')}
          </p>
        </div>
        {this.quickStats}
        <div className="profile-tabs">
          <div
            className={classnames('tabb preferences', { active: currentTab === 'preferences' })}
          >
            <span onClick={() => this.onChangeTab('preferences')}>
              Preferences
            </span>
          </div>
          <div
            className={classnames('tabb account', { active: currentTab === 'account' })}
          >
            <span onClick={() => this.onChangeTab('account')}>
              Account
            </span>
          </div>
        </div>
        {this.renderTabContent()}
        {this.versionPack}
      </div>
    );
  }
}

ProfilePage.propTypes = {
  dispatch: PropTypes.func,
  collectionsStore: ImmutablePropTypes.map.isRequired,
  radioStore: ImmutablePropTypes.map.isRequired,
  appStore: ImmutablePropTypes.map.isRequired,
};

ProfilePage.contextTypes = {
  user: ImmutablePropTypes.map.isRequired,
};

ProfilePage.defaultProps = {
  dispatch: () => {},
};

const mapStateToProps = (state) => ({
  collectionsStore: state.collectionsReducer,
  radioStore: state.radioReducer,
  appStore: state.appReducer,
});

export default connect(mapStateToProps)(ProfilePage);
