import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import Slider from 'react-rangeslider';

class VolumeSlider extends PureComponent {
  constructor(props) {
    super();

    const defaultVolume = parseInt(localStorage.getItem('volumeLevel'), 10) || 75;

    this.state = {
      volume: defaultVolume,
    };

    props.onVolumeChange(defaultVolume);

    window.changeVolumeSate = (volume) => {
      this.setState({ volume });
    };
  }

  setMax = () => {
    this.setState({ volume: 100 });
    this.props.onVolumeChange(100);
  }

  setMin = () => {
    this.setState({ volume: 0 });
    this.props.onVolumeChange(0);
  }

  handleOnChange = (volume) => {
    if (volume !== this.state.volume) {
      this.props.onVolumeChange(volume);
    }
    this.setState({
      volume,
    });
  }

  render() {
    const { volume } = this.state;
    return (
      <div>
        <i className="material-icons volume-icon down" role="none" onClick={this.setMin}>
          volume_mute
        </i>
        <Slider
          value={volume}
          onChange={this.handleOnChange}
          tooltip={false}
        />
        <i className="material-icons volume-icon up" role="none" onClick={this.setMax}>volume_up</i>
      </div>
    );
  }
}

VolumeSlider.propTypes = {
  onVolumeChange: PropTypes.func,
};

VolumeSlider.defaultProps = {
  onVolumeChange: () => {},
};

export default VolumeSlider;
