import React from 'react';
import PropTypes from 'prop-types';
import Loader from 'static/images/loader.svg';
import { connect } from 'react-redux';
import { TrackList } from 'components';
import ImmutablePropTypes from 'react-immutable-proptypes';
import Image from 'lib/Image';
import { Intent, Position, ControlGroup, Spinner } from '@blueprintjs/core';
import Subscription from 'lib/Subscription';
import { DIALOGS } from 'lib/constants';
import ellipsize from 'ellipsize';
import pluralize from 'pluralize';

import {
  createRadioPreview,
} from 'actions/previewActions';

import {
  openDialog,
  notify,
} from 'actions/appActions';

import {
  changeRadioSort,
  changeRadioT,
} from 'actions/radioActions';

import {
  setCurrentRadioStation,
} from 'actions/playerActions';

import PreviewInterface from './PreviewInterface';

class RadioPreview extends PreviewInterface {
  componentDidMount() {
    const { dispatch, id, radioStore } = this.props;
    const sort = radioStore.get('sort');
    const t = radioStore.get('t');
    dispatch(createRadioPreview(id, sort, t)).catch(() => {
      this.context.router.push('radio');
      dispatch(notify(
        `${id} is not available`,
        { intent: Intent.DANGER, timeout: 3000, position: Position.BOTTOM },
      ));
    });
  }

  state = {
    loading: false,
  }

  onSortChanged = (e) => {
    if (!Subscription.isPro()) {
      this.props.dispatch(openDialog(DIALOGS.PRO_DISCOVER));
      return;
    }

    ga('send', 'event', 'Change Station Sort');
    const sort = e.target.value;
    const { radioStore, dispatch, id } = this.props;
    this.setState({ loading: true });
    const t = radioStore.get('t');
    dispatch(changeRadioSort(sort));

    dispatch(createRadioPreview(id, sort, t)).then(() => {
      this.setState({ loading: false });
    });
  }

  onTChanged = (e) => {
    ga('send', 'event', 'Change Station T');
    const t = e.target.value;
    const { radioStore, dispatch, id } = this.props;
    this.setState({ loading: true });
    const sort = radioStore.get('sort');
    dispatch(changeRadioT(t));

    dispatch(createRadioPreview(id, sort, t)).then(() => {
      this.setState({ loading: false });
    });
  }

  onRadioItemSelect = (data) => {
    const { dispatch, id } = this.props;

    dispatch(setCurrentRadioStation(id));

    this.onItemSelect(data);
  }

  get backLink() {
    return (
      <a
        href="#"
        onClick={this.context.router.goBack}
        className="preivew-back-link"
      >
        <i className="material-icons">chevron_left</i>
      </a>
    );
  }

  /* eslint-disable class-methods-use-this */
  get noTracks() {
    return (
      <div className="no-tracks">
        <h3>No tracks found.</h3>
        <p>
          Maybe try a different filter?
        </p>
      </div>
    );
  }
  /* eslint-enable */

  get body() {
    if (this.playlist === undefined) {
      return (
        <div className="page-loader">
          <img alt="" src={Loader} />
          <h2>Loading</h2>
        </div>
      );
    }

    if (!this.tracks.size) return this.noTracks;

    return ([
      <div key={1} className="list-preview">Tracks</div>,
      <TrackList
        key={2}
        tracks={this.tracks}
        onItemSelect={this.onRadioItemSelect}
        currentTrack={this.currentTrack}
        sortable={false}
        onLoadMoreClick={this.onLoadMore}
        isLoadingMore={this.state.isLoadingMore}
        needLoadMore={this.isThereMoreToLoad}
      />,
    ]);
  }

  get redditFilter() {
    const { radioStore } = this.props;
    const sort = radioStore.get('sort');
    const t = radioStore.get('t');

    return (
      <ControlGroup className="reddit-select">
        <select
          value={sort}
          name="sort"
          onChange={this.onSortChanged}
          className="reddit-sort"
        >
          <option value="hot">Hot</option>
          <option value="new">New</option>
          <option value="rising">Rising</option>
          <option value="controversial">Controversial</option>
          <option value="top">Top</option>
        </select>
        {sort === 'top' && (
          <select
            value={t}
            name="t"
            disabled={sort !== 'top' && sort !== 'controversial'}
            onChange={this.onTChanged}
          >
            <option value="hour">Past Hour</option>
            <option value="day">Today</option>
            <option value="week">This week</option>
            <option value="month">This month</option>
            <option value="year">This year</option>
            <option value="all">All Time</option>
          </select>
        )}
        {this.state.loading && <Spinner size={Spinner.SIZE_SMALL} />}
      </ControlGroup>
    );
  }

  get header() {
    if (!this.playlist) return null;

    return (
      <div className="preview-header">
        <div className="preivew-overlay" />
        {this.backLink}
        <Image className="preview-image" src={this.playlist.get('image')} />
        <div className="preview-info">
          <h2 className="preview-title" title={this.title}>
            {ellipsize(this.title || this.props.id, 85)}
          </h2>
          <div className="preview-count">
            {pluralize('Track', this.tracks.size, true)}
          </div>
          {this.redditFilter}
        </div>
        <div className="preview-shuffle">
          <div className="preview-shuffle-button" role="none" onClick={this.onShufflePlay}>
            Shuffle
          </div>
        </div>
      </div>
    );
  }

  // override interface
  /* eslint-disable class-methods-use-this */
  shouldReloadQueue() {
    return true;
  }
  /* eslint-enable */
}

RadioPreview.propTypes = {
  dispatch: PropTypes.func.isRequired,
  playerStore: ImmutablePropTypes.map.isRequired,
  previewStore: ImmutablePropTypes.map.isRequired,
  radioStore: ImmutablePropTypes.map.isRequired,
  id: PropTypes.string,
  kind: PropTypes.string,
};

RadioPreview.defaultProps = {
  id: '',
  kind: 'radio',
};

RadioPreview.contextTypes = {
  router: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  previewStore: state.previewReducer,
  playerStore: state.playerReducer,
  radioStore: state.radioReducer,
});

export default connect(mapStateToProps)(RadioPreview);
